import React from 'react';
import "./MoreOption.scss";


const MoreOption = (props) => {

  const optionDropdownClickHandler = (ev) => {
    ev.stopPropagation()
    let option = document.querySelector(".more-option-dropdown");
    if (option && option.classList.contains("remove-more-option")){
      ev.target.blur();
      option.classList.remove("remove-more-option");
    } else {
      option.classList.add("remove-more-option");
    }
  }

  const removeExtraOptionClass = () => {
    let option = document.querySelector(".more-option-dropdown");
    if (option.classList.contains("remove-more-option")){
      option.classList.remove("remove-more-option");
    }
  }

  return (
    <div className={["more-option", props.className ? props.className : null].join(" ")}>
      <a tabIndex={2} className={["more-option-dropbtn d-block my-auto",!props.buttonClass ? "px-1" : "", props.iconClass, props.buttonClass ? props.buttonClass : "", props.data.length === 0 ? 'more-option-disable' : ''].join(" ")}
         onClick={props.data.length ? (e) => optionDropdownClickHandler(e) : (e) => {e.preventDefault()}}
         onBlur={props.data.length ? () => removeExtraOptionClass() : () => {}}
      />
      {props.data.length ?
        <div className={["more-option-dropdown"].join(" ")}>
          <div className="more-option-content py-1">
            <ul className="p-0 m-0">
              {props.data ?
                props.data.map((option, index) =>
                  <li className="text-sm" key={"option-" + index}>
                    <button className="py-2 px-4 more-option-item" onClick={option.action}>{option.name}</button>
                  </li>
                )
                : null}
            </ul>
          </div>
        </div>
        : null}
    </div>
  );
}

export default MoreOption;
