import React from 'react';
import { connect } from "react-redux";
import GuestSortFilter from "../guest-sort-filter/GuestSortFilter";
import './GuestSortSection.scss';
import { clearGuestViewFilter, setChildGuestViewFilter, setGuestViewProducts, clearViewGuestPaginationInfo } from "../../GuestViewAction";
import FilterItemChip from "modules/my-registry/components/filter/filter-item-chip/FilterItemChip";
import Button from "../../../commons/components/button-component/ButtonComponent";
import Modal from "../../../commons/components/modal-component/ModalComponent";
import GuestFilterMobile from "../guest-filter/GuestFilterMobile";

class GuestSortSection extends React.Component {

  state = {
    showGuestFilterModal: false,
    guestChildFilter: [],
    guestFilterProducts: [],
    totalFilteredProducts: null
  }

  // Filter chips close handler
  deleteChipHandler = async (isParent, childId) => {
    if (isParent) {
      await this.props.dispatch(clearGuestViewFilter())
    } else {
      const newChildArray = this.props.stateFilterApplied.childFilter.filter(element => {
        return element !== childId
      })
      await this.props.dispatch(setChildGuestViewFilter(newChildArray))
    }
    await this.props.dispatch(clearViewGuestPaginationInfo())
    await this.props.dispatch(setGuestViewProducts([]))
    await this.props.setFilterhandler()
  }

  // Filter chips
  getFilterChips = (stateFilterApplied) => {
    let filterChips = {}
    if (stateFilterApplied) {
      let allChild = []
      for (let filterCat of this.props.stateFilterCategory) {
        for (let childCat of filterCat.child) {
          allChild.push(childCat)
        }
      }
      if (stateFilterApplied.childFilter) {
        filterChips.childFilter = [...stateFilterApplied.childFilter].map(item => {
          let finalAllChild = [...allChild].find(element => {
            return element.id === item
          })
          return finalAllChild
        })
      }
    }
    return filterChips;
  }

  //Clear filter state
  clearFilterHandler = async (ev) => {
    ev.preventDefault()
    await this.props.dispatch(clearGuestViewFilter())
    await this.props.dispatch(clearViewGuestPaginationInfo())
    await this.props.dispatch(setGuestViewProducts([]))
    await this.props.setFilterhandler()
  }
  
  //Clear Filter For Mobile
  clearGuestFilterHandlerMobile = async () => {
    await this.props.dispatch(clearGuestViewFilter())
    await this.props.dispatch(clearViewGuestPaginationInfo())
    await this.props.dispatch(setGuestViewProducts([]))
    await this.props.setFilterhandler()
  }

  //filter previous states
  viewGuestFilterStateHandler = () => {
    this.setState({guestFilterProducts: this.props.guestFilterProducts})
    this.setState({guestChildFilter: this.props.stateFilterApplied.childFilter});
    this.setState({showGuestFilterModal: true});
    this.setState({totalFilteredProducts: this.props.currTotalCount})
  }

  doneGuestViewFilterModal = () => {
    this.setState({showGuestFilterModal: false})
  }
  
  clearGuestViewFilterModal = () =>{
    this.clearGuestFilterHandlerMobile()
    this.doneGuestViewFilterModal()
  }
  
  closeGuestViewFilterModal = () =>{
    this.props.changeTotalCount(this.state.totalFilteredProducts);
    this.props.dispatch(setGuestViewProducts([...this.state.guestFilterProducts]))
    this.props.dispatch(setChildGuestViewFilter([...this.state.guestChildFilter]));
    this.setState({showGuestFilterModal: false})
  }
  
  //open modal
  modalFooterButtons = (
    <>
      <Button
        variant="secondary"
        className="px-3 py-1 font-weight-bold font-medium text-body"
        disabled={false}
        onClick={this.clearGuestViewFilterModal}
      >Clear</Button>
      <Button
        variant="primary"
        className="px-4 py-1 font-weight-bold font-medium text-body"
        disabled={false}
        onClick={this.doneGuestViewFilterModal}
      >Done</Button>
    </>
  )

  render() {
    let filterChips = this.getFilterChips(this.props.stateFilterApplied)

    return (
      <>
        <div className="row">
          <div className="col">
            <h4 className="font-weight-bold mr-2 filter-section-total-items">{this.props.productCount} {this.props.productCount === 1 ? "Gift" : "Gifts"}</h4>
          </div>
          <div className="col-5 filter-sort-section d-none d-lg-inline-block">
            <div className="d-flex justify-content-end">
              <div className="text-sm sort-by-text mr-2 align-self-center">Sort by</div>
              <GuestSortFilter setGuestLayout={this.props.setGuestLayout} isListViewActive={this.props.isListViewActive}/>
            </div>
          </div>
          <div className="d-inline-block d-lg-none">
            <Button className="px-1 pt-1 px-lg-3 d-inline-block d-lg-none mr-1" variant="secondary" onClick={this.viewGuestFilterStateHandler}>
              <i className="icon-filter-1 filter-icon" />
            </Button>
          </div>
        </div>
        <div className="row">
          <div className="col-7">
            <div className="filtered-items">
              {filterChips.childFilter ?
                filterChips.childFilter.map(item =>
                  item ?
                    <FilterItemChip
                      text={item.display}
                      className="mr-2"
                      key={filterChips.id + "-" + item.id}
                      deleteHandler={() => this.deleteChipHandler(false, item.id)} /> : null)
                : null}
              {this.props.stateFilterApplied.childFilter.length > 0 ?
                <a className="font-medium text-sm filter-clear-link" href="#" tabindex="0"
                  onClick={(ev) => this.clearFilterHandler(ev)}>Clear all</a>
                : null}
            </div>
          </div>
        </div>
        <Modal
          show={this.state.showGuestFilterModal}
          title={"Filters"}
          closeModal={this.closeGuestViewFilterModal}
          footer={this.modalFooterButtons}
          >
          <GuestFilterMobile setFilterhandler={this.props.setFilterhandler} categoryFilterLimit={this.props.categoryFilterLimit}/>
        </Modal>
      </>
    )
  }
}


const mapStateToProps = state => {
  return {
    stateViewRegistryProducts: state.guestView.product,
    stateActiveSortFilter: state.guestView.activeGuestSortFilter,
    stateGuestViewRegistry: state.guestView.guestViewRegistry,
    stateRegistryInfo: state.guestView.info,
    stateFilterApplied: state.guestView.guestViewFilterApplied,
    stateFilterCategory: state.guestView.guestFilterCategory
  }
}

export default connect(
  mapStateToProps
)(GuestSortSection);
