import React from "react";
import "./ImportRegistry.scss";
import {getAnotherRegistryProducts} from "../../../../../api/axios";
import {toast} from "react-toastify";
import SelectCategory from "../../select-category/SelectCategory";
import CategoryPill from "./category-pill/CategoryPill";
import {connect} from "react-redux";
import {setAnotherStoreRegistryItems} from "../../../../my-registry/MyRegistryActions";
import Button from "../../button-component/ButtonComponent";
import Modal from "../../modal-component/ModalComponent";
import {IMPORT_REGISTRY_BABYLIST_TEXT} from "../../../../../utils/constants";

class ImportRegistry extends React.Component {
  HTTPS_REGEX = /^(https?:\/\/){1}.*/g

  REGISTRY_STORE_ALTERNATIVE_NAME = {
    tgt: "target"
  }

  state = {
    registryUrl: "",
    errorState: "",
    errorMessage: "",
    loading: false,
    categoryModalShow: false,
    activeCategoryProductIndex: null,
    currentRegistryLink: "",
    inputHasFocus: false,
    currentCategory: [],
    btnHasFocus: false,
    changeUrlConfirmationModal: false
  }

  doNotSearch = false

  inputRef = React.createRef()

  setCategoryModalVisible = () => {
    this.setState({categoryModalShow: false, currentCategory: []})
  }

  changeLoadingState = (value) => {
    this.setState({
      loading: value
    })
  }

  registryUrlChangeHandler = (ev) => {
    this.setState({
      registryUrl: ev.target.value,
      errorMessage: ""
    })
  }

  checkUrlAndGetRegistryItems = async () => {
    let url = this.state.registryUrl
    if (!url.match(this.HTTPS_REGEX)) {
      url = "https://" + url
    }
    await this.setState({
      registryUrl: url
    })

    // Checking for valid url
    try {
      new URL(url);
    } catch {
      toast.error("Invalid Store Url")
      return;
    }
    this.setState({
      btnHasFocus: false,
      errorMessage: ""
    })
    this.getAnotherRegistryProductsInfo();
  }

  registryUrlKeyUpHandler = (ev) => {
    ev.persist();
    ev.preventDefault();
    if (ev.keyCode === 13) { //Enter keycode is 13
      this.checkUrlAndGetRegistryItems()
    }
  }

  refineProductForBabyList = (products) => {
    // Removing products with no store
    return products.filter(product => product.title && product.stores && product.stores.length > 0)
      .map(product => {
        let newProduct = {...product}
        newProduct['price'] = newProduct.stores[0].price
        newProduct['url'] = newProduct.stores[0].url
        delete newProduct.stores
        return newProduct
      })

  }

  getAnotherRegistryProductsInfo = () => {

    this.setState({
      currentRegistryLink: this.state.registryUrl
    })
    if (this.inputRef.current) {
      this.inputRef.current.disabled = true;
    }

    const registryUrl = new URL(this.state.registryUrl);
    let storeName = registryUrl.hostname.split(".")

    if (storeName.length >= 2) {
      storeName = storeName[0] === "www" ? storeName[1] : storeName[0]
    } else {
      storeName = ""
    }


    const reqData = {
      store_name: storeName,
      store_url: this.state.registryUrl
    }

    this.changeLoadingState(true);

    getAnotherRegistryProducts(reqData).then(res => {
      try {
        if (res.data) {
          const products = res.data.products;

          if (products) {
            const registryUrl = new URL(this.state.registryUrl);
            let storeName = registryUrl.hostname.split(".")

            if (storeName.length >= 2) {
              storeName = storeName[0] === "www" ? storeName[1] : storeName[0]
            } else {
              storeName = ""
            }
            storeName = this.REGISTRY_STORE_ALTERNATIVE_NAME[storeName] ? this.REGISTRY_STORE_ALTERNATIVE_NAME[storeName] : storeName

            let newProducts = []

            if (String(storeName).toLowerCase() === IMPORT_REGISTRY_BABYLIST_TEXT) {
              newProducts = this.refineProductForBabyList(products)
            } else {
              newProducts = products.filter(product => product.title)
            }
            newProducts = newProducts.map(product => {
              product.categories = [];
              product.storeName = storeName
              return product;
            })
            this.props.dispatch(setAnotherStoreRegistryItems(newProducts))
            // this.setState({
            //   products: newProducts
            // })
            this.setState({
              errorState: "",
              errorMessage: ""
            })
          } else if (products === null) {
            this.setState({
              errorState: "no_products",
              errorMessage: "Unable to fetch products from this registry. Please check URL and try again."
            })
            this.props.dispatch(setAnotherStoreRegistryItems([]))
          } else if (res.data.store_name) {
            this.setState({
              errorState: "invalid_store",
              errorMessage: res.data.store_name[0]
            })
            this.props.dispatch(setAnotherStoreRegistryItems([]))
          } else {
            toast.error("Some error occurred while reading registry products.")
            this.props.dispatch(setAnotherStoreRegistryItems([]))
          }
        }
      } catch (e) {
        console.error(e)
        toast.error("Some error occurred while reading registry products.")
      }

    }).catch(err => {
      console.error(err.response);
      if (err.response && err.response.data && err.response.data.error_message) {
        this.setState({
          errorState: "backend",
          errorMessage: err.response.data.error_message
        })
      } else {
        toast.error("Some error occurred while fetching registry.")
      }
    }).finally(() => {
      this.changeLoadingState(false)
    })
  }

  openSelectCategoryModal = (ev, productIndex) => {
    ev.preventDefault()
    if (this.props.stateAnotherStoreRegistry[productIndex]) {
      this.setState({
        activeCategoryProductIndex: productIndex,
        categoryModalShow: true,
        currentCategory: this.props.stateAnotherStoreRegistry[productIndex].categories
      })
    } else {
      console.log("Invalid Product Index");
    }
  }

  setProductCategories = (categories) => {
    // Saving categories passed from SelectCategory Modal
    if (Number.isInteger(this.state.activeCategoryProductIndex) && this.props.stateAnotherStoreRegistry[this.state.activeCategoryProductIndex]) {
      let newProductState = [...this.props.stateAnotherStoreRegistry];
      // TODO: Change this to support multiple categories
      newProductState[this.state.activeCategoryProductIndex].categories = categories;
      this.props.dispatch(setAnotherStoreRegistryItems(newProductState))
      this.setState({
        activeCategoryProductIndex: null
      })
    }
  }

  removeSelectedCategory = (productIndex, categoryId) => {
    if (this.props.stateAnotherStoreRegistry[productIndex]) {
      let newProducts = [...this.props.stateAnotherStoreRegistry]
      newProducts[productIndex].categories = newProducts[productIndex].categories.filter(category => category.id !== categoryId);
      this.props.dispatch(setAnotherStoreRegistryItems(newProducts))
      // this.setState({
      //   products: newProducts
      // })
    }
  }

  openChangeConfirmModal = (e) => {
    e.preventDefault();
    this.setState({
      changeUrlConfirmationModal: true
    })
    this.props.changeTempCloseModal(true)
  }

  closeChangeConfirmModal = () => {
    this.setState({
      changeUrlConfirmationModal: false
    })
    this.props.changeTempCloseModal(false)
  }

  changeRegistryUrl = () => {
    this.props.dispatch(setAnotherStoreRegistryItems([]))
    this.doNotSearch = true
    this.closeChangeConfirmModal()
    setTimeout(() => {
      if (this.inputRef.current) {
        this.inputRef.current.disabled = false;
        this.inputRef.current.focus();
      }
    }, 50)
  }

  removeProductFromRegistry = (index) => {
    let newRegistryProduct = [...this.props.stateAnotherStoreRegistry]
    newRegistryProduct.splice(index, 1)
    this.props.dispatch(setAnotherStoreRegistryItems(newRegistryProduct))
  }

  inputKeyUpHandler = (ev) => {
    ev.persist();
    ev.preventDefault();
    if (ev.keyCode === 13 && ev.target.value.length > 0) { //Enter keycode is 13
      this.doNotSearch = true
      this.checkUrlAndGetRegistryItems()
    }
  }

  inputOnBlurHandler = (e) => {
    this.setState({inputHasFocus: false})
    if (e.target.value.length > 0 && !this.doNotSearch){
      this.checkUrlAndGetRegistryItems()
    }
    if (this.doNotSearch){
      this.doNotSearch = !this.doNotSearch
    }
  }

  render() {
    let notLoadingContent = null

    // For reference
    // if (this.state.errorState === "private_registry") {
    //   notLoadingContent = (
    //     <h4 className="mx-auto import-registry-no-content-text">
    //       Oops! This registry URL is private. Please change the setting to public and try again.
    //     </h4>
    //   )
    // } else
    if (this.state.errorState === "invalid_store") {
      notLoadingContent = (
        <>
          <h4 className="mx-auto import-registry-no-content-text">
            Sorry! The store you’re trying to import a registry from is not supported by the M+A registry. However, our
            Stork team can help you troubleshoot this.
          </h4>
          <Button variant="primary" className="text-body font-medium px-3 py-1 mt-3">Email M+A Storks</Button>
        </>)
    } else if (this.state.errorState) {
      notLoadingContent = (
        <h4 className="mx-auto import-registry-no-content-text">
          {this.state.errorMessage}
        </h4>
      )
    } else {
      notLoadingContent = <h4 className="mx-auto import-registry-no-content-text">
        Paste the registry URL above to import items to your M+A Registry.
      </h4>
    }

    return (
      <>
        <div className="baby-registry-import-registry">
          <div className="row">
            <div className="col d-flex flex-column">
              <span className="baby-registry-required-field text-sm font-medium">Registry URL</span>
              <div className="position-relative mt-1">
                <input type="text"
                       className={["import-registry-input-field w-100 mb-0 mw-100 text-body pl-2 py-1 pr-14", this.state.errorMessage ? "import-registry-error-field" : ""].join(" ")}
                       placeholder="Paste your registry URL here to import.  Please ensure it's publicly accessible."
                       value={this.state.registryUrl}
                       ref={this.inputRef}
                       onKeyUp={(ev) => this.inputKeyUpHandler(ev)}
                       onChange={this.registryUrlChangeHandler.bind(this)}
                       onFocus={() => this.setState({inputHasFocus: true, btnHasFocus: true})}
                       onBlur={(e) => this.inputOnBlurHandler(e)}
                />
                {this.state.registryUrl.length > 0 && this.state.currentRegistryLink === this.state.registryUrl && !this.state.inputHasFocus && !this.state.btnHasFocus ?
                  <a tabindex className="import-registry-change-link text-sm font-medium import-registry-category-link"
                     href="#"
                     onClick={(e) => this.openChangeConfirmModal(e)}>Change</a> : null}

                {this.state.registryUrl.length > 0 && (this.state.btnHasFocus) ?
                  <div className="import-registry-submit-url-section">
                    {/*TODO: Change icons once added to global icons*/}
                    <button className="url_edit_icons_cancel mr-3"
                            onClick={() => this.setState({registryUrl: "", btnHasFocus: false, errorMessage: ""})}
                    />
                    <button className="url_edit_icons_check" onClick={this.checkUrlAndGetRegistryItems}/>
                  </div>
                  : null}
              </div>
            </div>
          </div>

          <div className="row baby-registry-import-section">
            <div className="col pt-6">
              {this.props.stateAnotherStoreRegistry.length <= 0 || this.state.loading ?
                // When no product exists
                <div className="py-20 text-center import-registry-main-content">
                  {this.state.loading ?
                    <>
                      <div className="spinner-border import-registry-spinner" role="status">
                        <span className="sr-only">Loading...</span>
                      </div>
                      <h4 className="mt-1 mx-auto import-registry-loading-text">Please wait while we’re importing your
                        registry</h4>
                    </>
                    :
                    notLoadingContent
                  }
                </div>
                :
                // When products is filled
                <>
                  <p className="text-sm mb-2 import-registry-grey-text">Put your gifts in respective categories</p>
                  <div className="row">
                    <div className="col">
                      <div className="import-registry-main-content d-flex flex-column import-registry-products">
                        <div className="d-flex import-registry-products-column ">
                          <div className="w-50 pt-2 pb-1 px-3">
                            <p className="text-sm font-medium">Items</p>
                          </div>
                          <div className="w-50 pt-2 pb-1 px-3">
                            <p className="text-sm font-medium">Category: Item</p>
                          </div>
                        </div>


                        {this.props.stateAnotherStoreRegistry.map((product, index) => (
                          <div className="d-flex import-registry-products-column" key={"Product-" + index}>
                            <div className="w-50 px-3">
                              <p className="py-3 text-body">{product.title}</p>
                            </div>
                            <div className="w-50 px-3 d-flex justify-content-between">
                              {product.categories.length <= 0 ?
                                <div className="py-3">
                                  <a tabindex href="#" className="import-registry-category-link font-medium text-sm"
                                     onClick={(ev) => this.openSelectCategoryModal(ev, index)}>Select category</a>
                                </div>
                                :
                                <div className="pb-2">
                                  {product.categories.map(category => (
                                    <CategoryPill
                                      key={category.id}
                                      name={category.categoryName}
                                      mainCategory={category.mainCategory}
                                      removeCategory={() => this.removeSelectedCategory(index, category.id)}
                                      className="mt-2 mr-3"
                                    />
                                  ))}

                                  <a tabindex href="#"
                                     className="import-registry-category-link font-medium text-sm mt-2"
                                     onClick={(ev) => this.openSelectCategoryModal(ev, index)}>Add</a>
                                </div>
                              }
                              <div>
                                <button
                                  className="p-2 m-a-popover m-a-popover-top import-registry-item-delete-btn"
                                  onClick={() => this.removeProductFromRegistry(index)}
                                >
                                  <i className="icon-delete"/>
                                  <div className="m-a-popover-content import-registry-delete-popover p-2 text-sm">
                                    Delete
                                  </div>
                                </button>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </>
              }
            </div>
          </div>
        </div>
        <SelectCategory
          showModal={this.state.categoryModalShow}
          closeModal={this.setCategoryModalVisible}
          changeCurrentCategory={this.setProductCategories}
          currentCategory={this.state.currentCategory}
        />
        <Modal
          show={this.state.changeUrlConfirmationModal}
          size="md"
          closeIconClass="mb-auto"
          footer={(
            <>
              <Button variant="secondary" className="px-6 py-1" onClick={this.closeChangeConfirmModal}>No,
                cancel</Button>
              <Button variant="primary" className="px-6 py-1" onClick={this.changeRegistryUrl}>Yes, change</Button>
            </>
          )}
          closeModal={this.closeChangeConfirmModal}>
          You may lose the items on this screen. Are you sure you want to change?
        </Modal>
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateAnotherStoreRegistry: state.myRegistry.anotherStoreRegistry
  }
}

export default connect(
  mapStateToProps
)(ImportRegistry);