const MARK_AS_PURCHASED_BY_GUEST = `
mutation markAsPurchasedByGuest(
  $registryUrlSlug: String!,
  $registryItemId: Int!,
  $isPurchased: Boolean!,
  $storeName: String,
  $purchasedQuantity: Int,
  $contributionAmount: Float,
  $name: String!,
  $email: String!,
  $giftMessage: String,
  $orderNumber: String,
  ){
  markAsPurchasedByGuest(registryUrlSlug: $registryUrlSlug,registryItemId: $registryItemId ,purchasedRegistryItemInput:{
    isPurchased: $isPurchased,
    storeName: $storeName,
    purchasedQuantity: $purchasedQuantity,
    contributionAmount: $contributionAmount,
    giftMessage: $giftMessage,
    orderNo: $orderNumber,
  },guestUserInput:{
    name: $name,
    email: $email
  }){
    message,
    status
  }
}
`;

