import React from "react";
import "./AddCashFundModal.scss";
import Modal from "../../modal-component/ModalComponent";
import Button from "../../button-component/ButtonComponent";
import {toast} from "react-toastify";
import TogglePills from "./toggle-pills/TogglePills";
import {connect} from "react-redux";
import {graphQL, graphQLMultipart} from "../../../../../api/axios";
import {GET_CASH_FUND_CATEGORIES} from "../../../../../api/queries";
import {ADD_REGISTRY_ITEM, UPDATE_REGISTRY_ITEMS} from "../../../../../api/mutations";
import {setCashFundCategories} from "../../../../my-registry/MyRegistryActions";
import {CURRENCY, M_A_STORE_NAME} from "../../../../../utils/constants";
import {
  dataURItoBlob,
  getCategories,
  getProducts,
  fetchDefaultCashFundImage, checkForBankAccount, formatPrice, productImageLoadErrorHandler, productImageLoadHandler
} from "../../../../../utils/commonFunction";
import {Dropdown} from 'reactjs-dropdown-component';
import {setLoader} from "../../../../registry/RegistryActions";
import GiftStatusPill from "../../gift-status-pill/GiftStatusPill";

class AddCashFundModal extends React.Component {

  PRICE_REGEX = /(^[0-9]+(.?[0-9]{1,2})?$)/g
  PRICE_REGEX_WHILE_ENTERING = /^[0-9]+.{0,1}[0-9]{0,2}?$/g

  API_CALLED = false

  // Accepted Image input types for Cash fund image
  ACCEPTED_IMAGE_TYPE = [
    'image/jpg',
    'image/jpeg',
    'image/png'
  ]
  MAX_IMAGE_SIZE = 5000000; // 5MB

  INITIAL_IMAGE_SET = false

  // Cash Fund data
  state = {
    cashFundImage: {},
    cashFundName: this.props.categoryData && !this.props.edit ? this.props.categoryData.registryCategories[0].category.categoryName : "",
    cashFundAmount: "",
    hideTotal: false,
    cashFundType: {},
    cashFundDefaultImage: "",
    cashFundNote: "",
    mustHave: false,
    categories: [],
    errors: {},
    edit: false
  }

  imageRef = React.createRef()

  modalCloseHandler = (closeAll = false) => {
    if (this.props.fromAddGift) {
      this.props.closeModal(closeAll);
    } else {
      this.props.closeModal();
    }
  }

  validateCashFundData = () => {
    let valid = true;
    let errors = {}
    if (this.state.cashFundName.length <= 0) {
      errors['cashFundName'] = "Please enter Cash Fund Name."
      valid = false;
      // toast.error("Please enter Cash Fund Name.")
      // return false;
    }

    if (this.state.cashFundAmount.length <= 0) {
      errors['cashFundAmount'] = "Please enter Cash Fund Amount"
      valid = false;
      // toast.error("Please enter Cash Fund Amount")
      // return false;
    } else if (!this.state.cashFundAmount.match(this.PRICE_REGEX)) {
      valid = false;
      errors['cashFundAmount'] = "Please enter valid amount"
    } else if (this.props.edit && Number(this.state.cashFundAmount) < this.props.categoryData?.quantityStatus.total_contribution_amount) {
      valid = false;
      errors['cashFundAmount'] = "Amount is less than already contributed amount."
    }

    let selectedCat = this.state.categories.find(category => category.selected)
    if (!selectedCat) {
      errors['cashFundType'] = "Please select Cash Fund Type."
      valid = false;
      // toast.error("Please select Cash Fund Type.")
      // return false;
    }

    this.setState({
      errors: errors
    })

    return valid;
  }

  // Adding Cash Fund
  addCashFundClickHandler = () => {
    if (this.validateCashFundData() && !this.API_CALLED) {
      let formData = new FormData()
      let selectedCat = this.state.categories.find(category => category.selected)
      let itemData = {
        title: this.state.cashFundName,
        price: this.state.cashFundAmount,
        mostWanted: this.state.mustHave,
        quantity: 1,
        isGroupGift: true,
        hideTotal: this.state.hideTotal,
        note: this.state.cashFundNote,
        registryCategories: [Number(selectedCat.id)]
      }

      if (!this.props.edit) {
        itemData['isCashFund'] = true
        itemData['tags'] = []
      }

      let reqData = {}
      let mutationCalled = ""

      if (this.props.edit) {
        mutationCalled = "updateRegistryItems"
        reqData = {
          query: UPDATE_REGISTRY_ITEMS,
          operationName: "updateRegistryItem",
          variables: {
            "itemId": this.props.categoryData.id,
            "itemData": itemData
          }
        }
      } else {
        mutationCalled = "addRegistryItems"
        reqData = {
          query: ADD_REGISTRY_ITEM,
          operationName: "addRegistryItems",
          variables: {
            registryId: this.props.stateRegistryInfo.id,
            input: [{
              storeName: M_A_STORE_NAME,
              items: [itemData]
            }]
          }
        }
      }


      if (this.state.cashFundImage.type === "custom") {
        let map = {}
        if (this.props.edit) {
          map = {
            images: ["variables.itemData.images"]
          }
          reqData.variables.itemData['images'] = null;
          reqData.variables.itemData['imageUrl'] = null;
        } else {
          map = {
            images: ["variables.input.0.items.0.images"]
          }
          reqData.variables.input[0].items[0]['images'] = null;
        }

        formData.append("map", JSON.stringify(map))
        formData.append("images", dataURItoBlob(this.state.cashFundImage.value, this.state.cashFundImage.fileType, this.state.cashFundImage.fileName))
      } else {
        if (this.props.edit) {
          reqData.variables.itemData['images'] = null;
          reqData.variables.itemData['imageUrl'] = this.state.cashFundImage.value ? this.state.cashFundImage.value : this.state.cashFundDefaultImage;
        } else {
          reqData.variables.input[0].items[0]['imageUrl'] = this.state.cashFundImage.value ? this.state.cashFundImage.value : this.state.cashFundDefaultImage;
        }
      }
      formData.append("operations", JSON.stringify(reqData))
      this.API_CALLED = true
      this.props.dispatch(setLoader(true))
      graphQLMultipart(formData).then(async res => {
        await this.props.dispatch(setLoader(false))
        if (res.data.data && res.data.data[mutationCalled]) {
          // Successfully added product
          getProducts(this.props.stateRegistryInfo.id, this.props.dispatch);
          getCategories(this.props.stateRegistryInfo.id, this.props.dispatch);
          if (this.props.edit) {
            toast.success((
              <p><b>{this.state.cashFundName}</b> updated successfully!</p>
            ))
          } else {
            toast.success((
              <p><b>{this.state.cashFundName}</b> added to your registry!</p>
            ))
          }

          // Checking if cash fund is added without bank account
          if (!this.props.edit) {
            checkForBankAccount(true, false, false, this.props.dispatch);
          }
          this.modalCloseHandler(true)
        } else if (res.data.errors) {
          this.API_CALLED = true
        }
      }).catch(async err => {
        await this.props.dispatch(setLoader(false))
        this.API_CALLED = true
        if (err.response.data && err.response.data.errors) {
          for (let error of err.response.data.errors) {
            toast.error(error.message);
          }
        }
        console.error(err.response)
      })
    }
  }

  presentCategories = (allCategories) => {
    let categories = allCategories.map(category => {
      let categoryData = {
        id: category.id,
        title: category.categoryName,
        selected: this.state.cashFundType && this.state.cashFundType.id === category.id,
        key: "categories"
      }
      if (category.catId) {
        categoryData['catId'] = category.catId
      }
      return categoryData
    })
    this.setState({categories: [...categories]})
    if (this.state.cashFundType && this.state.cashFundType.catId) {
      this.fetchDefaultImage(this.state.cashFundType.catId);
    }
  }

  resetThenSet = (id, key) => {
    this.INITIAL_IMAGE_SET = false
    if (this.state.errors.cashFundType) {
      let newErrorState = {...this.state.errors}
      delete newErrorState.cashFundType
      this.setState({
        errors: newErrorState
      })
    }
    this.setState({
      categories: this.state.categories.map(category => {
        category.selected = category.id === id;
        return category;
      })
    })
    let cashFund = this.state.categories.find(category => category.id === id)
    if (cashFund.catId) {
      this.fetchDefaultImage(cashFund.catId)
    }
  }

  // Modal Data
  modalTitle = `${this.props.edit ? 'Edit' : 'Add'} Cash Fund`;
  modalFooterButton = (
    <Button
      variant="primary"
      className="px-5 py-1 font-medium text-body"
      onClick={this.addCashFundClickHandler}
    >{`${this.props.edit ? 'Save' : 'Add'} & Close`}</Button>
  )

  // Handling custom image selection
  selectFileHandler = (ev) => {
    if (ev.target && ev.target.files.length > 0) {
      const file = ev.target.files[0]

      // Verifying file is image
      if (this.ACCEPTED_IMAGE_TYPE.includes(file.type)) {
        if (file.size <= this.MAX_IMAGE_SIZE) {
          let reader = new FileReader();

          reader.onload = (e) => {
            const newProductImage = {
              type: "custom",
              value: e.target.result,
              fileType: file.type,
              fileName: file.name
            }
            this.setState({
              cashFundImage: newProductImage
            })
          }

          reader.readAsDataURL(file); // Converting to base64 string
        } else {
          toast.error("File size exceeds the limit");
        }
      } else {
        toast.error("Please select valid image.");
      }
    }
  }


  // Input change handlers
  cashFundNameChangeHandler = (ev) => {
    if (this.state.errors.cashFundName) {
      let newErrorState = {...this.state.errors}
      delete newErrorState.cashFundName
      this.setState({
        errors: newErrorState
      })
    }

    this.setState({
      cashFundName: ev.target.value
    })
  }

  cashFundAmountChangeHandler = (ev) => {

    if ((ev.target.value.length > 0 && (Number(ev.target.value[ev.target.value.length - 1]) >= 0 || ev.target.value[ev.target.value.length - 1] === ".")) && ev.target.value.match(this.PRICE_REGEX_WHILE_ENTERING) || ev.target.value.length <= 0) {
      if (this.state.errors.cashFundAmount) {
        let newErrorState = {...this.state.errors}
        delete newErrorState.cashFundAmount
        this.setState({
          errors: newErrorState
        })
      }

      this.setState({
        cashFundAmount: ev.target.value
      })
    }
  }

  cashFundHideTotalChangeHandler = (_) => {
    this.setState((prevState, _) => ({
      hideTotal: !prevState.hideTotal
    }))
  }

  cashFundTypeChangeHandler = (ev) => {
    if (this.props.stateCashFundCategories.length > 0) {
      const changedCategory = this.props.stateCashFundCategories.find(category => Number(category.id) === Number(ev.target.value))
      if (changedCategory.id) {
        this.setState({
          cashFundType: changedCategory
        })
      }
    }
  }

  cashFundNoteChangeHandler = (ev) => {
    this.setState({
      cashFundNote: ev.target.value
    })
  }

  mustHaveClickHandler = () => {
    this.setState((prevState, _) => ({
      mustHave: !prevState.mustHave
    }))
  }

  // Fetch DEFAULT IMAGE of category

  fetchDefaultImage = async (categoryId) => {
    if (!this.state.cashFundImage.value && !this.INITIAL_IMAGE_SET) {
      let result = fetchDefaultCashFundImage(categoryId)
      result.then(res => {
        if (res.status === "success") {
          this.setState({
            cashFundDefaultImage: res.data
          })
        }
      })
    }
  }

  render() {
    if (this.props.categoryData?.imageUrl && !this.state.cashFundDefaultImage && this.state.cashFundDefaultImage !== this.props.categoryData.imageUrl) {
      this.setState({
        cashFundDefaultImage: this.props.categoryData.imageUrl
      })
      this.INITIAL_IMAGE_SET = true
    }

    if (this.props.edit && !this.state.edit) {
      if (this.props.categoryData) {
        const data = this.props.categoryData

        this.setState({
          edit: true,
          cashFundNote: data.note ? data.note : "",
          mustHave: data.mostWanted ? data.mostWanted : false,
          cashFundName: data.title ? data.title : "",
          cashFundAmount: String(data.item?.price) ? String(data.item.price) : "",
          hideTotal: data.item?.hideTotal ? data.item.hideTotal : false
        })
      }
    }

    if (this.props.stateCashFundCategories.length > 0 && this.state.categories.length <= 0) {
      this.presentCategories(this.props.stateCashFundCategories)
    }

    if (this.props.stateCashFundCategories.length <= 0) {
      // Fetching all cash fund categories
      const reqData = {
        query: GET_CASH_FUND_CATEGORIES,
        operationName: "getCashFund",
        variables: {
          registryId: this.props.stateRegistryInfo.id
        }
      }

      graphQL(reqData).then(res => {
        if (res.data.data && res.data.data.cashFundCategories) {
          let allCashFundCategories = res.data.data.cashFundCategories.map(cat => {
            cat.category['catId'] = cat.category.id
            cat.category.id = cat.id
            return cat.category
          })
          this.presentCategories(allCashFundCategories);
          this.props.dispatch(setCashFundCategories(allCashFundCategories))
        }
      }).catch(err => {
        console.error(err.response)
      })
    }


    if (!this.state.cashFundType.id && this.props.stateCashFundCategories.length > 0) {
      let foundCategory = false

      if (this.props.categoryData?.registryCategories && this.props.categoryData.registryCategories.length > 0) {
        for (let cashFundCategory of this.props.stateCashFundCategories) {
          if ((this.props.edit && Number(this.props.categoryData.registryCategories[0].category.id) === Number(cashFundCategory.catId)) || (!this.props.edit && Number(this.props.categoryData.registryCategories[0].category.id) === Number(cashFundCategory.id))) {
            foundCategory = true;
            (
              async () => {
                await this.setState({
                  cashFundType: cashFundCategory
                })
                this.presentCategories(this.props.stateCashFundCategories)
              }
            )()
            break
          }
        }
      }
      //For future ref
      //Default category will be first
      // if (!foundCategory) {
      //   (
      //     async () => {
      //       await this.setState({
      //         cashFundType: this.props.stateCashFundCategories[0]
      //       })
      //       this.presentCategories(this.props.stateCashFundCategories)
      //     }
      //   )()
      // }
    }

    let titleContent = null;
    if (this.props.categoryData?.quantityStatus) {
      titleContent = (
        <div className="my-2">
          <div className="d-flex w-100 align-items-center flex-wrap">
            {this.props.categoryData.quantityStatus && this.props.categoryData.quantityStatus.total_contribution_amount <= 0 ?
              <GiftStatusPill
                text={`${formatPrice(this.props.categoryData.item.price * this.props.categoryData.quantity, CURRENCY)} Requested`}
                className="d-inline-block mr-1"/>
              :
              this.props.categoryData.isPurchased ?
                <GiftStatusPill variant="purchased"
                  // text="Purchased"
                                text={`${formatPrice(this.props.categoryData.quantityStatus.total_contribution_amount, CURRENCY)} Received`}
                                className="d-inline-block mr-1"/>
                :
                <div className="flex-grow-1">
                      <span
                        className="text-sm font-medium">{`Received ${formatPrice(this.props.categoryData.quantityStatus.total_contribution_amount, CURRENCY)}`}</span>
                  <span
                    className="text-sm">{` of ${formatPrice(this.props.categoryData.item.price * this.props.categoryData.quantity, CURRENCY)}`}</span>
                  <div className="added-card-group-gift-progress-bar w-50">
                    <div className="added-card-group-gift-progress mw-100"
                         style={{width: `${(this.props.categoryData.quantityStatus.total_contribution_amount / (this.props.categoryData.item.price * this.props.categoryData.quantity)) * 100}%`}}/>
                  </div>
                </div>
            }
          </div>
        </div>
      )
    }

    return (
      <>
        <Modal
          show={this.props.show}
          titleContent={this.props.edit ? titleContent : null}
          noBodyPadding={true}
          title={this.modalTitle}
          footer={this.modalFooterButton}
          closeModal={() => this.modalCloseHandler(false)}>

          <div className="row cash-fund-modal-content">
            <div className="col-12 col-md-5">
              <div className="cash-fund-modal-image-section mt-5 mr-1">
                {this.state.cashFundImage.type ?
                  <img src={this.state.cashFundImage.value} draggable="false" alt="Cash Fund"
                       className="cash-fund-modal-image"/>
                  :
                  this.state.cashFundDefaultImage ?
                    <img src={this.state.cashFundDefaultImage} draggable="false" alt="Cash Fund"
                         onError={(ev) => productImageLoadErrorHandler(ev)}
                         onLoad={(ev) => productImageLoadHandler(ev)}
                         className="cash-fund-modal-image"/>
                    :
                    <div className="cash-fund-modal-no-image"/>
                }
                <i
                  className="custom-icon-camera change-cash-fund-image-icon p-3"
                  onClick={() => {
                    this.imageRef.current.click()
                  }}
                />
                <input
                  type="file"
                  className="d-none"
                  ref={this.imageRef}
                  accept={this.ACCEPTED_IMAGE_TYPE.join(",")}
                  onChange={(ev) => this.selectFileHandler(ev)}
                />
              </div>
            </div>
            <div className="col-12 col-md-7 cash-fund-input-section">
              <div className="ml-3 mr-6 mt-5">

                {/* Cash Fund name */}
                <div className="row">
                  <div className="col cash-fund-input-container">
                    <p className="cash-fund-input-label text-sm font-medium mt-2 mt-md-0">Cash Fund Name<label
                      className="asterik">*</label></p>
                    <input
                      className={["cash-fund-input h2 mt-1 w-100 mw-100 p-2 mb-0", this.state.errors.cashFundName ? "cash-fund-input-error" : ""].join(" ")}
                      type="text"
                      value={this.state.cashFundName}
                      onChange={(ev) => this.cashFundNameChangeHandler(ev)}
                    />
                    {this.state.errors.cashFundName ? <label
                        className="text-sm cash-fund-input-error-text mt-1">{this.state.errors.cashFundName}</label>
                      : null}
                  </div>
                </div>

                {/* Cash Fund type */}
                <div className="row mt-5">
                  <div className="col cash-fund-input-container">
                    <p className="cash-fund-input-label text-sm font-medium mb-1">Type<label
                      className="asterik">*</label></p>
                    <div
                      className={["cash-fund-select w-100 mw-100", this.state.errors.cashFundType ? "cash-fund-input-error" : ""].join(" ")}>
                      <Dropdown
                        title="Select category"
                        list={this.state.categories}
                        resetThenSet={this.resetThenSet}
                      />
                    </div>
                    {this.state.errors.cashFundType ? <label
                        className="text-sm cash-fund-input-error-text mt-1">{this.state.errors.cashFundType}</label>
                      : null}
                  </div>
                </div>


                <div className="row mt-5">

                  <div className="col cash-fund-input-container">
                    <p className="cash-fund-input-label text-sm font-medium mb-1">Total Amount<label
                      className="asterik">*</label></p>
                    <div className="row">

                      {/* Cash fund amount */}
                      <div className="col">
                        <div className="position-relative">
                          <input
                            className={["cash-fund-input w-100 mw-100 p-1 pl-5 text-body mb-0", this.state.errors.cashFundAmount ? "cash-fund-input-error" : ""].join(" ")}
                            type="text"
                            value={this.state.cashFundAmount}
                            onChange={(ev) => this.cashFundAmountChangeHandler(ev)}
                          />
                          <span className="cash-fund-price-input-currency text-body">$</span>
                        </div>
                      </div>

                      {/* Cash fund hidden option */}
                      <div className="col align-self-center">
                        <label
                          className="checkbox d-flex align-items-center">
                          <span className="checkbox__input mr-2">
                            <input
                              type="checkbox"
                              checked={this.state.hideTotal}
                              onChange={(ev) => this.cashFundHideTotalChangeHandler(ev)}
                            />
                            <span className="checkbox__control d-flex justify-content-center align-items-center">
                              <i className="icon-check"/>
                            </span>
                          </span>
                          <span
                            className="radio__label text-sm">Hide total from guests</span>
                        </label>
                      </div>
                    </div>
                    {this.state.errors.cashFundAmount ? <label
                        className="text-sm cash-fund-input-error-text mt-1">{this.state.errors.cashFundAmount}</label>
                      : null}
                  </div>
                </div>

                <div className="row mt-5">
                  <div className="col">
                    <TogglePills
                      text="Must Have"
                      iconClass="custom-icon-heart"
                      selectedClass={this.state.mustHave ? "cash-fund-must-have-selected custom-icon-heart-filled" : null}
                      onClick={this.mustHaveClickHandler}
                    />

                  </div>
                </div>


                {/* Cash fund note */}
                <div className="row mt-5 mb-3">
                  <div className="col cash-fund-input-container">
                    <p className="cash-fund-input-label text-sm font-medium">Notes</p>
                    <textarea
                      placeholder="Write a note for your friends and family"
                      className="cash-fund-textarea w-100 mw-100 mt-1 w-100 mw-100 p-1 text-body mb-0"
                      value={this.state.cashFundNote}
                      onChange={(ev) => this.cashFundNoteChangeHandler(ev)}
                      rows={2}/>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </Modal>
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateCashFundCategories: state.myRegistry.cashFundCategories,
    stateRegistryInfo: state.registry.info
  }
}

export default connect(
  mapStateToProps
)(AddCashFundModal);