import React, { Component } from "react";
import "./CheckListItems.scss";
import CircularProgress from "modules/commons/components/circular-progress/CircularProgress";
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import { connect } from 'react-redux';

class CheckListItems extends Component {

  constructor(props) {
    super(props);
    // create a ref to store the checkList header DOM element
    this.checkListHeader = React.createRef();
  }

  activeCategory = null

  componentDidMount() {
    // window.addEventListener('scroll', this.handleScroll);
  }

  getOffset = (el) => {
    const rect = el.getBoundingClientRect();
    return {
      left: rect.left + window.scrollX,
      top: rect.top + window.scrollY
    };
  }

  scrollToCategory = (category) => {
    const categorySection = document.getElementById(category)
    if (categorySection) {
      let offset = this.getOffset(categorySection);
      window.scrollTo(0, offset.top)
    }
  }

  handleScroll = () => {
    let elements = document.getElementsByClassName('product-category-wrapper')
    for (let el of elements) {
      let otherEl = document.getElementById(el.id + '_scroll')
      if (!!otherEl && this.activeCategory !== el.id) {
        otherEl.classList.remove('font-medium')
      }
      if (el.getBoundingClientRect().top >= 0 && el.getBoundingClientRect().top <= el.offsetHeight) {
        this.activeCategory = el.id
        let targetEl = document.getElementById(el.id + '_scroll')
        if (targetEl) {
          targetEl.classList.add('font-medium')
        }
      }
    }
  }

  getSubList = (getSubList) => {
    let subItems = getSubList.map(({ id, categoryName, addedItemsCount, totalItemsCount, subProductTypes, parentCategory }, index) => {
      return (
        <div key={`subItems-${index}`}>
          {subProductTypes.length === 0 ?
            <div className="d-flex card-body-contain checklist-item-highlight align-items-center py-2" key={categoryName + id}
              data-cardid={this.props.index}
              id={parentCategory.name + '(' + categoryName + ')_scroll'}
              onClick={() => this.scrollToCategory(parentCategory.name + '(' + categoryName + ')')}>
              <p className="mb-0 card-body-header text-sm">{categoryName}</p>

              <div className='ml-auto mb-0 align-self-end text-sm progess-divison'>
                {addedItemsCount >= totalItemsCount ?
                  <div className="icon-done" /> :
                  (totalItemsCount > 0 ?
                    <div className='ml-auto mb-auto align-self-end card-progress'>
                      {addedItemsCount}/{totalItemsCount}
                    </div>
                    : null)}
              </div>
            </div> :
            // Todo handle scroll
            <>
              <div className="sub-product-header align-items-center py-2" key={categoryName + id}
                data-cardid={this.props.index}
                id={categoryName + '_scroll'}
                onClick={() => this.scrollToCategory(categoryName)}>
                <p className="mb-0 card-body-header text-sm">{categoryName}</p>
              </div>
              <div className="text-sm">
                {subProductTypes.map((subProductType, _) => (
                  <div className="sub-product-list checklist-item-highlight d-flex py-2 ml-3 align-items-center"
                    key={subProductType.categoryName + subProductType.id}
                    data-cardid={this.props.index}
                    id={subProductType.parentCategory.name + '(' + subProductType.categoryName + ')_scroll'}
                    onClick={() => this.scrollToCategory(subProductType.parentCategory.name + '(' + subProductType.categoryName + ')')}
                  >
                    <div>{subProductType.categoryName}</div>
                    <div className='ml-auto mb-0 align-self-end text-sm progess-divison'>
                      {subProductType.addedItemsCount >= subProductType.totalItemsCount ?
                        <div className="icon-done" /> :
                        (totalItemsCount > 0 ?
                          <div className='ml-auto mb-auto align-self-end card-progress'>
                            {subProductType.addedItemsCount}/{subProductType.totalItemsCount}
                          </div>
                          : null)}

                    </div>
                  </div>
                ))}

              </div>
            </>}
        </div>
      )

    });
    return subItems;
  }

  handleToggleEvent = () => {
    const checkListIconEl = this.checkListHeader.current.firstChild;
    checkListIconEl.classList.toggle("closed");
    checkListIconEl.classList.toggle("opened");
    const currentIndex = checkListIconEl.getAttribute('data-index');

    // removing toggle from other card
    let allElement = document.getElementsByClassName("checklist-icon-arrow");
    for (let el of allElement) {
      if (currentIndex !== el.getAttribute('data-index') && el.classList.contains("opened")) {
        el.classList.remove("opened");
      }
    }
  }

  render() {

    let subListItem = this.getSubList(this.props.item.subCategories)
    // TODO: For calculation for subitem
    // let parentTotal = 10;
    // let parentAdded = 16;

    // for (let subItems of this.props.item.subCategories) {
    //     if (subItems.added !== subItems.total) {
    //         parentAdded += Number(subItems.added);
    //         parentTotal += Number(subItems.total);
    //     }
    // }

    return (
      <>
        {this.props.item.categoryName && this.props.item.isParent ?
          <Card className="mb-5">
            <Accordion.Toggle
              onClick={this.handleToggleEvent}
              as={Card.Header}
              id={'checklist-header-' + this.props.index}
              // eventKey={this.props.item.categoryName}
              eventKey={this.props.index}
            >
              <div ref={this.checkListHeader} className='d-flex icon-checklist'>
                <span className={["checklist-icon-arrow", this.props.defaultActiveKey === this.props.index ? "opened" : "closed"].join(" ")} data-index={this.props.index} />
                <div className="ml-3 mb-0 w-100">
                  <div className="text-body">{this.props.item.categoryName}</div>
                  <div className="d-flex">
                    <div className="check-list-hearder-background">
                      <div className="check-list-hearder-progress mw-100" style={{ width: `${(this.props.item.addedItemsCount / this.props.item.totalItemsCount) * 100}%` }} />
                    </div>
                  </div>
                </div>
              </div>
            </Accordion.Toggle>
            <Accordion.Collapse className='mt-1 ml-6' eventKey={this.props.index}>
              <Card.Body>
                {subListItem}
              </Card.Body>
            </Accordion.Collapse>
          </Card>
          :
          <p
            className="pl-6 mr-3 text-body others-cat checklist-item-highlight"
            id={this.props.item.categoryName + '(' + this.props.item.categoryName + ')_scroll'}
            onClick={() => this.scrollToCategory(this.props.item.categoryName + '(' + this.props.item.categoryName + ')')}>
            {this.props.item.categoryName}
          </p>
        }
      </>
    );

  }
}

export default connect()(CheckListItems);