import React from "react";
import "./Filters.scss";
import {connect} from 'react-redux';
import CheckBox from "../../../commons/components/check-box/CheckBox";
import {setTrackGiftsFilters} from "../../TrackGiftsAction";
import {getTrackGiftsProduct} from "../../../../utils/commonFunction";
import defaultData from "data/ReducerDefaultData.json"

class Filters extends React.Component {

  filterChangeHandler = async filterId => {
    let allUnselected = true
    let newFilters = this.props.stateFilters.map(filter => {
      let newFilter = {...filter}
      if (newFilter.id === filterId) {
        newFilter.selected = !newFilter.selected
      }
      if (newFilter.count > 0 && newFilter.selected) {
        allUnselected = false
      }
      return newFilter
    })

    // Resetting filter to default when all filter are unselected
    // if (allUnselected){
    //   newFilters = defaultData.trackGifts.filters
    // }

    await this.props.dispatch(setTrackGiftsFilters(newFilters))
    getTrackGiftsProduct(true, newFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)
  }

  render() {
    return (
      <div className="track-gifts-filters">
        <p className="text-sm">Filters</p>
        <div className="mt-3">
          {this.props.stateFilters.map(filter => (
            filter.count > 0 ?
              <CheckBox
                display={filter.text}
                count={filter.count}
                checked={filter.selected ? "checked" : ""}
                id={`trackGiftFilter-${filter.id}`}
                className="mb-3"
                textClass="track-gifts-filter-text"
                changeHandler={() => this.filterChangeHandler(filter.id)}
              />
              :
              null
          ))}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  stateFilters: state.trackGifts.filters,
  stateTrackGiftsView: state.trackGifts.activeView,
  stateTrackGiftsProducts: state.trackGifts.products,
  stateTrackGiftsPagination: state.trackGifts.paginationInfo,
  stateRegistryInfo: state.registry.info,
  stateTrackGiftsSearch: state.trackGifts.search
})

export default connect(
  mapStateToProps
)(Filters);