import React from "react";
import "./ThankYouList.scss"
import {downloadThankYouList} from "../../../../api/axios";
import {connect} from 'react-redux';
import {withRouter} from "react-router";
import {getTodoList} from "../../../../utils/commonFunction";
import {ROUTES} from "utils/cdn-assets-links";

class ThankYouList extends React.Component {

  componentDidMount() {
    let queryParams = new URLSearchParams(window.location.search)
    if (queryParams.has('download-thank-you-list')) {
      this.downloadClickHandler()
      this.setState({
        showShareRegistry: true
      })
      queryParams.delete('download-thank-you-list')
      this.props.history.replace({
        search: queryParams.toString(),
      })
    }
  }

  saveData = (function () {
    const a = document.createElement("a");
    document.body.appendChild(a);
    a.style = "display: none";
    return function (data, fileName) {
      const blob = new Blob([data], {type: "octet/stream"}),
        url = window.URL.createObjectURL(blob);
      a.href = url;
      a.download = fileName;
      a.click();
      window.URL.revokeObjectURL(url);
    };
  }());

  downloadClickHandler = (ev = null) => {
    if (ev) {
      ev.preventDefault()
    }
    downloadThankYouList(this.props.stateRegistryInfo.id).then(res => {
      if (res.data) {
        // Converting csv response to downloaded file
        getTodoList(this.props.stateRegistryInfo.id, this.props.dispatch)
        this.saveData(res.data, `${this.props.stateRegistryInfo.displayName}-thank-you-list.csv`);
      }

    }).catch(err => {
      console.error({...err.response.data})
    })
  }

  render() {
    let displayThankYou = false;
    for (let filter of this.props.stateTrackGiftsFilters) {
      if (filter.id === 1) {
        displayThankYou = filter.count > 0;
        break;
      }
    }

    if (displayThankYou) {
      return (
        <div className="track-gifts-thank-you-list p-4 mr-0 mr-md-5">
          <img src={ROUTES.file_heart_svg}
               alt="Thank you list"/>
          <p className="mt-2 text-body font-medium">Thank you list</p>
          <p className="mt-1 text-sm track-gifts-thank-you-desc">Let your gift-givers know that you are thankful to
            them.</p>
          <a href="#" onClick={(ev) => this.downloadClickHandler(ev)}
             className="mt-1 track-gifts-thank-you-link text-sm">Download</a>
        </div>
      );
    } else {
      return null;
    }
  }
}

const mapStateToProps = state => ({
  stateRegistryInfo: state.registry.info,
  stateTrackGiftsFilters: state.trackGifts.filters
})

export default connect(
  mapStateToProps
)(withRouter(ThankYouList));