import React, {useEffect, useState} from 'react';
import "./SortFilterMobile.scss";
import {useSelector, useDispatch} from 'react-redux';
import {activeSortFilter} from '../../ViewRegistryActions';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import RadioButton from '../../../commons/components/radio-button/RadioButton';

const SortFilterMobile = () => {

  const dispatch = useDispatch()
  const activeSortFilterState = useSelector(state => state.viewRegistry.activeSortFilter)
  const [currentSort, setCurrentSort] = useState(1)
  const filterContentMobileRef = React.createRef();

  useEffect(() => {
    setCurrentSort(activeSortFilterState.sortId);
  })
  const toggleFilterContent = () => {
    const checkListIconEl = filterContentMobileRef.current.firstChild;
    checkListIconEl.classList.toggle("closed");
    checkListIconEl.classList.toggle("opened");
  }


  const handleSortSelection = (sortItem) => {
    if (currentSort !== sortItem.id) {
      setCurrentSort(sortItem.sortId)
      dispatch(activeSortFilter(sortItem))
    }
  }

  const sortByList = [
    {
      sortId: 1,
      sortTitle: 'Featured'
    },
    {
      sortId: 2,
      sortTitle: 'Price: Low to High'
    },
    {
      sortId: 3,
      sortTitle: 'Price: High to Low'
    }
  ]

  return (
    <div className="sort-section-mobile-filter w-100">
      <Accordion className="accordion-list mb-5">
        <Card>
          <Accordion.Toggle
            onClick={() => toggleFilterContent()}
            as={Card.Header}
            id="view-sort-filter"
            eventKey="view-sort-filter"
          >
            <div ref={filterContentMobileRef} className='d-flex icon-checklist'>
              <span className="checklist-icon-arrow" />
              <div className="ml-3 mt-1 w-100">
                <div className="text-body">Sort By</div>
              </div>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse className='ml-6 mt-3' eventKey="view-sort-filter">
            <Card.Body className="text-sm">
              {sortByList.map(sortItem => {
                return (
                  <div className="mb-1"> <RadioButton
                    centerText="true"
                    textClass="text-body"
                    name="viewSortFilter"
                    key={sortItem.sortId}
                    checked={sortItem.sortId === currentSort ? true : null}
                    changeHandler={() => handleSortSelection(sortItem)} >
                    {sortItem.sortTitle}
                  </RadioButton>
                  </div>
                );
              })}
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </div>
  )
}

export default SortFilterMobile;
