import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from 'react-redux';
import { setAlreadyBoughtProductInfo } from 'modules/guest-view/GuestViewAction';
import ProductDetailsModel from '../product-details-model/ProductDetailsModel';
import Counter from "../counter/Counter";
import { store } from "store/Store";
import './AlreadyBoughtProduct.scss';
import { LOCAL_STORAGE_GUEST_GIFT_BUY_KEY } from 'utils/constants';

const AlreadyBoughtProduct = (props) => {

  const dispatch = useDispatch();
  const stateRegistryInfo = useSelector(state => state.guestView.info)
  const productInfo = useSelector(state => state.guestView.alreadyBoughtProduct)
  const quantity = useSelector(state => state.guestView.alreadyBoughtProduct.quantity)
  const name = useSelector(state => state.guestView.alreadyBoughtProduct.name)
  const email = useSelector(state => state.guestView.alreadyBoughtProduct.email)
  const orderNumber = useSelector(state => state.guestView.alreadyBoughtProduct.orderNumber)
  const storeName = useSelector(state => state.guestView.alreadyBoughtProduct.storeName)
  const giftMessage = useSelector(state => state.guestView.alreadyBoughtProduct.giftMessage)
  const stateUserInfo = useSelector(state => state.registry)
  const [reservedQuantity, setReservedQuantity] = useState(null)

  useEffect(() => {
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    let foundUserData = false
    // Setting default value for email and quantity
    if (jsonVisitedItems) {
      for (let item of jsonVisitedItems) {
        if (Number(item.rId) === Number(stateRegistryInfo.id)) {
          if (Number(item.pId) === Number(props.data.id)) {
            setInfo(item.email, item.quantity, props.data.item.store.storeName)
            foundUserData = true
            setReservedQuantity(item.quantity)
          }
        }
      }
    }
    if (!foundUserData) {
      if (stateUserInfo.loggedIn) {
        for (let item of [...props.data.purchasedRegistryItem]) {
          if (item.guestUser.email === stateUserInfo.user.email) {
           setInfo(item.guestUser.email, item.purchasedQuantity, item.storeName)
           setReservedQuantity(item.purchasedQuantity)
          }
        }
      }
    }
  }, [])

  const setInfo = async (email, quantity, storeName) => {
    let prodInfo = { ...productInfo };
    prodInfo.email = email;
    prodInfo.storeName = storeName;
    prodInfo.quantity = quantity;
    await dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  const setQuantity = async (quantity) => {
    let prodInfo = { ...productInfo };
    prodInfo.quantity = quantity;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  const incrementQuantity = () => {
    let totalQuantityAvalible = reservedQuantity ? reservedQuantity : props.data.quantityStatus.still_needed;
    if (quantity < totalQuantityAvalible) {
      setQuantity(quantity + 1)
    }
  }

  const decrementQuantity = () => {
    if (quantity - 1 > 0) {
      setQuantity(quantity - 1)
    }
  }

  const nameChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('name')
    prodInfo.name = ev.target.value;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }


  const emailChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('email')
    prodInfo.email = ev.target.value;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  const storeNameChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('storeName')
    prodInfo.storeName = ev.target.value;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  const orderNumberChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    prodInfo.orderNumber = ev.target.value;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  const giftMessageChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    prodInfo.giftMessage = ev.target.value;
    dispatch(setAlreadyBoughtProductInfo(prodInfo));
  }

  return (
    <div className="row add-gift-another-store">
      <div className="col">
        <div className="row pb-2">
          <div className="col-12 col-md-5 py-4 add-gift-image-select">
            <div className="mr-3">
              <ProductDetailsModel data={props.data} />
            </div>
          </div>
          {/* // I Bought This Already Model Body */}
          <div className="col-12 col-md-7 mt-4">
            <div className="ml-3">
              <div className="row col">
                <div className="col">
                  <p className="font-body font-medium">Great! Let’s update their registry.</p>
                  <p className="text-sm mt-1">This information will help {stateRegistryInfo.user.firstName} {stateRegistryInfo.user.partnerUser ? 'and ' + stateRegistryInfo.user.partnerUser.firstName : ''} keep track of all their gifts.</p>
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label guest-view-required">Name</p>
                  <input
                    className={["w-100 mt-1 mb-0 px-2 py-1", props.fieldErrors.name ? "already-bought-input-error" : ""].join(" ")}
                    type='text'
                    placeholder='John Doe'
                    value={name}
                    onChange={(ev) => nameChangeHandler(ev)}
                  />
                  {props.fieldErrors.name ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.name}</span>
                    : null}
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label guest-view-required">Email Address</p>
                  <input
                    className={["w-100 mt-1 mb-0 px-2 py-1", props.fieldErrors.email ? "already-bought-input-error" : ""].join(" ")}
                    type='text'
                    placeholder='johndoe@email.com'
                    value={email}
                    onChange={(ev) => emailChangeHandler(ev)}
                  />
                  {props.fieldErrors.email ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.email}</span>
                    : null}
                </div>
              </div>
              <div className="row col mt-5">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Quantity</p>
                  <Counter
                    className="mt-2"
                    counter={quantity}
                    incrementHandler={incrementQuantity}
                    decrementHandler={decrementQuantity}
                  />
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Store Name</p>
                  <input
                    className={["w-100 mt-1 mb-0 px-2 py-1", props.fieldErrors.storeName ? "already-bought-input-error" : ""].join(" ")}
                    type='text'
                    placeholder='Eg. Target'
                    value={storeName}
                    onChange={(ev) => storeNameChangeHandler(ev)}
                  />
                  {props.fieldErrors.storeName ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.storeName}</span>
                    : null}
                </div>
                <div className="col m-a-input-container ml-3">
                  <p className="text-sm font-medium add-gift-store-label">Order Number</p>
                  <input
                    className="w-100 mt-1 mb-0 px-2 py-1"
                    type='text'
                    placeholder='0012345'
                    value={orderNumber}
                    onChange={(ev) => orderNumberChangeHandler(ev)}
                  />
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Gift Message</p>
                  <textarea className="w-100 mt-1 text-body p-1 m-a-resize-textarea mb-2"
                    rows="2"
                    placeholder="Write a message for gift receiver"
                    value={giftMessage}
                    onChange={(ev) => giftMessageChangeHandler(ev)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default AlreadyBoughtProduct;