export const GUEST_VIEW_ACTION_TYPES = {
	UPDATE_GUEST_VIEW_INFO: "UPDATE_GUEST_VIEW_INFO",
	SET_GUEST_VIEW_PRODUCT: "SET_GUEST_VIEW_PRODUCT",
	GUEST_ACTIVE_SORT_FILTER: "GUEST_ACTIVE_SORT_FILTER",

	// Filter
	SET_GUEST_VIEW_PARENT_FILTER: "SET_GUEST_VIEW_PARENT_FILTER",
	SET_GUEST_VIEW_CHILD_FILTER: "SET_GUEST_VIEW_CHILD_FILTER",
	CLEAR_GUEST_VIEW_FILTER: "CLEAR_GUEST_VIEW_FILTER",

	SET_GUEST_VIEW_FILTER_CATEGORY: "SET_GUEST_VIEW_FILTER_CATEGORY",
	GUEST_VIEW_SET_PAGINATION_INFO: "GUEST_VIEW_SET_PAGINATION_INFO",
	CLEAR_GUEST_PAGINATION_INFO: "CLEAR_GUEST_PAGINATION_INFO",

	GET_GUEST_CHECKLIST_ITEMS: "GET_GUEST_CHECKLIST_ITEMS",

	//I BOUGHT THIS ALREADY STORE PRODUCT
  SET_ALREADY_BOUGHT_PRODUCT_INFO : "SET_ALREADY_BOUGHT_PRODUCT_INFO",
	CLEAR_ALREADY_BOUGHT_PRODUCT_INFO : "CLEAR_ALREADY_BOUGHT_PRODUCT_INFO",

	//CASH CONTRIBUTED ALREADY INFO
  SET_CASH_CONTRIBUTED_ALREADY_INFO : "SET_CASH_CONTRIBUTED_ALREADY_INFO",
	CLEAR_CASH_CONTRIBUTED_ALREADY_INFO : "CLEAR_CASH_CONTRIBUTED_ALREADY_INFO",
	
	GUEST_GIFTS_SET_REDIRECTING_INFO: "GUEST_GIFTS_SET_REDIRECTING_INFO",
}

export const updateGuestViewInfo = (params) => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.UPDATE_GUEST_VIEW_INFO,
		payload: params
	}
}

export const setGuestViewProducts = params => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_PRODUCT,
		payload: params
	}
}

export const activeGuestSortFilter = (params) => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.GUEST_ACTIVE_SORT_FILTER,
		payload: params
	}
}

// Filter Section
export const setParentGuestViewFilter = params => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_PARENT_FILTER,
		payload: params
	}
}

export const setChildGuestViewFilter = params => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_CHILD_FILTER,
		payload: params
	}
}

export const clearGuestViewFilter = () => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.CLEAR_GUEST_VIEW_FILTER,
	}
}

// View Filter Category Section
export const setGuestViewFilterCategory = params => {
	return {
		type: GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_FILTER_CATEGORY,
		payload: params
	}
}

