import React from "react";
import "./CheckListItems.scss";
import { withTranslation } from "react-i18next";
import { withRouter } from "react-router";
import routes from "utils/routes";
import { GET_PERSONALIZE_QUESTIONS } from "api/queries";
import { graphQL } from "api/axios";
import { toast } from "react-toastify";
import {connect} from "react-redux";

class CheckList extends React.Component {

    gotoPersonalize = (event) => {
        event.preventDefault();

        // If user has given answer or not
        let isQuizPending = true
        let reqData = {
            query: GET_PERSONALIZE_QUESTIONS,
            variables: {
                registryId: this.props.stateRegistryInfo.id,
            }
        }
        // To route to edit-checklist or Quiz
        graphQL(reqData).then(res => {
            if (res.data.data.editablePersonalizeQuestions) {
                const questions = res.data.data.editablePersonalizeQuestions
                for (let question of questions) {
                    if (question.isPersonalize && question.answers.length > 0) {
                        isQuizPending = false
                        break;
                    }
                }
                if (isQuizPending) {
                    this.props.history.push(routes.PERSONALIZE.HOME)
                } else {
                    this.props.history.push(routes.PERSONALIZE.CHECKLIST)
                }

            }
        }).catch(err => {
            toast.error("Error occurred while fetching questions");
            console.error(err);
        })
    }

    render() {
        return (
            <div className="mb-0 mb-lg-3 w-100">
                <div className="row check-list-header">
                    <div className="col d-flex align-items-baseline pb-1">
                        <p className="checklist-title mb-0">{this.props.t("checkList.title")}</p>
                        <a href="#" className="ml-2 text-sm checklist-personalize d-none d-lg-inline-block" tabIndex="0" onClick={(e) => this.gotoPersonalize(e)}>{this.props.t("checkList.personalize")}</a>
                        {this.props.percentage.totalItems ?
                            <p className="font-medium text-sm checklist-progress-count ml-2 ml-lg-auto ">{this.props.percentage.addedItems}/{this.props.percentage.totalItems}</p>
                            : null}
                    </div>
                </div>
                <div className="row">
                    <div className="col d-flex">
                        <div className="check-list-background">
                            <div className="check-list-progress mw-100" style={{width: `${this.props.percentage.result}%`}} />
                        </div>
                    </div>
                </div>
                <div className="row check-list-header d-inline-block d-lg-none">
                    <a href="#" className="text-sm checklist-personalize ml-3 mt-3" tabIndex="0" onClick={(e) => this.gotoPersonalize(e)}>{this.props.t("checkList.personalize")}</a>
                </div>
            </div>

        );
    }
};

const mapStateToProps = state => {
    return {
        stateRegistryInfo: state.registry.info,
    }
}

export default withTranslation()(withRouter(connect(
    mapStateToProps
)(CheckList)));