import React from 'react';
import {connect} from "react-redux";
import AddGift from "modules/commons/components/add-gift/AddGift";
import SortFilter from "../sort-filter/SortFilter";
import './SortFilterSection.scss';
import {isGotIt, clearViewFilter, setChildViewFilter} from "modules/view-registry/ViewRegistryActions";
import {UPDATE_REGISTRY} from "api/mutations";
import {graphQL} from "api/axios";
import {updateRegistryDetail} from "utils/commonFunction";
import FilterItemChip from "modules/my-registry/components/filter/filter-item-chip/FilterItemChip";
import Button from "../../../commons/components/button-component/ButtonComponent";
import Modal from "../../../commons/components/modal-component/ModalComponent";
import ViewFilterMobile from "../view-filter/ViewFilterMobile";

class SortFilterSection extends React.Component {
  state = {
    showFilterModal: false,
    parentFilter: "",
    childFilter: []
  }

  doneViewFilterModal = () => {
    this.props.setFilterhandler()
    this.setState({showFilterModal: false})
  }

  clearViewFilterModal = () =>{
    this.props.dispatch(clearViewFilter())
    this.doneViewFilterModal()
  }

  closeViewFilterModal = () =>{
    this.props.dispatch(setChildViewFilter([...this.state.childFilter]));
    this.setState({showFilterModal: false})
  }

  handleIsGotIt = () => {
    let data = {
      query: UPDATE_REGISTRY,
      operationName: "updateRegistryDetail",
      variables: {
        "registryId": this.props.stateRegistryInfo.id,
        "input": {
          isFeaturedNoticeRead: true
        }
      }
    }
    graphQL(data).then(res => {
      try {
        if (res.data.data.updateRegistry.registry) {
          // this.props.dispatch(isGotIt(true))
          updateRegistryDetail(this.props.dispatch);
        } else {
          console.log(res.data.data.updateRegistry.errors);
          console.error("Some error occurred");
        }
      } catch (e) {
        console.error(e);
      }
    }).catch(err => {
      console.error(err.message)
    })
  }

  // Filter chips
  getFilterChips = (stateFilterApplied) => {
    let filterChips = {}
    if (stateFilterApplied) {
      let allChild = []
      for (let filterCat of this.props.stateFilterCategory) {
        for (let childCat of filterCat.child) {
          allChild.push(childCat)
        }
      }
      if (stateFilterApplied.childFilter) {
        filterChips.childFilter = [...stateFilterApplied.childFilter].map(item => {
          let finalAllChild = [...allChild].find(element => {
            return element.id === item
          })
          return finalAllChild
        })
      }
    }
    return filterChips;
  }

  // Filter chips close handler
  deleteChipHandler = async (isParent, childId) => {
    if (isParent) {
      await this.props.dispatch(clearViewFilter())
    } else {
      const newChildArray = this.props.stateFilterApplied.childFilter.filter(element => {
        return element !== childId
      })
      await this.props.dispatch(setChildViewFilter(newChildArray))
    }
    await this.props.setFilterhandler()
  }

  //Clear filter state
  clearFilterHandler = async (ev) => {
    ev.preventDefault()
    await this.props.dispatch(clearViewFilter())
    await this.props.setFilterhandler()
  }


  //filter previous states
  viewFilterStateHandler = () =>{
    this.setState({parentFilter: this.props.stateFilterApplied.parentFilter})
    this.setState({childFilter: this.props.stateFilterApplied.childFilter});
    this.setState({showFilterModal: true});
  }

  //open modal
  modalFooterButtons = (
		<>
			<Button
				variant="secondary"
				className="px-3 py-1 font-weight-bold font-medium text-body"
				disabled={false}
				onClick={this.clearViewFilterModal}
			>Clear</Button>
			<Button
				variant="primary"
				className="px-4 py-1 font-weight-bold font-medium text-body"
				disabled={false}
				onClick={this.doneViewFilterModal}
			>Done</Button>
		</>
	)

  render() {
    let filterChips = this.getFilterChips(this.props.stateFilterApplied)

    return (
      <>
        <div className="row align-items-center align-items-md-start">
          <div className="col-3">
            <h4 className="font-weight-bold mr-2 filter-section-total-items">{this.props.productCount} {this.props.productCount === 1 ? "Gift" : "Gifts"}</h4>
          </div>
          <div className="col filter-sort-section">
            <div className="d-flex justify-content-end">
              <div>
                <Button className="px-1 pt-1 px-lg-3 d-inline-block d-lg-none mr-1" variant="secondary" onClick={this.viewFilterStateHandler}>
                  <i className="icon-filter-1 filter-icon" />
                </Button>
              </div>
              <div className="text-sm sort-by-text mr-2 align-self-center d-none d-lg-inline-block">Sort by</div>
              <div className="d-none d-lg-inline-block"><SortFilter /></div>
              {this.props.stateGuestViewRegistry ?
                <></> :
                <div className="ml-0 ml-md-4 filter-add-gift-btn"><AddGift /></div>}
            </div>

          </div>
        </div>
        <div className="row">
          <div className="col-7">
            <div className="filtered-items">
              {filterChips.childFilter ?
                filterChips.childFilter.map(item =>
                  item ?
                    <FilterItemChip
                      text={item.display}
                      className="mr-2"
                      key={filterChips.id + "-" + item.id}
                      deleteHandler={() => this.deleteChipHandler(false, item.id)} /> : null )
                : null}
              {this.props.stateFilterApplied.childFilter.length > 0 ?
                <a className="font-medium text-sm filter-clear-link" href="#" tabindex="0"
                  onClick={(ev) => this.clearFilterHandler(ev)}>Clear
                all</a>
                : null}
            </div>
          </div>
        </div>
        {this.props.stateRegistryInfo.isFeaturedNoticeRead === false && this.props.stateActiveSortFilter.sortId !== 1 && !this.props.stateGuestViewRegistry ?
          <div className="row mt-2 mb-3">
            <div className="col">
              <div className="text-sm py-2 d-flex flex-wrap justify-content-center sort-filter-warning">
                <div>Enable rearrange gifts by</div>
                <div className="font-weight-bold px-1">clearing your filters</div>
                <div>and setting to</div>
                <div className="font-weight-bold px-1">Sort by: Featured.</div>
                <div className="sort-filter-warning-btn" onClick={this.handleIsGotIt}>Ok, got it.</div>
              </div>
            </div>
          </div> : <></>
        }

        <Modal
          show={this.state.showFilterModal}
          title={"Filters"}
          closeModal={this.closeViewFilterModal}
          footer={this.modalFooterButtons}
          >
          <ViewFilterMobile />
        </Modal>
      </>
    )
  }
}


const mapStateToProps = state => {
  return {
    stateViewRegistryProducts: state.viewRegistry.viewRegistryProducts,
    stateActiveSortFilter: state.viewRegistry.activeSortFilter,
    stateGuestViewRegistry: state.viewRegistry.guestViewRegistry,
    stateRegistryInfo: state.registry.info,
    stateFilterApplied: state.viewRegistry.viewFilterApplied,
    stateFilterCategory: state.viewRegistry.filterCategory
  }
}

export default connect(
  mapStateToProps
)(SortFilterSection);
