import React, {useState, createRef} from "react";
import {useDispatch, useSelector} from "react-redux";
import "./ShareRegistry.scss";
import Modal from "../../../commons/components/modal-component/ModalComponent";
import {UPDATE_REGISTRY, UPDATE_REGISTRY_URL} from "../../../../api/mutations";
import {graphQL} from "../../../../api/axios";
import {getTodoList, updateRegistryDetail} from "../../../../utils/commonFunction";
import {updateRegistryInfo} from "../../../registry/RegistryActions";
import Button from "../../../commons/components/button-component/ButtonComponent";
import {toast} from "react-toastify";
import routes from "../../../../utils/routes";
import {ROUTES as CDN_ROUTES} from "utils/cdn-assets-links";
import {SETTINGS_LINKS, TIME_TO_ENABLE_COPY_BTN} from "../../../../utils/constants";

const ShareRegistry = (props) => {

  const siteHostName = window.location.host + routes.REGISTRY.HOME;
  // const siteHostName = "monicaandandy.com";
  const dispatch = useDispatch()
  const [registryInfoUrl, setRegistryInfoUrl] = useState(props.registryInfo.urlSlug);
  const [registryVisibility, setRegistryVisibility] = useState(props.registryInfo.isPublic)
  const [registryUrl, setRegistryUrl] = useState(registryInfoUrl)
  const [registryError, setRegistryError] = useState("")
  const inputContainerRef = createRef();
  const [isPublicError, setIsPublicError] = useState("")

  let stateProducts = useSelector(state => state.myRegistry.products)

  let sharingText = "Check out my Monica and Andy baby registry - "
  let sharingUrl = window.location.origin + routes.REGISTRY.HOME + "/" + registryUrl

  let facebookShareLink = `https://www.facebook.com/sharer/sharer.php?u=${encodeURI(sharingUrl)}&quote=${sharingText}`
  let twitterShareLink = `https://twitter.com/share?url=${encodeURI(sharingUrl)}&text=${sharingText}`

  const closeModal = () => {
    setRegistryUrl(props.registryInfo.urlSlug)
    props.closeShareRegistry();
  }

  const changeRegistryVisibility = (value) => {
    if (props.hasNoProducts){
      toast.error("You cannot publish empty registry.");
      return
    }

    if (value === registryVisibility) {
      return;
    }

    if (!props.registryInfo.id) {
      console.log("No registry found!");
      return;
    }

    if (((props.registryInfo.isCashFundItemAdded || props.registryInfo.isCashInLieuItemAdded || props.registryInfo.isGroupGiftItemAdded) && value && props.registryInfo.user.bankAccounts.length <= 0)) {
      // toast.error("You cannot make your registry public since it contains products where we are collecting cash on your behalf and bank details are not added yet.")
      return
    }

    setRegistryVisibility(value);

    let data = {
      query: UPDATE_REGISTRY,
      operationName: "updateRegistryDetail",
      variables: {
        "registryId": props.registryInfo.id,
        "input": {
          isPublic: value
        }
      }
    }

    graphQL(data).then(res => {
      try {
        if (res.data.data.updateRegistry.registry) {
          let newRegistry = props.registryInfo
          newRegistry.isPublic = value
          dispatch(updateRegistryInfo(newRegistry))
          getTodoList(props.registryInfo.id, dispatch)
        } else {
          console.log(res.data.data.updateRegistry.errors);
          setRegistryVisibility(!value);
          toast.error("Some error occurred");
        }
      } catch (e) {
        setRegistryVisibility(!value);
        toast.error("Some error occurred");
        console.error(e);
      }
    }).catch(err => {
      setRegistryVisibility(!value);
      if (err.response && err.response.data && err.response.data.errors) {
        for (let error of err.response.data.errors){
          if (error.errors.is_public) {
            setIsPublicError(error.errors.is_public[0])
          }
        }
      } else {
        toast.error("Some error occurred");
      }
      console.error(err.message)
    })
  }

  const registryUrlChangeHandler = (ev) => {
    if (registryError) {
      setRegistryError("")
    }
    setRegistryUrl(ev.target.value);
  }

  const checkUrlAvailability = () => {
    if (registryUrl === props.registryInfo.urlSlug) {
      return
    }

    if (registryUrl.length <= 0) {
      toast.error("Registry URL cannot be empty!");
      return;
    }

    let data = {
      query: UPDATE_REGISTRY_URL,
      operationName: "updateRegistryUrl",
      variables: {
        "registryId": props.registryInfo.id,
        "urlSlug": registryUrl
      }
    }

    graphQL(data).then(res => {
      try {
        if (res.data.data.checkUpdateRegistryUrlSlug.message) {
          let newRegistry = props.registryInfo
          newRegistry.urlSlug = registryUrl
          setRegistryInfoUrl(registryUrl);
          setRegistryError("")
          dispatch(updateRegistryInfo(newRegistry))
        } else {
          setRegistryError(`${registryUrl} is not available. Please try something else.`)
          console.log(res.data.data.checkUpdateRegistryUrlSlug.errors);
          toast.error("Some error occurred");
        }
        getTodoList(props.registryInfo.id, dispatch)
      } catch (e) {
        toast.error("Some error occurred");
        console.error(e);
      }
    }).catch(err => {
      if (err.response.status === 400) {
        setRegistryError(`${registryUrl} is not available. Please try something else.`)
      } else {
        toast.error("Some error occurred");
      }
      console.error(err.response.data)
    })
  }

  const copyRegistryClickHandler = (ev) => {
    if (ev.target) {
      ev.target.setAttribute("disabled", "true")
    }
    setTimeout(() => {
      let el = document.querySelector(".copy-btn-registry");
      if (el) {
        el.removeAttribute("disabled")
      }
    }, TIME_TO_ENABLE_COPY_BTN)
    const el = document.createElement('textarea');
    el.value = `${siteHostName}/${registryUrl}`;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    toast.success("Copied to clipboard");
  }

  let xDown = null
  let yDown = null

  const handleTouchStart = (evt) => {
    evt.persist()
    let touches = Array.from(evt.touches, touch => touch);
    xDown = touches[0].clientX;
    yDown = touches[0].clientY;
  };

  const handleTouchMove = (evt) => {
    evt.persist()
    if (!xDown || !yDown) {
      return;
    }
    let touches = Array.from(evt.touches, touch => touch);
    let xUp = touches[0].clientX;
    let yUp = touches[0].clientY;

    let xDiff = xDown - xUp;
    let yDiff = yDown - yUp;

    if (Math.abs(xDiff) > Math.abs(yDiff)) {/*most significant*/
      if (xDiff > 0) {
        /* left swipe */
        changeRegistryVisibility(false)
      } else {
        /* right swipe */
        changeRegistryVisibility(true)
      }
    } else {
      if (yDiff > 0) {
        /* up swipe */
      } else {
        /* down swipe */
      }
    }
    /* reset values */
    xDown = null;
    yDown = null;
  };

  let canChangeRegistryVisibility = true

  if (((props.registryInfo.isCashFundItemAdded || props.registryInfo.isCashInLieuItemAdded || props.registryInfo.isGroupGiftItemAdded) && props.registryInfo.user.bankAccounts.length <= 0)) {
    canChangeRegistryVisibility = false
  }

  const modalTitle = "Share Registry";
  const modalBody = (
    <>
      <div className="row">
        <div className="col d-flex flex-column">
          <span className="section-title-share-registry text-body font-medium mb-1">Status</span>
          <div
            className={["visibility-status-btn d-flex", !registryVisibility ? "private-registry-status" : "", !canChangeRegistryVisibility ? "disabled-registry-status" : ""].join(" ")}
            onClick={canChangeRegistryVisibility ? () => changeRegistryVisibility(!registryVisibility) : () => {
            }}
            onTouchStart={(ev) => handleTouchStart(ev)}
            onTouchMove={(ev) => handleTouchMove(ev)}
          >
            <div className="registry-status-dot"/>
            <span
              className="registry-status-text font-medium text-body">{registryVisibility ? "Public" : "Private"}</span>
          </div>

          <p
            className="text-sm mt-1">{registryVisibility ? "Your registry is available to guests." : "Your registry is hidden from guests."}</p>
        </div>
      </div>
      {!canChangeRegistryVisibility || isPublicError ?
        <div className="row mt-1">
          <div className="col">
            <div className="registry-status-bank-detail-section p-3">
              <p className="text-sm">{isPublicError ? isPublicError : 'You cannot make your registry public since it contains products where we are collecting cash on your behalf. We need your bank details for that.'}</p>
              <a className="font-medium registry-status-bank-link text-sm" href={SETTINGS_LINKS.BANK_ACCOUNT}>Add bank
                details</a>
            </div>
          </div>
        </div>
        : null}

      <div className="row mt-6 align-items-end">
        <div className="col d-flex flex-column">
          <span className="section-title-share-registry text-sm font-medium">Link</span>
          <div className="mt-1 d-flex align-items-baseline">
            <div className="d-inline-block w-100">
              <div
                className={["registry-url-section d-flex text-body", registryError ? "error-input-container" : ""].join(" ")}
                ref={inputContainerRef}>
                <input
                  className="disabled-input-share-registry py-1 px-2"
                  value={siteHostName + "/"}
                  readOnly/>
                <input
                  className="registry-url-input py-1 px-2 w-100"
                  value={registryUrl}
                  onFocus={() => inputContainerRef.current.classList.add('focused-input-container')}
                  onBlur={() => inputContainerRef.current.classList.remove('focused-input-container')}
                  onChange={(ev) => registryUrlChangeHandler(ev)}
                />
              </div>
              {registryError ?
                <span className="text-sm input-error-message">{registryError}</span>
                : null}
            </div>
            {registryUrl !== registryInfoUrl ?
              <Button variant="primary" className="px-2 py-1 text-body font-medium ml-3"
                      onClick={checkUrlAvailability}>Check</Button>
              :
              <Button variant="primary" className="px-2 py-1 text-body font-medium ml-3 copy-btn-registry"
                      onClick={(ev) => copyRegistryClickHandler(ev)}>Copy</Button>
            }

          </div>
        </div>
      </div>

      <div className="row mt-10">
        <div className="col">
          <span className="text-sm font-medium section-title-share-registry">Share on Social Media</span>
          <div className="mt-2 d-flex">
            {registryVisibility ?
              <>
                {/*TODO: Add click handler*/}
                <a href={facebookShareLink} target="_blank" rel="noreferrer noopener">
                  <img className="mr-3 social-btn"
                       src={CDN_ROUTES.facebook}/>
                </a>
                <img className="mr-3 social-btn"
                     src={CDN_ROUTES.instagram}/>
                <a href={twitterShareLink} target="_blank" rel="noreferrer noopener">
                  <img className="social-btn"
                       src={CDN_ROUTES.twitter}/>
                </a>
              </>
              :
              <>
                <img className="mr-3 social-btn"
                     src={CDN_ROUTES.facebook_disabled}/>
                <img className="mr-3 social-btn"
                     src={CDN_ROUTES.instagram_disabled}/>
                <img className="social-btn"
                     src={CDN_ROUTES.twitter_disabled}/>
              </>
            }
          </div>
        </div>
      </div>
    </>
  );

  return (
    <Modal
      show={props.show}
      closeModal={closeModal}
      title={modalTitle}
      size="md"
    >
      {modalBody}
    </Modal>
  )
}

export default ShareRegistry;