import React, {useState, useEffect} from 'react';
import "./GuestSortFilter.scss";
import {useSelector, useDispatch} from 'react-redux';
import {activeGuestSortFilter} from '../../GuestViewAction';
import RadioButton from "../../../commons/components/radio-button/RadioButton";

const GuestSortFilterMobile = () => {

	const dispatch = useDispatch()
	const activeSortFilterState = useSelector(state => state.guestView.activeGuestSortFilter)
	const [currentSort, setCurrentSort] = useState(1)

	useEffect(() => {
		setCurrentSort(activeSortFilterState.sortId);
	})

	const handleSortSelection = (sortItem) => {
		setCurrentSort(sortItem.sortId)
		dispatch(activeGuestSortFilter(sortItem))
	}

	const sortByList = [
		{
			sortId: 1,
			sortTitle: 'Featured'
		},
		{
			sortId: 2,
			sortTitle: 'Price: Low to High'
		},
		{
			sortId: 3,
			sortTitle: 'Price: High to Low'
		}
	]

	return (
		<div className="sort-section-filter w-100">
			<div className="text-body filter-title pt-5 d-flex">Sort By</div>
			<div className="w-100 mt-5 text-sm mb-3">
				{sortByList.map(sortItem => {
					return (
						<div className="mt-1">
							<RadioButton
								centerText="true"
								textClass="text-body"
								name="guestViewSortFilter"
								key={sortItem.sortId}
								checked={sortItem.sortId === currentSort ? true : null}
								changeHandler={() => handleSortSelection(sortItem)} >
								{sortItem.sortTitle}
							</RadioButton>
						</div>
					);
				})}
			</div>
		</div>
	)
}

export default GuestSortFilterMobile;
