import React from "react";
import "./BookmarkletSection.scss";
import Modal from "../../modal-component/ModalComponent";
import Button from '../../button-component/ButtonComponent';
import {
  BOOKMARKLET_SCRIPT,
  BOOKMARKLET_STEP_THREE_PRODUCT_LINK,
  IMPORT_REGISTRY_EMAIL,
  M_A_BUTTON_CONTENT
} from "utils/constants";
import { connect } from "react-redux";
import { UPDATE_MA_BUTTON_CONFIGURED } from 'api/mutations';
import { updateRegistryInfo } from "modules/registry/RegistryActions";
import { graphQL } from "api/axios";

class BookmarkletSection extends React.Component {

  state = {
    show: false,
    updateModal: false,
  }

  modalTitle = ""
  modalBody = ""
  modalTitleContent = ""
  modalFooterButtons = ""

  // Event Handlers
  modalCloseHandler = () => {
    if (this.props.changeTempCloseModal){
      this.props.changeTempCloseModal(false)
    }
    this.setState({
      show: false
    })
  }

  doneBookmarkletHandler = () => {
    this.modalCloseHandler()
  }

  maButtonConfiguredhandler = (ev) => {
    ev.stopPropagation();

    let data = {
      query: UPDATE_MA_BUTTON_CONFIGURED,
      variables: {
        registryId: this.props.stateRegistryInfo.id,
        isMaButtonConfigured: true,
      }
    }
    graphQL(data).then(res => {
      if (res.status === 200) {
        console.log('Done..')
      }
    }).catch(err => {
      console.error(err.message)
    })
    let registryInfo = { ...this.props.stateRegistryInfo }
    registryInfo.isMaButtonConfigured = true
    this.props.dispatch(updateRegistryInfo(registryInfo))
  }

  openAddBookmarkletModal = () => {

    this.modalTitle = "Get M+A Button"
    this.modalTitleContent = (
      <div className="py-2 text-sm">
        It’s as easy as dragging the button to your browser’s Bookmarks bar.
      </div>
    )

    if (this.props.changeTempCloseModal){
      this.props.changeTempCloseModal(true)
    }
    this.setState((prevState, props) => ({
      show: true,
    }))

    this.modalBody = (
      <div className="row bookmarlet-model justify-content-center">
        <div className="col">
          <div className="d-flex justify-content-center mb-6">
            <div className="text-body m-a-heading mr-5">
              <div>Drag the button to your</div> <div>browser’s Bookmarks bar</div>
            </div>
            <a
              tabindex="0"
              href={BOOKMARKLET_SCRIPT}
              onClick={(ev) => {ev.preventDefault()}}
              className="m-a-btn m-a-primary-btn primary-btn px-3 py-2 font-weight-bold font-medium text-body d-flex"
            ><i className="add-bookmarklet-btn-icon mr-1"/>Add to M+A</a>
          </div>
          <div className="bookmarklet-steps">
            <ol className="my-5 order-list-bookmarklet">
              <li className="steps-title pl-4">
                <div className="text-body font-medium">Make sure your bookmarks bar is visible</div>
                <div className="mt-1 text-sm steps-details">
                  {this.getContentBasedOnBrowser()}
                </div>
              </li>
              <li className="steps-title pl-4 mt-5">
                <div className="text-body font-medium">Add to your Bookmarks bar</div>
                <div className="mt-1 text-sm steps-details">
                  Click the “Add to M+A” button and drag it to your Bookmarks bar.
                </div>
              </li>
              <li className="steps-title pl-4 mt-5">
                <div className="text-body font-medium">Give it a go!</div>
                <div className="mt-1 text-sm steps-details">
                  Go to <a className="bookmarklet-link font-medium" href={BOOKMARKLET_STEP_THREE_PRODUCT_LINK} target='_blank' tabIndex="0">Key Rattle</a> on Plan Toys and click on your NEW M+A button.
                </div>
              </li>
            </ol>
          </div>
        </div>
      </div>
    )
    this.modalFooterButtons = (
      <>
        <a tabindex="0" className="bookmarklet-link mr-auto" onClick={() => this.doneBookmarkletHandler()}>I Already Have M+A Button</a>
        <a
          className="px-3 py-1 bookmarklet-disable-btn text-body font-medium"
          href={"mailto:" + IMPORT_REGISTRY_EMAIL}
          target='_blank'
          tabindex="0"
        >Email Us For Help</a>
        <Button
          variant="primary"
          className="px-4 py-1 font-weight-bold font-medium text-body"
          onClick={() => this.doneBookmarkletHandler()}
        >Done</Button>
      </>
    )
  }

  getContentBasedOnBrowser = () => {
    // Get the user-agent string
            let userAgentString =
                navigator.userAgent;

            // Detect Chrome
            let chromeAgent =
                userAgentString.indexOf("Chrome") > -1;

            // Detect Internet Explorer
            let edgeAgent =
                userAgentString.indexOf("Edg") > -1 || userAgentString.indexOf("Edge") > -1

            // Detect Firefox
            let firefoxAgent =
                userAgentString.indexOf("Firefox") > -1;

            // Detect Safari
            let safariAgent =
                userAgentString.indexOf("Safari") > -1;

            // Discard Safari since it also matches Chrome
            if ((chromeAgent) && (safariAgent))
                safariAgent = false;

            if (edgeAgent){
              safariAgent = false;
              chromeAgent = false;
            }
            console.log(edgeAgent)
            if (chromeAgent){
              return M_A_BUTTON_CONTENT["Chrome"];
            } else if (safariAgent){
              return M_A_BUTTON_CONTENT["Safari"];
            } else if (edgeAgent) {
              return M_A_BUTTON_CONTENT["Edge"];
            } else if (firefoxAgent) {
              return M_A_BUTTON_CONTENT["Firefox"];
            } else {
              return M_A_BUTTON_CONTENT["Chrome"]
            }
  }



  render() {
    return (
      !this.props.stateRegistryInfo.isMaButtonConfigured ?
        <div className="d-none d-xl-block">
          <div className="py-2 bookmarklet-section" onClick={this.openAddBookmarkletModal}>
            <div className="row">
              <div className="col flex-grow-0">
                {/* TODO: Add M$A Icon once added */}
              </div>
              <div className="col">
                <h4>Get M+A Button</h4>
                <p className="text-sm mt-1">M+A Button lets you add product from any store directly to Monica+Andy Baby
            Registry.</p>
                <a tabindex="0" className="bookmarklet-link mt-1 text-sm" onClick={(ev) => this.maButtonConfiguredhandler(ev)}>I already have it</a>
              </div>
            </div>
          </div>
          <Modal
            show={this.state.show}
            updateModal={this.state.updateModal}
            title={this.modalTitle}
            titleContent={this.modalTitleContent}
            footer={this.modalFooterButtons}
            closeModal={this.modalCloseHandler}>
            {this.modalBody}
          </Modal>
        </div>
        : null
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
  }
}


export default connect(
  mapStateToProps
)(BookmarkletSection);