import React, { useState, useEffect } from 'react'
import "./ViewFilter.scss";
import { useSelector, useDispatch } from 'react-redux';
import { setChildViewFilter, clearViewFilter, setViewFilterCategory } from '../../ViewRegistryActions';
import { FILTERS } from "utils/constants"

const ViewFilter = (props) => {

  const dispatch = useDispatch()
  let tempParentFilterId = useSelector(state => state.viewRegistry.viewFilterApplied.parentFilter)
  let tempChildFilterIds = useSelector(state => state.viewRegistry.viewFilterApplied.childFilter)
  let stateAddedProducts = useSelector(state => state.myRegistry.addedProducts)
  const stateCategoriesInfo = useSelector(state => state.myRegistry.checkList)
  const stateGuestViewRegistry = useSelector(state => state.viewRegistry.guestViewRegistry)
  let stateFilterCategories = useSelector(state => state.viewRegistry.filterCategory)
  let filterCountState = useSelector(state => state.myRegistry.filterCount)
  let [filterCategoryLimit, setFilterCategoryLimit] = useState([])

  // Constant variables for filters
  const AVAILABLE = "available"
  const RESERVED = "reserved"
  const PURCHASED = "purchased"
  const PRICE_1 = "price-1"
  const PRICE_2 = "price-2"
  const PRICE_3 = "price-3"
  const MONICA_AND_ANDY = "monicaandandy"
  const ELSEWHERE = "elsewhere"

  const getLimitOfFliter = (parentId) => {
    let limit = null
    const filterCategory = [...filterCategoryLimit]
    for (let category of filterCategory) {
      if (parentId === category.id && category.limit) {
        limit = category.limit
      }
    }
    return limit
  }

  // Updating product count in filter categories
  const updateFilterList = (addedProducts) => {

    let newFilterList = [
      {
        id: "categories",
        display: FILTERS.CATEGORIES_DISPLAY,
        limit: getLimitOfFliter("categories") ? getLimitOfFliter("categories") : 6,
        child: [...stateCategoriesInfo].filter(category => {
          let id = category.category.id
          let count = filterCountState.categoryData[id]
          if (count > 0) {
            return true
          }
        }).map(category => {
          let newCategory = { ...category }
          let id = category.category.id
          newCategory.id = category.category.id
          newCategory['display'] = newCategory.categoryName
          newCategory['count'] = filterCountState.categoryData[id]
          return newCategory
        })
      },
      {
        id: "price",
        display: FILTERS.PRICE_DISPLAY,
        child: [
          {
            id: PRICE_1,
            display: FILTERS.PRICE_DISPLAY_1,
            count: filterCountState.registryItemsAnalysisCount.under50
          },
          {
            id: PRICE_2,
            display: FILTERS.PRICE_DISPLAY_2,
            count: filterCountState.registryItemsAnalysisCount.between50100
          },
          {
            id: PRICE_3,
            display: FILTERS.PRICE_DISPLAY_3,
            count: filterCountState.registryItemsAnalysisCount.moreThan100
          }
        ]
      },
      {
        id: "store",
        display: FILTERS.STORE_DISPLAY,
        child: [
          {
            id: MONICA_AND_ANDY,
            display: FILTERS.MONICA_AND_ANDY_DISPLAY,
            count: filterCountState.registryItemsAnalysisCount.maStoreItems
          },
          {
            id: ELSEWHERE,
            display: FILTERS.ELSEWHERE_DISPLAY,
            count: filterCountState.registryItemsAnalysisCount.otherStoreItems
          },
        ]
      },

    ]
    if (!stateGuestViewRegistry) {
      const giftStatus = {
        id: "giftStatus",
        display: "Gift Status",
        child: [
          {
            id: AVAILABLE,
            display: FILTERS.AVAILABLE_DISPLAY,
            count: filterCountState.registryItemsAnalysisCount.totalAvailable
          },
          {
            id: RESERVED,
            display: FILTERS.RESERVED_DISPLAY,
            count: filterCountState.registryItemsAnalysisCount.totalReserved
          },
          {
            id: PURCHASED,
            display: FILTERS.PURCHASED_DISPLAY,
            count: filterCountState.registryItemsAnalysisCount.totalPurchased
          }
        ]
      }
      newFilterList.splice(0, 0, giftStatus)
    }
    dispatch(setViewFilterCategory(newFilterList))
    return newFilterList
  }

  useEffect(() => {
    if (tempChildFilterIds.length === 0) {
      updateFilterList(stateAddedProducts)
    }
  }, [stateAddedProducts, stateCategoriesInfo])


  // Change event handlers

  // TDOD: Add this if needed
  // const parentChangeHandler = (parent) => {
  //   dispatch(setParentViewFilter(parent.id))
  //   if (parent.child) {
  //     dispatch(setChildViewFilter(parent.child.map(child => child.id)))
  //   } else {
  //     dispatch(setChildViewFilter([]))
  //   }
  // }

  const childChangeHandler = async (childId, parentId) => {

    if (parentId !== tempParentFilterId) {
      if (tempChildFilterIds.length > 0) {
        await dispatch(setChildViewFilter([]))
      }
    }

    const childIndex = tempChildFilterIds.indexOf(childId)
    let childFilter = []
    if (childIndex === -1) {
      childFilter = [...tempChildFilterIds, childId]
      await dispatch(setChildViewFilter([...tempChildFilterIds, childId]))
    } else {
      let newChildArray = [...tempChildFilterIds]
      newChildArray.splice(childIndex, 1)
      childFilter = [...newChildArray]
      await dispatch(setChildViewFilter(newChildArray))
    }
		await props.setFilterhandler(childFilter)
  }

  const clearClickHandler = async () => {
    await dispatch(clearViewFilter())
		await props.setFilterhandler([])
  }

  const changeParentLimitHandler = (parentFilterID) => {
    let limitLength = null
    let filterCategory = [...stateFilterCategories].map(parentFilter => {
      if (parentFilter.id === parentFilterID) {
        parentFilter.limit = parentFilter.child.length
        limitLength = parentFilter.child.length
      }
      return parentFilter
    })
    dispatch(setViewFilterCategory(filterCategory))

    let isFound = false
    let newFilterCateoryLimit = [...filterCategoryLimit].map(categorylimit => {
      if (categorylimit.id === parentFilterID) {
        categorylimit.limit = limitLength
        isFound = true
        return categorylimit
      }
      return categorylimit
    })

    if (!isFound) {
      newFilterCateoryLimit.push({ 'id': parentFilterID, 'limit': limitLength })
    }
    setFilterCategoryLimit(newFilterCateoryLimit)
  }

  return (
    <div className="view-filter">
      <div className="view-filter-heading pb-1 d-flex align-items-baseline">
        <h4 className="font-weight-bold">Filters</h4>
        {tempChildFilterIds.length !== 0 ?
          <a tabindex='0' className="filter-clear-all text-sm font-medium ml-auto" onClick={() => clearClickHandler()}>Clear all</a> : null
        }
      </div>
      <div className="d-flex flex-column">
        {[...stateFilterCategories].map(parentFilter => (
          <div className="filter-item" key={"filter-" + parentFilter.id}>
            {/* Parent filter */}
            <div className="text-body filter-title pt-5 d-flex">{parentFilter.display}</div>

            {/* Child filter */}
            {parentFilter.child ? (
              <div className="d-flex flex-column mt-2" key={"child-filter-" + parentFilter.id}>
                <div className="mt-3 fliter-checkbox-section">
                  {parentFilter.child.map((childFilter, index) => (
                    childFilter.count > 0 ?
                      index < (parentFilter.limit ? parentFilter.limit : parentFilter.child.length) ?
                        <label className="view-fliter-checkbox d-flex align-items-center mb-4" key={childFilter.id}>
                          <span className="checkbox__input mr-2">
                            <input type="checkbox"
                              name={childFilter.display}
                              value={childFilter.display}
                              checked={tempChildFilterIds.includes(childFilter.id) ? "checked" : ""}
                              onChange={() => childChangeHandler(childFilter.id, parentFilter.id)} />
                            <span className="checkbox__control d-flex justify-content-center align-items-center">
                              <i className="icon-check"/>
                            </span>
                          </span>
                          <p className="radio__label text-sm">{childFilter.display} {childFilter.count > 0 ? <>({childFilter.count})</> : null}</p>
                        </label>
                        : null
                      : null

                  ))}
                  {(parentFilter.limit && (parentFilter.limit !== parentFilter.child.length) && (parentFilter.child.length - parentFilter.limit) > 0) ?
                    <div className="filter-clear-all text-sm font-medium ml-4 pb-3" onClick={() => changeParentLimitHandler(parentFilter.id)}>Show {parentFilter.child.length - parentFilter.limit} more</div>
                    : null
                  }
                </div>
              </div>
            )
              : null}
          </div>
        ))}
      </div>
    </div>
  )
}

export default ViewFilter;
