import React, {createRef} from 'react';
import {NavLink, Switch, Route, Redirect, withRouter} from 'react-router-dom';
import MyRegistry from '../my-registry/MyRegistry';
import ViewRegistry from "../view-registry/ViewRegistry";
import {useTranslation} from "react-i18next";
import route from "utils/routes";
import RegistryHeroData from "data/RegistryHero.json";
import './RegistryHero.scss';
import {useSelector, useDispatch} from 'react-redux';
import Toast from "modules/commons/components/toast/Toast";
import TodoWidget from "./component/todo-widget/TodoWidget";
import TrackGifts from "../track-gifts/TrackGifts";
import RedirectionScreen from "../track-gifts/components/redirection-screen/RedirectionScreen";
import moment from 'moment';
import {setguestViewRegistry} from "modules/view-registry/ViewRegistryActions";
import {InView} from "react-intersection-observer";
import Filter from "../my-registry/components/filter/Filter";


const RegistryHero = (props) => {

  const stateGuestViewRegistry = useSelector(state => state.viewRegistry.guestViewRegistry)
  const stateRegistryInfo = useSelector(state => state.registry.info)
  const stateTrackGiftsRedirectionInfo = useSelector(state => state.trackGifts.redirectionInfo)
  let stateBuildRegistryFilterApplies = useSelector(state => state.myRegistry.filterApplied)
  const stateRouter = useSelector(state => state.router)
  const {t} = useTranslation();
  const registryData = RegistryHeroData
  const dispatch = useDispatch()
  const stickyNav = createRef()

  if (!stateRegistryInfo.id && stateRegistryInfo.notFound) {
    return <Redirect to={route.ONBOARDING.HOME}/>
  }

  window.onpopstate = (ev) => {
    if (stateGuestViewRegistry) {
      dispatch(setguestViewRegistry(false))
    }
  }

  const registryHeroObserver = (inView, entry) => {
    // This is for sticky nav
    if (stateRouter.location && stateRouter.location.pathname !== route.VIEW_REGISTRY.HOME) {
      if (!inView && entry.boundingClientRect.top <= 0) {
        if (stickyNav.current) {
          stickyNav.current.classList.remove("d-none")
        }
      } else {
        if (stickyNav.current) {
          stickyNav.current.classList.add("d-none")
        }
      }
    }

    // This is for sticky checklist
    let el = document.getElementById('checklist-scrollarea');
    let registryContainer = document.getElementById('m-a-registry-container');
    if(document.body.offsetWidth > 768 && registryContainer?.offsetHeight > el?.offsetHeight) {
      if (inView) {
        if (el) {
          el.style.position = 'static';
          el.style.top = 'auto';
          el.classList.add = 'w-100';
          el.style.paddingTop = '0px';

        }
      } else {
        if (el) {
          // stickyNav.current.classList.add("d-none")
          let parentWidth = document.getElementById('checklist-container').offsetWidth;
          el.style.position = 'fixed';
          el.style.top = 0;
          el.style.paddingTop = '70px';
          el.style.width = `${parentWidth}px`;
        }
      }
    }
  }

  document.body.style.overflow = "hidden";

  return (
    <>{stateTrackGiftsRedirectionInfo.store ?
      <RedirectionScreen store={stateTrackGiftsRedirectionInfo.store}/>
      :
      <>
        {!stateGuestViewRegistry ?
          <>
            <InView as="div" className="registry-hero" threshold={0.2} onChange={registryHeroObserver}>
              {/*<div className="registry-hero">*/}
              <div className="container pt-3 pt-md-6">
                <div className="row mb-5 mb-md-0">
                  <div className="col d-flex justify-content-between align-items-center align-items-md-start">
                    <div className="d-flex align-items-end">
                      <h1 className="registry-name-header">{stateRegistryInfo.name}</h1>
                      <span
                        className="text-sm ml-1 registry-due-date-text">{stateRegistryInfo.dueDate !== null ? t("registryHeader.subTitle") + " " + moment(stateRegistryInfo.dueDate).format("MMM Do YYYY") : ""}</span>
                    </div>
                    <div>
                      <TodoWidget/>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col">
                    <div className="d-flex registry-hero-navigation-links pt-2">
                      <NavLink to={route.MY_REGISTRY.HOME} className="registry-header-link">
                        <h4>{t("registryHeader.link.myRegistry")}</h4></NavLink>

                      <NavLink to={route.VIEW_REGISTRY.HOME} className="registry-header-link">
                        <h4>{t("registryHeader.link.viewRegistry")}</h4></NavLink>

                      <NavLink to={route.TRACK_GIFTS.HOME} className="registry-header-link">
                        <h4>{t("registryHeader.link.trackGifts")}</h4></NavLink>
                    </div>
                  </div>
                </div>
              </div>
              {/*</div>*/}
            </InView>
            {stateRouter.location && stateRouter.location.pathname !== route.VIEW_REGISTRY.HOME ?
              <div className="registry-hero fixed-registry-hero w-100 d-none" ref={stickyNav}>
                <div className="container">
                  <div className="row">
                    <div className="col">
                      <div className="d-flex registry-hero-navigation-links pt-2">
                        <NavLink to={route.MY_REGISTRY.HOME} className="registry-header-link">
                          <h4>{t("registryHeader.link.myRegistry")}</h4></NavLink>

                        <NavLink to={route.VIEW_REGISTRY.HOME} className="registry-header-link">
                          <h4>{t("registryHeader.link.viewRegistry")}</h4></NavLink>

                        <NavLink to={route.TRACK_GIFTS.HOME} className="registry-header-link">
                          <h4>{t("registryHeader.link.trackGifts")}</h4></NavLink>
                      </div>
                    </div>
                    {stateRouter.location && stateRouter.location.pathname === route.MY_REGISTRY.HOME && stateBuildRegistryFilterApplies ?
                      <div className="col-2 text-right pt-1">
                        <Filter radioName={true}/>
                      </div>
                      : null}
                  </div>
                </div>
              </div>
              : null}
          </> : null}

        {/* Registry Nav Routes */}
        {stateRegistryInfo.id ?
          <Switch>
            <Route exact path={route.REGISTRY.HOME}><Redirect to={route.MY_REGISTRY.HOME}/></Route>
            <Route exact path={route.MY_REGISTRY.HOME} component={MyRegistry}/>
            <Route exact path={route.VIEW_REGISTRY.HOME} component={ViewRegistry}/>
            <Route exact path={route.TRACK_GIFTS.HOME} component={TrackGifts}/>
          </Switch>

          : null}
      </>
    }


      <Toast/>
    </>
  )
}

export default withRouter(RegistryHero);