export const TRACK_GIFTS_ACTION_TYPES = {
  TRACK_GIFTS_ACTIVE_VIEW: "TRACK_GIFTS_ACTIVE_VIEW",

  TRACK_GIFTS_SET_PRODUCT: "TRACK_GIFTS_SET_PRODUCT",

  TRACK_GIFTS_SET_PAGINATION_INFO: "TRACK_GIFTS_SET_PAGINATION_INFO",

  TRACK_GIFTS_SET_FILTERS: "TRACK_GIFTS_SET_FILTERS",

  TRACK_GIFTS_SET_SEARCH: "TRACK_GIFTS_SET_SEARCH",

  TRACK_GIFTS_SET_REDIRECTING_INFO: "TRACK_GIFTS_SET_REDIRECTING_INFO"
}

export const setActiveView = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_ACTIVE_VIEW,
    payload: params
  }
}

export const setTrackGiftsProduct = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_PRODUCT,
    payload: params
  }
}

export const setTrackGiftsPaginationInfo = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_PAGINATION_INFO,
    payload: params
  }
}

export const setTrackGiftsFilters = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_FILTERS,
    payload: params
  }
}

export const setTrackGiftsSearch = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_SEARCH,
    payload: params
  }
}

export const setTrackGiftsRedirectionInfo = (params) => {
  return {
    type: TRACK_GIFTS_ACTION_TYPES.TRACK_GIFTS_SET_REDIRECTING_INFO,
    payload: params
  }
}
