import React from "react";
import "./TogglePills.scss";

const TogglePills = (props) => {
  return (
    <div
      className={["toggle-pills px-2 py-1 d-flex align-items-end", props.activeClass, props.className ? props.className : "", props.iconClass].join(" ")}
      onClick={props.onClick}
      data-tut={props.dataTut ? props.dataTut : ''}
    >
      <i className={["mr-2"].join(" ")}/>
      <span className="text-sm">{props.text}</span>
    </div>
  )
}

export default TogglePills;