import React, {createRef} from "react";
// import "./CheckListItems.scss";
import {withTranslation} from "react-i18next";
import PersonaliseHeader from "./components/personalise-header/PersonaliseHeader";
import {getPersonalizeQuestions, setMetaInfoToDom, updateRegistryDetail} from "utils/commonFunction";
import {connect} from "react-redux";
import PersonaliseFooter from "./components/personalise-footer/PersonaliseFooter";
import {toast, ToastContainer} from "react-toastify";
import Option from "../commons/components/questions/option/Option";
import {
  updatePersonalizeQuestionAnswer,
  addPersonalizeQuestionAnswer,
  addPersonalizeQuestion
} from "./PersonaliseCheckListActions";
import "./PersonaliseChecklist.scss";
import "../onboarding/Onboarding.scss";
import Button from "modules/commons/components/button-component/ButtonComponent";
import routes from "utils/routes";
import {SAVE_SINGLE_QUESTION_RESPONSE} from 'api/mutations';
import {graphQL} from 'api/axios';
import {ONBOARDING_ANIMATION_DELAY} from "../../utils/constants";

const SkipQuestionLink = props => {
  const linkClickHandler = ev => {
    ev.preventDefault()
    if (props.onClick) {
      props.onClick();
    }
  }
  return (
    <a
      tabIndex={1}
      href="/"
      onClick={(ev) => linkClickHandler(ev)}
      className={["personalize-quiz-skip-question-link onboarding-font-2 onboading-text-color", props.className ? props.className : ""].join(" ")}
    >Skip this question</a>
  )
}

class PersonaliseChecklist extends React.Component {

  state = {
    parentId: null,
    prevQuestionId: [],
    question: {},
    activeQuestionId: null,
    toastMessage: "",
    percentage: 0,
    checkBoxSelected: [],
  }

  isActiveToast = false
  headerHeightFromTop = null
  isAnsweredChanged = false

  // Answer type mapping
  RADIO = "Radio"
  CHECKBOX = "Checkbox"

  ANIMATED = false

  componentDidMount() {
    // getPersonalizeQuestions(this.props.stateRegistryInfo.id, this.props.dispatch)
    this.headerHeightFromTop = null
    this.isAnsweredChanged = false
    window.addEventListener('scroll', this.handleScroll);
    window.addEventListener("keyup", (ev) => this.handleKeyboardNavigation(ev));
    this.setQuestionBasedOnId(this.props.match.params['questionId'])
    this.clearToastMessage()
  }

  calulatePercentage = (answerQuestionCount, totalQuestionCount) => {
    let totalAnwer = 0;

    const questions = [...this.props.statePersonaliseQuestions]
    for (let question of questions) {
      if (question.answers.length > 0) {
        totalAnwer++;
      }
      if (questions.followUpQuestion) {
        for (let subQuestion of question.followUpQuestions) {
          if (subQuestion.answers.length > 0) {
            totalAnwer++;
          }
        }
      }
    }
    return this.props.statePersonaliseQuestions.length ? Math.ceil(totalAnwer * 100 / totalQuestionCount) : 0;
  }

  gotoNextQuestion = async (question, parentId, option = {}, doNotRedirect = false) => {
    this.ANIMATED = false;
    await this.clearToastMessage();
    if (this.state.question.answerInputType === this.CHECKBOX) {
      this.answerCheckBoxQuestion(this.state.question.id, this.state.checkBoxSelected)
    }
    let nextQuestionId = null;
    let returnNext = false;
    const idToMatch = parentId ? parentId : question.id;

    // Finding if given anwer has followup or not & Only works for 1 follow up question
    if (this.state.question.answers.length) {
      let answerId = this.state.question.answers[0].questionOption.id
      let tempOption = null
      for (let ans of [...this.state.question.options]) {
        if (ans.id === answerId && ans.followUpQuestions?.length) {
          tempOption = {...ans};
          break;
        }
      }
      if (tempOption) {
        await this.setState({
          question: tempOption.followUpQuestions[0].question,
          parentId: tempOption.followUpQuestions[0].parentQuestion
        })
        await this.gotoNextQuestion(this.state.question, this.state.parentId, tempOption)
        return;
      }
    }

    // Only works for 1 follow up question
    if (option.followUpQuestions && option.followUpQuestions.length > 0) {
      nextQuestionId = option.followUpQuestions[0].question.id;
    } else {
      for (let ques of this.props.statePersonaliseQuestions) {
        if (returnNext) {
          nextQuestionId = ques.id
          break
        }
        if (ques.id === idToMatch) {
          returnNext = true;
        }
      }
    }

    if (nextQuestionId === null && returnNext) {
      // This is last question
      // And Forwarding him to edit checklist
      // TODO: change this if needed
      // if(this.props.location.state?.openEditChecklist) {
      //   this.props.history.push(routes.PERSONALIZE.CHECKLIST)
      // }
      // // And Forwarding to build registry
      // else {
      //   this.props.history.push(routes.MY_REGISTRY.HOME)
      // }
      if (!doNotRedirect) {
        this.props.history.push({
          pathname: routes.MY_REGISTRY.HOME,
          state: {
            toastMessage: "Your checklist and recommendations have been updated",
            getRecommendation: true,
          }
        })
      }
    } else if (nextQuestionId) {
      await this.setState({
        parentId: null,
        prevQuestionId: this.isActiveIdPresent() ? [...this.state.prevQuestionId, this.state.question.id] : this.state.prevQuestionId,
        activeQuestionId: nextQuestionId
      })
      await this.setQuestionBasedOnId(nextQuestionId)
    }
  }

  gotoPrevQuestion = (prevQuestionId) => {
    this.setQuestionBasedOnId(prevQuestionId);
    if (this.state.prevQuestionId.length > 1) {
      let popPrevQuestionId = [...this.state.prevQuestionId];
      popPrevQuestionId.pop();
      this.clearToastMessage();
      this.setState({
        prevQuestionId: popPrevQuestionId,
        activeQuestionId: popPrevQuestionId.length > 0 ? popPrevQuestionId[popPrevQuestionId.length - 1] : this.state.activeQuestionId
      })
      this.ANIMATED = false
    }
  }

  setQuestionBasedOnId = (questionId) => {
    if (questionId) {
      let finalQuestion = {};
      let foundQuestion = false;
      let parentId = null;
      for (const question of this.props.statePersonaliseQuestions) {
        if (foundQuestion) {
          break;
        }
        if (question.id === questionId) {
          finalQuestion = question;
          foundQuestion = true;
          break;
        }
        for (const option of question.options) {
          if (option.followUpQuestions.length > 0) {
            for (const followUpQuestion of option.followUpQuestions) {
              if (followUpQuestion.question.id === questionId) {
                finalQuestion = followUpQuestion.question;
                parentId = followUpQuestion.parentQuestion;
                foundQuestion = true
                break;
              }
            }
          }
        }
      }

      if (finalQuestion.id) {
        this.setState({
          question: finalQuestion,
          parentId: parentId,
        })
      } else {
        console.log("No Question Found");
      }
    }

  }

  addAnswerToState = (response) => {
    const alreadyAnswered = this.props.statePersonaliseAnswers.find(el => el.question === this.state.question.id)
    if (alreadyAnswered) {
      // This will delete the answers that were given after the matched response
      let newState = []
      for (let answer of this.props.statePersonaliseAnswers) {
        if (answer.question === this.state.question.id) {
          answer.response = response;
          newState.push(answer)
        } else {
          newState.push(answer)
        }
      }
      this.props.dispatch(updatePersonalizeQuestionAnswer(newState))
    } else {
      this.props.dispatch(addPersonalizeQuestionAnswer({question: this.state.question.id, response: response}))
    }
  }

  isActiveIdPresent = () => {
    if (this.state.prevQuestionId.length) {
      if (this.state.prevQuestionId[this.state.prevQuestionId.length - 1] === this.state.question.id) {
        return false;
      }
    }
    return true;
  }

  answerRadioQuestion = (questionId, option) => {
    this.addAnswerToState(option);
    this.gotoNextQuestion(this.state.question, this.state.parentId, option);
    // TODO: Remove comment this if needed
    // if (this.isActiveIdPresent) {
    //   this.setState({
    //     prevQuestionId: this.isActiveIdPresent() ? [...this.state.prevQuestionId, this.state.activeQuestionId] : this.state.prevQuestionId,
    //   })
    // }

    let finalAnswer = [{
      option: option.id,
      optionText: option.value
    }]
    this.submitQuestionAnswer(questionId, finalAnswer, option);
  }

  answerCheckBoxQuestion = (questionId, option) => {
    this.addAnswerToState(option[0]);
    // this.calulatePercentage();
    if (this.isActiveIdPresent) {
      this.setState({
        prevQuestionId: this.isActiveIdPresent() ? [...this.state.prevQuestionId, this.state.activeQuestionId] : this.state.prevQuestionId,
      })
    }

    // let finalAnswer = [{
    //   option: option.id,
    //   optionText: option.value
    // }]

    let finalAnswer = []

    for (let op of option) {
      finalAnswer.push({option: op.id, optionText: op.value})
    }

    // Checking if answer has been changed
    let hasAnswerChanged = false;
    if (this.state.question.answers.length !== finalAnswer.length) {
      hasAnswerChanged = true;
    } else {
      for (let prevAnswer of [...this.state.question.answers]) {
        let foundAnswer = false;
        for (let finAnswer of [...finalAnswer]) {
          if (finAnswer.option === prevAnswer.questionOption.id) {
            foundAnswer = true
            break;
          }
        }
        if (!foundAnswer) {
          hasAnswerChanged = true;
          break;
        }
      }
    }
    // If answer are changed then api call
    if (hasAnswerChanged) {
      this.submitQuestionAnswer(questionId, finalAnswer, option);
    }
  }

  selectCheckboxAnswer = (event, checkBoxRef, option) => {
    this.ANIMATED = true;
    checkBoxRef.current.classList.toggle("personalise-checkbox-selected");
    if (event.target.checked) {
      this.setState({
        // checkBoxSelected: selectedOption.push(option)
        checkBoxSelected: [...this.state.checkBoxSelected, option]
      })
    } else {
      var removeIndex = this.state.checkBoxSelected.map(function (item) {
        return item.id;
      }).indexOf(option.id);
      this.state.checkBoxSelected.splice(removeIndex, 1);
    }
  }

  submitQuestionAnswer = (questionId, answer, option = null) => {

    if (option?.toastMessage) {
      this.setState({
        toastMessage: option.toastMessage
      })
    } else {
      this.setToastMessage()
    }
    let questionOptionInput = {
      question: questionId,
      questionOptionInput: answer
    }
    let reqData = {
      query: SAVE_SINGLE_QUESTION_RESPONSE,
      variables: {
        registryId: this.props.stateRegistryInfo.id,
        registryQuestionOptionInput: questionOptionInput
      }
    }

    graphQL(reqData).then(res => {
      if (res.data.data) {
        console.log('Answer Done')
        // For updaing the Quiz Mode
        if (!this.isAnsweredChanged && !this.props.stateRegistryInfo.quizMode) {
          this.isAnsweredChanged = true
          updateRegistryDetail(this.props.dispatch)
        }
        this.setStateQuestionWithSelectedAnswer(questionId, answer)
      }
    }).catch(err => {
      toast.error("Error occurred while fetching questions");
      console.error(err);
    })
  }

  setStateQuestionWithSelectedAnswer = (questionId, answer) => {
    let answerOpt = []
    for (let ans of answer) {
      answerOpt.push({questionOption: {id: ans.option}, optionText: ans.optionText})
    }
    // Setting question state with selected answer
    const questions = [...this.props.statePersonaliseQuestions].map(question => {

      if (question.id === questionId) {
        question.answers = answerOpt
      }
      question.options = question.options.map(opt => {
        if (opt.followUpQuestions.length > 0) {
          opt.followUpQuestions.map(followUpQuestion => {
            if (followUpQuestion.question.id === questionId) {
              followUpQuestion.question.answers = answerOpt
            }
          })
        }
        return opt;
      })
      question.isAnswered = false;
      return question;
    })
    this.props.dispatch(addPersonalizeQuestion(questions))
  }

  clearToastMessage = () => {
    this.setState({
      toastMessage: ""
    })
  }

  setToastMessage = () => {
    let question = {...this.state.question}
    if (question.toastMessage) {
      this.setState({
        toastMessage: question.toastMessage
      })
      this.isActiveToast = false
    } else {
      this.clearToastMessage()
    }
  }

  selectedColorSchemes = (data) => {
    if (this.state.checkBoxSelected.length <= 0) {
      this.setState({
        checkBoxSelected: [...data]
      })
    }
  }

  handleScroll = (event) => {
    let el = document.getElementById('personalise-header');
    if (el) {
      // Setting height from top to checklist
      if (el.getBoundingClientRect().top === 0 && !this.headerHeightFromTop) {
        this.headerHeightFromTop = window.scrollY
      }
      // Setting position of checklist to fixed
      if (el.getBoundingClientRect().top <= 0) {
        el.style.position = 'fixed';
        el.style.top = 0;
        el.style.width = '100%';
      }
      // Setting position of checklist to static
      if (this.headerHeightFromTop >= window.scrollY) {
        el.style.position = 'static';
        el.style.top = 'auto';
      }
    }
  }

  handleKeyboardNavigation = (ev) => {
    switch (ev.keyCode) {
      // Left arrow key
      case 37:
        this.gotoPrevQuestion(this.state.prevQuestionId[this.state.prevQuestionId.length - 1])
        break;
      //  Right arrow key
      case 39:
        this.gotoNextQuestion(this.state.question, this.state.parentId, {}, true)
        break;
      default:
        break;
    }
  }

  render() {
    let toastMessageToDisplay = ""

    // Setting Meta info to DOM
    if (this.props.stateSeo) {
      setMetaInfoToDom(this.props.stateSeo['registry-quiz-ma'], "registry-quiz")
    }

    if (!this.state.question.id && this.props.statePersonaliseQuestions.length > 0) {
      let prevQuestionID = []
      let currentQuestion = null
      for (let question of this.props.statePersonaliseQuestions) {
        if (!question.isPersonalize || question.answers.length > 0) {
          prevQuestionID.push(question.id)
        } else {
          currentQuestion = question
          break
        }
      }
      if (!currentQuestion) {
        prevQuestionID = []
        currentQuestion = null
        for (let question of this.props.statePersonaliseQuestions) {
          if (!question.isPersonalize) {
            prevQuestionID.push(question.id)
          } else {
            currentQuestion = question
            break
          }
        }
      }
      this.setState({
        prevQuestionId: prevQuestionID,
        activeQuestionId: currentQuestion.id,
        question: currentQuestion
      })

    }

    if (this.state.toastMessage && !this.isActiveToast) {
      toastMessageToDisplay = this.state.toastMessage
      // toast.dismiss()
      // toast.info(<h4 className="font-medium text-center" dangerouslySetInnerHTML={{__html: this.state.toastMessage}}/>)
      this.isActiveToast = true
    } else {
      // toast.dismiss()
    }

    // Setting all the options for the questions
    let options = []
    let staticQuestionTitle = "";

    if (this.state.question.answerInputType === this.RADIO) {
      const selectedOptionID = this.state.question.answers?.length > 0 ? this.state.question.answers[0].questionOption.id : null
      if (this.state.question.options.length === 2 && this.state.question.options[0].helpText) {
        let questionOptions = this.state.question.options;
        options.push(
          <div className="row">
            <div className="col-12 col-md-6 pb-2">
              <div className="d-flex">
                <div className="text-center ml-auto mr-auto mr-md-0 position-relative">
                  <Option
                    className={["mb-2", selectedOptionID === questionOptions[0].id ? "onboarding-selected" : ""].join(" ")}
                    onClick={() => this.answerRadioQuestion(this.state.question.id, questionOptions[0])}>
                    <h4 className="d-inline">{questionOptions[0].value}</h4></Option>
                  {questionOptions[0].helpText ?
                    <span
                      className="text-sm ml-1 onboading-text-color onboarding-font-3 d-block w-75 mx-auto">{questionOptions[0].helpText}</span> : null}
                </div>
              </div>
            </div>
            <div
              className="col-12 col-md-6 position-relative pt-md-0 pt-2">
              <div className="onboarding-option-half-border"/>
              <div className="d-flex">
                <div className="text-center mr-auto ml-auto ml-md-0 position-relative">
                  <Option
                    className={["mb-2", selectedOptionID === questionOptions[1].id ? "onboarding-selected" : ""].join(" ")}
                    onClick={() => this.answerRadioQuestion(this.state.question.id, questionOptions[1])}>
                    <h4 className="d-inline">{questionOptions[1].value}</h4></Option>
                  {questionOptions[1].helpText ?
                    <span
                      className="text-sm ml-1 onboading-text-color onboarding-font-3 d-block w-75 mx-auto">{questionOptions[1].helpText}</span> : null}
                </div>
              </div>
            </div>
          </div>
        )
      } else {
        options = this.state.question.options.map(option => (
          <Option className={["mb-2 mx-2", selectedOptionID === option.id ? "onboarding-selected" : ""].join(" ")}
                  onClick={() => this.answerRadioQuestion(this.state.question.id, option)}>
            <h4 className="d-inline">{option.value}</h4>{option.helpText ?
            <div className="text-sm onboarding-option-help-text">{option.helpText}</div> : null}</Option>
        ))
      }
    } else if (this.state.question.answerInputType === this.CHECKBOX) {
      // TODO: If it is selected
      const selectedOptionID = []
      if (this.state.question.answers?.length > 0) {
        for (let ans of this.state.question.answers) {
          if (!selectedOptionID.includes(ans.questionOption.id)) {
            selectedOptionID.push(ans.questionOption.id)
          }
        }
        const selectOptions = []
        for (let selectedOption of this.state.question.options) {
          if (selectedOptionID.includes(selectedOption.id)) {
            selectOptions.push(selectedOption)
          }
        }
        this.selectedColorSchemes(selectOptions);
      }
      options = this.state.question.options.map(option => {
        let checkBoxRef = createRef();
        return (
          <div
            className={["personalise-checkbox  mx-2 mb-3 py-3 px-3", selectedOptionID.includes(option.id) ? "personalise-checkbox-selected" : ""].join(" ")}
            ref={checkBoxRef}>
            <div className="d-flex">
              <label className="checkbox d-flex align-items-center pb-0 mb-0">
              <span className="checkbox__input mr-4">
                <input
                  type="checkbox"
                  name={option.id}
                  value={option.id}
                  onChange={(e) => this.selectCheckboxAnswer(e, checkBoxRef, option)}
                  defaultChecked={selectedOptionID.includes(option.id)}
                />
                <span className="checkbox__control d-flex justify-content-center align-items-center">
                  <i className="icon-check"></i>
                </span>
              </span>
                <span className="radio__label d-flex align-items-center">{option.meta && JSON.parse(option.meta).colors ?
                  JSON.parse(option.meta).colors.map(color => <span className="d-inline-block personalise-checkbox-color mr-1" style={{height: "15px", width: "15px", backgroundColor: color}}/>)
                  : null}<span className="d-inline-block ml-2 onboading-text-color onboarding-font-1 font-medium personalise-checkbox-text">{option.value}</span></span>
              </label>
            </div>
          </div>
        )
      })
      options.push(
        <div>
          <Button
            variant="primary"
            className="px-7 mt-3 font-medium onboarding-next-button"
            onClick={() => this.gotoNextQuestion(this.state.question, this.state.parentId)}
          >Continue</Button>
        </div>
      );
    }

    document.body.style.overflow = "hidden";

    if (this.state.question && !this.ANIMATED) {
      let el1_toast = document.querySelector("#onboarding-animate-1")
      let el2_question = document.querySelector("#onboarding-animate-2")
      let el3_options = document.querySelector("#onboarding-animate-3")

      if (el1_toast && el2_question && el3_options) {
        el1_toast.classList.remove("onboarding-animate-toast");
        el2_question.classList.remove("onboarding-animate-question");
        el3_options.classList.remove("onboarding-animate-options");

        el1_toast.classList.add("onboarding-animate-toast");
        el2_question.classList.add("onboarding-animate-question");
        el3_options.classList.add("onboarding-animate-options");

        el1_toast.style.opacity = 0;
        el2_question.style.opacity = 0;
        el3_options.style.opacity = 0;

        this.ANIMATED = true
      }
    }

    return (
      <div>
        <div className="personalise">

          {/* Personalise Body */}
          <div className="baby-registry-onboarding">
            <div className="onboarding-background-color">
              {/* Personalise Header */}
              <PersonaliseHeader/>

              <div className="container pb-25 pt-10 pt-lg-10">

                {/* Toast */}
                <div className="row justify-content-center mb-3">
                  <div className="col-12 col-md-6 text-center">
                    {/* Toast messages */}
                    <span
                      className="onboading-text-color onboarding-toast onboarding-font-1 onboarding-animate-toast"
                      dangerouslySetInnerHTML={{__html: this.state.toastMessage ? this.state.toastMessage : null}}
                      id="onboarding-animate-1"
                      onAnimationEnd={(ev) => {
                        ev.target.style.opacity = 1;
                        ev.target.classList.remove('onboarding-animate-toast')
                      }}
                    />
                  </div>
                </div>
                <div className="row justify-content-center mb-3">
                  <div className="col-12 col-md-8 text-center">
                <span
                  className="font-body onboading-question onboarding-animate-question"
                  id="onboarding-animate-2"
                  onAnimationEnd={(ev) => {
                    ev.target.style.opacity = 1;
                    ev.target.classList.remove('onboarding-animate-question')
                  }}
                  style={{animationDelay: this.state.toastMessage.length > 0 ? ONBOARDING_ANIMATION_DELAY.QUESTION_WITH_TOAST : ONBOARDING_ANIMATION_DELAY.QUESTION_WITHOUT_TOAST}}
                >{this.state.question.title}</span>
                  </div>
                </div>
                <div className="row justify-content-center mt-6 mb-16 mb-md-0">
                  <div
                    className="col-12 text-center onboarding-animate-options onboarding-options-section"
                    onAnimationEnd={(ev) => {
                      ev.target.style.opacity = 1;
                      ev.target.classList.remove('onboarding-animate-options')
                    }}
                    style={{animationDelay: this.state.toastMessage.length > 0 ? ONBOARDING_ANIMATION_DELAY.OPTION_WITH_TOAST : ONBOARDING_ANIMATION_DELAY.OPTION_WITHOUT_TOAST}}
                    id="onboarding-animate-3"
                  >
                    {options.map(option => option)}
                    <SkipQuestionLink className="d-block d-lg-none mt-7" onClick={() => {
                      this.gotoNextQuestion(this.state.question, this.state.parentId)
                    }}/>
                  </div>
                </div>
              </div>
            </div>

            {/* Personalise Footer */}
            <PersonaliseFooter
              percentage={this.calulatePercentage(this.props.statePersonaliseAnswers.length, this.props.statePersonaliseQuestions.length)}
              prevQuestion={() => {
                this.gotoPrevQuestion(this.state.prevQuestionId[this.state.prevQuestionId.length - 1])
              }}
              nextQuestion={() => {
                this.gotoNextQuestion(this.state.question, this.state.parentId)
              }}
            >
              <SkipQuestionLink className="d-none d-lg-block footer-skip-question-link" onClick={() => {
                this.gotoNextQuestion(this.state.question, this.state.parentId)
              }}/>
            </PersonaliseFooter>
          </div>
        </div>

        <ToastContainer
          position="top-center"
          className="personalise-toast-container"
          toastClassName="personalise-toast px-3 py-1"
          autoClose={2000}
          hideProgressBar={true}
          newestOnTop={true}
          draggable={false}
          closeOnClick={false}
          closeButton={null}
        />
      </div>
    );
  }
};

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateLoggedIn: state.registry.loggedIn,
    stateCheckList: state.personaliseCheckList.editedPersonaliseCheckList,
    statePersonaliseQuestions: state.personaliseCheckList.questions,
    statePersonaliseAnswers: state.personaliseCheckList.answers,
    stateSeo: state.registry.seo
  }
}


export default connect(
  mapStateToProps
)(PersonaliseChecklist);