import React from 'react';
import './TourCard.scss';


const TourCard = (props) => {
  // { current, content, totalSteps, gotoStep, close }

  let dotsArray = []
  if (props.totalSteps > 1) {
    for (let i = 0; i < props.totalSteps; i++) {
      if (i <= props.current) {
        dotsArray.push(true)
      } else {
        dotsArray.push(false)
      }
    }
  }

  let el = document.getElementById('___reactour');
  if(el) {
    let markmain = el.querySelector('[mask="url(#mask-main)"]')
    if(markmain) {
      markmain.style.fill = "#89d6d7"
    }

    let mask = document.getElementById('mask-main')
    if(mask) {
      let rect = mask.querySelector("rect[fill='black']")
      rect.style.stroke = "#89d6d7"
      rect.style.strokeWidth= "12px"
    }

    let bgDivEl = document.querySelector('.kWwJhA');
    if(bgDivEl) {
      bgDivEl.style.color = "#fff";
      bgDivEl.style.opacity = "0.8";
    }
  }

  return (
    <div className="p-6 tour-card">
      <div className="d-flex mb-2">
        {/* {[...dotsArray].map((dotsData, index) =>
          // <div className={['tour-card-dots', dotsData ? 'visited' : ''].join(" ")}>
          // </div>
          <div
            className={['mr-1 text-sm tour-card-number', index === props.current ? 'font-medium' : ''].join(" ")}
            onClick={() => props.gotoStep(index)}
          >
            {index + 1}</div>
        )} */}
        <div className="tour-card-number text-sm">{props.current + 1}/{props.totalSteps}</div>
      </div>
      {props.content}
      <div
        className="mt-4 py-1 px-3 tour-card-btn font-medium"
        onClick={() => props.current === Number(props.totalSteps - 1) ? props.close() : props.gotoStep(props.current + 1)}
      >
        {props.totalSteps === 1 ? 'Ok, Got It!' : props.current === Number(props.totalSteps - 1) ? "Let’s Get Started" : "Next"}
      </div>
    </div>
  )
}

export default TourCard;

