export const REGISTRY_ACTION_TYPES = {
  UPDATE_REGISTRY_INFO: "UPDATE_REGISTRY_INFO",
  SET_LOGGING_STATUS: "SET_LOGGING_STATUS",
  SET_TODO : "SET_TODO",
  SET_ONBOARDING_REGISTRY: "SET_ONBOARDING_REGISTRY",

  // For Loader
  SET_LOADER: 'SET_LOADER',

  // For login user
  SET_USER_INFO: 'SET_USER_INFO',

  // For meta title description
  SET_META_INFORMATION: "SET_META_INFORMATION"
}

export const updateRegistryInfo = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.UPDATE_REGISTRY_INFO,
    payload: params
  }
}

export const setLoggingStatus = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_LOGGING_STATUS,
    payload: params
  }
}

export const setTodo = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_TODO,
    payload: params
  }
}

export const setOnboardingRegistry = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_ONBOARDING_REGISTRY,
    payload: params
  }
}

export const setLoader = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_LOADER,
    payload: params
  }
}

export const setUserInfo = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_USER_INFO,
    payload: params
  }
}

export const setMetaInformation = (params) => {
  return {
    type: REGISTRY_ACTION_TYPES.SET_META_INFORMATION,
    payload: params
  }
}
