export const getProducts = (registryId, dispatch) => {
  let data = {
    query: GET_PRODUCTS,
    variables: {
      registryId: parseInt(registryId)
    }
  }
  dispatch(setLoader(true))
  graphQL(data).then(res => {
    // Filter Count
    let filterCount = {
      categoryData: {...res.data.data.registryItems.categoryData},
      registryItemsAnalysisCount: {...res.data.data.registryItems.registryItemsAnalysisCount}
    }
    dispatch(setFilterProductCount(filterCount))

    let responseData = res.data.data.registryItems.registryItems.edges.map(item => {
      // return { ...item.node }
      let newItemObj = {...item.node}
      delete newItemObj.registryitemImage;
      if (item.node.registryitemImage.length > 0) {
        newItemObj.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + item.node.registryitemImage[0].image;
      }
      return newItemObj;
    })

    // TODO: change category
    let finalData = responseData.filter(product => product.registryCategories.length > 0)
    dispatch(setRegistryProducts(finalData))
    dispatch(setLoader(false))
  }).catch(err => {
    dispatch(setLoader(false))
    console.error(err.response)
  })

  getTodoList(registryId, dispatch)
}

export const toggleMustHaveProduct = (product, dispatch) => {
  let data = {
    query: TOGGLE_MUST_HAVE,
    variables: {
      productId: product.id,
      mustHaveValue: !product.mostWanted,
      quantity: product.quantity
    }
  }
  graphQL(data).then(res => {
    if (res.status === 200) {
      dispatch(toggleMustHaveProductAction(product.item.id))
    }
  }).catch(err => {
    console.error(err.message)
  })
}

export const removeProductFromCartHandler = (card, registryId, dispatch) => {
  let registryItemId = card.id
  let data = {
    query: REMOVE_REGISTRY_ITEM,
    variables: {
      registryItemId: registryItemId
    },
  }
  dispatch(setLoader(true))
  graphQL(data).then(res => {
    console.log({...res.data.data.errors})
    if (res.data.errors && res.data.errors.length) {
      for (let error of res.data.errors) {
        toast.error(error.message)
      }
      return;
    } else {
      // dispatch(removeProductFromCart(card.item.id))
      // this.getProducts();
      getProducts(registryId, dispatch);
      getCategories(registryId, dispatch);
      dispatch(setLoader(false))
      toast.error((
        <p><b>{card.title}</b> removed from your registry!</p>
      ))
      let isCashFund = false;
      if (card.registryCategories.length > 0) {
        for (let category of card.registryCategories) {
          if (Number(category.category.categoryType.id) === 2) {
            isCashFund = true;
            break;
          }
        }
      }
      if (card.isGroupGift || card.isCashAcceptedOnly || isCashFund) {
        updateRegistryDetail(dispatch)
      }
      // getCategories()
    }

  }).catch(err => {
    dispatch(setLoader(false))
    console.error(err.message)
    toast.error((
      <p>Some error occurred.</p>
    ))
  })
}

// While using this method. Plase mak sure of parentCategory Parameter
export const getRefinedCategory = (category, parentCategory = null) => {
  return ({
    id: category.id,
    category: category.category,
    categoryName: category.category.categoryName,
    description: category.category.description,
    categoryType: category.category.categoryType,
    isDesired: category.isDesired,
    isExcluded: category.isExcluded,
    addedItemsCount: category.addedItemsCount < category.totalItemsCount ? category.addedItemsCount : category.totalItemsCount,
    totalItemsCount: category.totalItemsCount,
    isParent: category.category.isParent,
    parentCategory: parentCategory
  })
}


// Get all categories
export const getAllCategories = (registryCategories, getAll) => {
  // let finalCondition = category.subCategoriesIsExcluded ?
  let parentCategories = [...registryCategories].filter(category => getAll ? true : (category.subCategoriesIsExcluded ? !category.subCategoriesIsExcluded : !category.isExcluded))
  let finalCategories = [...parentCategories].map(parentCategory => {
    let newParentCategory = getRefinedCategory({...parentCategory})
    let subCategory = parentCategory.registrySubCategories;
    // delete parentCategory.registrySubCategories
    let newSubCategories = subCategory.filter(cat => getAll ? true : (cat.subCategoriesIsExcluded ? !cat.subCategoriesIsExcluded : !cat.isExcluded))
    let newSubCategoriesAddedItemsCount = 0;
    newSubCategories = newSubCategories.map(cat => {
      let newAddedItemsCount = 0;
      let refinedSubCate = getRefinedCategory({...cat}, {
        id: parentCategory.category.id,
        name: parentCategory.category.categoryName
      })
      let subProductTypes = cat.registrySubProductTypes.map(subProduct => getRefinedCategory(subProduct, {
        id: cat.category.id,
        name: cat.category.categoryName
      }))

      refinedSubCate['subProductTypes'] = subProductTypes.filter(category => {
        let returnStatus = getAll ? true : !category.isExcluded
        if (returnStatus) {
          newAddedItemsCount += category.addedItemsCount
        }
        return returnStatus
      })
      newSubCategoriesAddedItemsCount += refinedSubCate.subProductTypes.length ? newAddedItemsCount : refinedSubCate.addedItemsCount
      refinedSubCate['addedItemsCount'] = refinedSubCate.subProductTypes.length ? newAddedItemsCount : refinedSubCate.addedItemsCount
      return refinedSubCate
    })
    newParentCategory['subCategories'] = newSubCategories
    newParentCategory['addedItemsCount'] = newParentCategory.subCategories.length ? newSubCategoriesAddedItemsCount : newParentCategory.addedItemsCount
    return newParentCategory
  })

  return finalCategories
}

export const getCategories = (registryId, dispatch) => {
  let data = {
    operationName: "getCategories",
    query: GET_CATEGORIES,
    variables: {
      registryId: parseInt(registryId)
    },
  }
  // dispatch(setLoader(true))
  graphQL(data).then(res => {
    if (res.data.errors || res.data.error) {
      // TODO: Add toast
      console.log('Something went wrong-->', res.data.errors)
      return;
    } else {
      let resCategories = [...res.data.data.registryCategories]
      let finalCategories = getAllCategories([...resCategories], false)
      let allCategories = []
      if (finalCategories.length === 0) {
        allCategories = getAllCategories([...resCategories], true)
        dispatch(setCheckListItems(allCategories))
      } else {
        dispatch(setCheckListItems(finalCategories))
      }

      dispatch(setLoader(false))
      if (allCategories.length === 0) {
        allCategories = getAllCategories([...resCategories], true)
      }
      // Setting all checklist items
      dispatch(setAllCheckListItems(allCategories))
      dispatch(setLoader(false))
    }
  })
}

export const updateRegistryDetailState = (dispatch, registryData) => {
  let currentRegistryInfo = null;

  currentRegistryInfo = registryData[0].node;
  if (currentRegistryInfo.image) {
    currentRegistryInfo.image.image = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + currentRegistryInfo.image.image;
  }

  if (currentRegistryInfo) {
    dispatch(updateRegistryInfo({...currentRegistryInfo}))
  } else {
    dispatch(updateRegistryInfo({notFound: true}))
    console.log("No registry found!");
  }
}

export const updateRegistryDetail = (dispatch) => {
  let data = {
    query: GET_REGISTRIES
  }

  graphQL(data).then(res => {
    try {
      if (res.data.data.registries.edges) {
        updateRegistryDetailState(dispatch, res.data.data.registries.edges)
      } else {
        dispatch(updateRegistryInfo({notFound: true}))
      }
    } catch (e) {
      dispatch(updateRegistryInfo({notFound: true}))
      console.error(e)
    }
  }).catch(err => {
    dispatch(updateRegistryInfo({notFound: true}))
    console.error(err.response ? err.response.data : null)
  })
}

