const getTrackGiftsFilterStatus = (filterString, trackGiftsFilter) => {
  let returnStatus = false
  switch (filterString) {
    case "available":
      for (const filter of trackGiftsFilter) {
        if (filter.id === 4 && filter.count > 0) {
          returnStatus = filter.selected
          break;
        }
      }
      break
    case "purchased":
      for (const filter of trackGiftsFilter) {
        if (filter.id === 1 && filter.count > 0) {
          returnStatus = filter.selected
          break;
        }
      }
      break
    case "reserved":
      for (const filter of trackGiftsFilter) {
        if (filter.id === 3 && filter.count > 0) {
          returnStatus = filter.selected
          break;
        }
      }
      break
    case "fundsReceived":
      for (const filter of trackGiftsFilter) {
        if (filter.id === 2 && filter.count > 0) {
          returnStatus = filter.selected
          break;
        }
      }
      break
    case "returned":
      for (const filter of trackGiftsFilter) {
        if (filter.id === 5 && filter.count > 0) {
          returnStatus = filter.selected
          break;
        }
      }
      break
    default:
      returnStatus = false
      break
  }
  return returnStatus;
}

export const getTrackGiftsProduct = (clearProducts = false, trackGiftsFilters, trackGiftsProduct, trackGiftsPagination, trackGiftsView, registryInfo, trackGiftsSearch, dispatch, first = TRACK_GIFTS_PRODUCTS_PER_REQUEST) => {
  dispatch(setLoader(true))
  // TODO: Change this Function when Search API in implemented
  let reqData = {
    query: GET_TRACK_GIFTS,
    operationName: "registryGifts",
    variables: {
      registryId: registryInfo?.id,
      giftGivers: trackGiftsView?.id === TRACK_GIFTS_VIEWS[1].id,  // [1] index is Gift Giver
      after: clearProducts ? null : trackGiftsPagination.endCursor,
      first: first,
      searchKeyword: trackGiftsSearch.searchText,
      isReserved: getTrackGiftsFilterStatus("reserved", trackGiftsFilters),
      isAvailable: getTrackGiftsFilterStatus("available", trackGiftsFilters),
      isPurchased: getTrackGiftsFilterStatus("purchased", trackGiftsFilters),
      isFundsReceived: getTrackGiftsFilterStatus("fundsReceived", trackGiftsFilters),
      isReturned: getTrackGiftsFilterStatus("returned", trackGiftsFilters),
    }
  }

  graphQL(reqData).then(res => {
    // Updating Track Gifts Filters
    if (res.data.data.registryGifts && res.data.data.registryGifts.registryGiftItemsCount) {
      const allFilterCounts = res.data.data.registryGifts.registryGiftItemsCount;
      const newFilters = trackGiftsFilters.map(filter => {
        filter.count = allFilterCounts[LOCAL_2_API_FILTERS_MAPPING[filter.id]] ? allFilterCounts[LOCAL_2_API_FILTERS_MAPPING[filter.id]] : 0
        return filter
      })

      dispatch(setTrackGiftsFilters(newFilters))
    }

    // Updating Track Gifts Products
    if (res.data.data.registryGifts && res.data.data.registryGifts.gifts) {
      let registryGifts = res.data.data.registryGifts.gifts
      let products = registryGifts.edges.map(item => {
        let product = {...item.node}
        if (product.registryitemImage.length > 0) {
          product.imageUrl = (MEDIA_URL_PREFIX.LOCAL_MEDIA_URL ? MEDIA_URL_PREFIX.LOCAL_MEDIA_URL : "") + product.registryitemImage[0].image;
        }
        return product
      })
      let pagination = null
      if (registryGifts.pageInfo) {
        pagination = {
          hasNextPage: registryGifts.pageInfo.hasNextPage,
          endCursor: registryGifts.pageInfo.endCursor,
        }
      }
      if (pagination) {
        dispatch(setTrackGiftsPaginationInfo({...pagination}))
      }
      if (clearProducts) {
        dispatch(setTrackGiftsProduct([...products]))
      } else {
        dispatch(setTrackGiftsProduct([...trackGiftsProduct, ...products]))
      }

    } else {
      dispatch(setTrackGiftsPaginationInfo({
        hasNextPage: false,
        endCursor: null
      }))
    }
    dispatch(setLoader(false))
  }).catch(err => {
    dispatch(setTrackGiftsPaginationInfo({
      hasNextPage: false,
      endCursor: null
    }))
    console.error(err.response)
    dispatch(setLoader(false))
  })
}

