export const setMetaInfoToDom = (metaInfo, pageName) => {

  let titleTag = document.getElementsByTagName("title")
  titleTag = titleTag.length > 0 ? titleTag[0] : null;

  let setMetaInfo = titleTag && titleTag.getAttribute("data-page") ? titleTag.getAttribute("data-page") !== pageName : true

  // Setting Meta description
  if (setMetaInfo) {
    // Fetching existing meta description tag
    let existingMetaTag = document.getElementsByName("description")
    let descriptionMetaTag = null

    if (existingMetaTag.length > 0) {
      for (let metaTag of existingMetaTag) {
        if (metaTag.tagName === "META") {
          descriptionMetaTag = metaTag
          break;
        }
      }
    }
    if (descriptionMetaTag) {
      // Updating existing meta description tag
      descriptionMetaTag.setAttribute("content", metaInfo.description)
    } else {

      // Creating new meta description tag
      let newMetaTag = document.createElement("meta")
      newMetaTag.setAttribute("name", "description")
      newMetaTag.setAttribute("content", metaInfo.description)
      document.head.appendChild(newMetaTag)
    }
    // Setting title
    document.title = metaInfo.title
    titleTag.setAttribute("data-page", pageName)
  }
}

export const checkForBankAccount = (isCashFund, isCashInLieu, isGroupGift, dispatch) => {
  const state = store.getState()
  if (state.registry.info && state.registry.info.user && state.registry.info.user.bankAccounts.length <= 0 && state.registry.info.isPublic) {

    let data = {
      query: UPDATE_REGISTRY,
      operationName: "updateRegistryDetail",
      variables: {
        "registryId": state.registry.info.id,
        "input": {
          isPublic: false
        }
      }
    }

    graphQL(data).then(res => {
      try {
        if (res.data.data.updateRegistry.registry) {
          // TODO: Add msg to tell user that registry is been set to private
          console.log("Registry status is changed to Private.")
          let newRegistry = state.registry.info;
          newRegistry.isPublic = false
          newRegistry.isCashFundItemAdded = res.data.data.updateRegistry.registry.isCashFundItemAdded
          newRegistry.isCashInLieuItemAdded = res.data.data.updateRegistry.registry.isCashInLieuItemAdded
          newRegistry.isGroupGiftItemAdded = res.data.data.updateRegistry.registry.isGroupGiftItemAdded
          dispatch(updateRegistryInfo(newRegistry))
          getTodoList(state.registry.info.id, dispatch)
        } else {
          console.log(res.data.data.updateRegistry.errors);
        }
      } catch (e) {
        toast.error("Some error occurred while changing status of registry");
        console.error(e);
      }
    }).catch(err => {
      toast.error("Some error occurred while changing status of registry");
      console.error(err.message)
    })

    // let productMsg = isCashFund ? "Cash Fund" : isCashInLieu ? "Cash In Lieu of product" : isGroupGift ? "Group Gift" : "";
    // toast.error(<p>{`You've added ${productMsg} without adding Bank Account. Please add bank account detail.`}</p>)
  }
}

export const getAllVarient = (card, registryInfo, dispatch) => {
  if (card.activeVariant && !card.activeVariant.isAllVariantFetch) {
    let data = {
      query: GET_MA_VARIANT_BY_SIZE,
      variables: {
        registryId: registryInfo.id,
        itemRecommendationId: card.id,
        maItemVariantId: card.activeVariant.id
      }
    }
    graphQL(data).then(res => {
      if (res.status === 200) {
        let resData = [...res.data.data.maItemVariantsBySize]
        let finalData = []
        for (let variant of resData) {
          let finalVariant = {...variant}
          finalVariant.title = variant.size
          finalVariant.selected = card.activeVariant.activeSizeVariant.id === variant.id
          finalData.push(finalVariant)
        }
        dispatch(setActiveRecommandationVariantBySize(card.id, card.activeVariant.variantId, finalData))
      }
    }).catch(err => {
      console.error(err.message)
    })
  }
}

export const setActiveVariantSize = (card, variantId, dispatch) => {
  let sizeVariant = null
  for (let variant of card.maItemVariantItemRecommendation) {
    if (variant.variantId === card.activeVariant.variantId) {
      for (let vrt of variant.allSizeVariant) {
        if (vrt.id === variantId) {
          sizeVariant = {...vrt};
          delete sizeVariant.activeVariant;
          delete sizeVariant.allSizeVariant;
          delete sizeVariant.activeSizeVariant;
          break;
        }
      }
    }
    if (sizeVariant) {
      break;
    }
  }
  if (sizeVariant) {
    dispatch(setActiveRecommandationSizeVariant(card.id, sizeVariant))
  }
}

// Fetching lower price if price is in range
export const getRefinedPrice = (price) => {
  price = price.split("-")[0].trim()
  let newPrice = "";
  for (let char of price) {
    if (char !== ".") {
      if (Number.isInteger(Number(char))) {
        newPrice += char
      }
    } else {
      newPrice += char
    }
  }

  return newPrice;
}

