import React from "react";
import './ProductDetailsModel.scss';
import GiftStatusPill from '../gift-status-pill/GiftStatusPill'
import { formatPrice, productImageLoadErrorHandler, productImageLoadHandler } from "../../../../utils/commonFunction";

const ProductDetailsModel = (props) => {
  return (
    <div className="row product-details-model">
      <div className="col-12 product-details-image">
        {props.data?.imageUrl ?
          <img className="product-img d-block mx-auto" src={props.data?.imageUrl}
            onError={(ev) => productImageLoadErrorHandler(ev)}
            onLoad={(ev) => productImageLoadHandler(ev)}
            alt={props.data?.title} />
          :
          <div className="added-card-no-product-image" />
        }
        {props.data.mostWanted ?
          <div
            className="d-flex align-items-center product-details-have product-details-must-have-btn px-1 added-gift-must-have-active">
            <i className="icon mr-1 d-block heart-fill" />
            <p className="text-sm product-details-must-have-text">Must Have</p>
          </div>
          : <></>}
        <div className="px-3 mt-2">
          <p className="font-medium">{props.data.title}</p>
          {!props.data.item.isCashFund ? <a className="product-detail-link text-sm font-medium mt-1" href={props.data.item.storeItemUrl} target="_blank">View Details</a> : null}
          <div className="d-flex align-items-baseline give-gift-model">
            {props.data.isGroupGift ?
              <div className="guest-toggle-pills px-1 mr-1 d-flex align-items-center">
                <i className="guest-icon-active mr-1" />
                <span className="text-sm guest-icon-text">Group Gift</span>
              </div> : null}
            {props.data.item.hideTotal ? null : <p className="mt-2">{formatPrice(props.data.item.price)}</p>}
          </div>
          {props.data.note ?
            <>
              <p className="mt-2 text-sm product-note font-medium">Note:</p>
              <p className="text-sm product-note">{props.data.note}</p>
            </>
            : null}
          <div className="d-flex mt-2 mb-3 flex-wrap align-items-end">
            {props.data.quantityStatus.purchased_quantity ?
              <div>
                <GiftStatusPill className="mr-1" variant="purchased" text={props.data.quantityStatus.purchased_quantity + " Purchased"} />
              </div>
              : null}
            {props.data.quantityStatus.reserved_quantity ?
              <div className="mr-1 mt-md-1 mt-lg-0">
                <GiftStatusPill variant="reserved" text={props.data.quantityStatus.reserved_quantity + " Reserved"} />
              </div>
              : null}
            {props.data.quantityStatus.still_needed && props.data.registryCategories && props.data.registryCategories.length > 0 && Number(props.data.registryCategories[0].category.categoryType.id) !== 2 ?
              <div className="mr-1 mt-md-1 mt-lg-1">
                <GiftStatusPill variant="default" text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.purchased_quantity === 0 ? " Requested" : " Still Needed"}`} />
              </div>
              : null}
          </div>
        </div>
      </div>
    </div>
  )
}

export default ProductDetailsModel;