import React from "react";
import "./TodoWidget.scss";
import {connect} from "react-redux";
import {getTodoList} from "../../../../utils/commonFunction";

class TodoWidget extends React.Component {

  performTodoAction = todo => {
    //  Perform action related to to-do
    if (todo.redirect) {
      window.open(todo.redirectUrl, "_self")
    }
  }

  render() {
    if (this.props.stateTodo === null && this.props.stateRegistryInfo.id) {
      getTodoList(this.props.stateRegistryInfo.id, this.props.dispatch)
    }

    return (
      <div className="baby-registry-todo-widget">
        {this.props.stateTodo !== null && this.props.stateTodo.length > 0 ?
          (
            //  When there is To-do
            <div className="baby-registry-todo-section">
              <a
                tabIndex={1}
                className="baby-steps-button d-flex align-items-center pt-1 pt-md-3 pb-0 pb-md-3 pl-1 pl-md-4 pr-1 pr-md-8">
                <i className="baby-steps-icon"/>
                <div className="ml-5 baby-steps-text">
                  <p className="text-body font-medium">{this.props.stateTodo.length} Baby {`Step${this.props.stateTodo.length > 1 ? 's': ''}`}</p>
                  <p className="text-sm text-left">to take next!</p>
                </div>
              </a>

              <div className="baby-steps-todo-content p-2">
                {this.props.stateTodo.map((todo, index) => (
                  <div className="baby-step-todo d-flex justify-content-between py-2 align-items-center" key={`todo-${index}`}>
                    <p className="text-body mr-3">{todo.label}</p>
                    <button className="baby-step-action-btn"
                            onClick={() => this.performTodoAction(todo)}/>
                  </div>
                ))}
              </div>
            </div>
          )
          :
          this.props.stateTodo !== null ?
            //  When there is no To-do
            <div className="baby-registry-no-todo-section d-flex align-items-center py-4">
              <i className="no-todo-icon"/>
              <div className="ml-2">
                <p className="text-body font-medium">Great job!</p>
                <p className="text-sm">More to come, soon.</p>
              </div>
            </div>
            : null
        }
      </div>
    );
  }
}

const mapStateToProps = state => ({
  stateTodo: state.registry.todo,
  stateRegistryInfo: state.registry.info
})

export default connect(
  mapStateToProps
)(TodoWidget);