const GET_PRODUCTS = `
query registryItems(
  $registryId:Int!
  $after: String,
  $first: Int,
  $isReserved: Boolean,
  $isPurchased: Boolean,
  $isAvailable: Boolean,
  $sortBy: String,
  $categories:[Int],
  $price_Lte: Float,
  $price_Gte: Float,
  $store: [String],
  ){
    registryItems(
      registryId:$registryId
      isReserved: $isReserved,
      isPurchased: $isPurchased,
      isAvailable: $isAvailable,
      sortBy: $sortBy,
      categories: $categories,
      price_Lte: $price_Lte,
      price_Gte: $price_Gte,
      store: $store,
      ){
      registryItems(after: $after, first: $first) {
        edges {
          node {
            id
            registryitemImage {
              image
            }
            isGroupGift
            isReserved
            quantity
            mostWanted
            featuredOrder
            isReserved
            isAmountReceived
            isPurchased
            title
            imageUrl
            isCashAcceptedOnly
            quantityStatus
            purchasedQuantity
            isBundleProduct
            purchasedRegistryItem{
              id,
              purchasedQuantity,
              isPurchased,
              giftMessage,
              orderNo,
              storeName,
              createdTs,
              contributionAmount
              groupGift{
                amountReceived
              }
              guestUser{
                email,
                firstName,
                lastName,
              }
            }
            registryCategories {
              id
              category {
                id
                categoryName
                categoryType {
                  id
                  name
                }
                isParent
                parentCategory {
                  id
                  categoryName
                  isParent
                    parentCategory {
                    categoryName
                  }
                }
              }
            }
            note
            item {
              store {
                id
                storeName
              }
              id
              shopifyItemId
              shopifyItemVariantId
              storeItemUrl
              sku
              price
              hideTotal
            }
          }
        }
        pageInfo {
          endCursor
          hasNextPage
        }
      }
      registryItemsAnalysisCount{
        totalAvailable,
        totalReserved,
        totalPurchased,
        maStoreItems,
        otherStoreItems,
        under50,
        moreThan100,
        between50100,
        totalFundsReceived
      }
      categoryData
    }
  }
`

