const ADD_REGISTRY_ITEM = `
mutation addRegistryItems(
    $registryId:Int!,
    $title:String!,
    $category:[Int],
    $tags:[Int]!,
    $storeItemUrl:String!,
    $shopifyItemVariantId: String,
    $sku:String!,
    $price:Float!,
    $quantity:Int!,
    $shopifyItemId:String,
    $storeName:String!
    $imageUrl: String,
    $mostWanted: Boolean,
    $isBundleProduct: Boolean!,
    $isGroupGift: Boolean,
    $bundleProduct: [BundleProductInput]
    $isCashAcceptedOnly: Boolean){
  addRegistryItems(registryId:$registryId,
  registryItems:[{
    storeName: $storeName,
    items: [{
      title:$title,
      registryCategories:$category,
      tags:$tags,
      storeItemUrl:$storeItemUrl,
      sku:$sku,
      price:$price,
      quantity:$quantity,
      shopifyItemId: $shopifyItemId,
      imageUrl: $imageUrl,
      isBundleProduct: $isBundleProduct,
      bundleProduct: $bundleProduct,
      shopifyItemVariantId: $shopifyItemVariantId,
      mostWanted: $mostWanted,
      isGroupGift: $isGroupGift,
      isCashAcceptedOnly: $isCashAcceptedOnly
    },]
  }])
  {
    message
  }
}
`;

const UPDATE_REGISTRY_ITEM = `
mutation updateRegistryItems(
  $registryItemId: Int!,
  $quantity: Int!,
  $title:String,
  $category:[Int],
  $storeItemUrl:String,
  $price:Float,
  $mostWanted: Boolean,
  $isGroupGift: Boolean,
  $isCashAcceptedOnly: Boolean){
  updateRegistryItems(registryItemId:$registryItemId, registryItemData:{
    quantity:$quantity,
    title:$title,
    registryCategories:$category,
    storeItemUrl:$storeItemUrl,
    price:$price,
    isReserved:false,
    mostWanted: $mostWanted,
    isGroupGift: $isGroupGift,
    isCashAcceptedOnly: $isCashAcceptedOnly
  }){
    message
  }
}
`;

const REMOVE_REGISTRY_ITEM = `
mutation deleteRegistryItems($registryItemId: Int!){
  deleteRegistryItems(registryItemId:$registryItemId){
    message
  }
}
`;

