import React from "react";
import "./CategoryPill.scss";

const CategoryPill = (props) => {
  return (
    <div className={["import-registry-category-pill text-sm p-1", props.className ? props.className : ""].join(" ")}>
      <div className="d-flex align-items-center">
        <p>{props.mainCategory+": "+props.name}</p>
        <i className="icon-close remove-category-pill-icon ml-2" onClick={props.removeCategory}/>
      </div>
    </div>
  );
}

export default CategoryPill;
