import React from 'react';
import "./PersonaliseHeader.scss";
import {withRouter} from "react-router";
import routes from "utils/routes";
import {ROUTES} from "../../../../utils/cdn-assets-links";


const PersonaliseHeader = (props) => {
  const closePersonalise = (event) => {
    event.preventDefault();
    // And Forwarding him to edit checklist
    if (props.location.state?.openEditChecklist) {
      props.history.push(routes.PERSONALIZE.CHECKLIST)
    }
    // And Forwarding to build registry
    else {
      props.history.push(routes.MY_REGISTRY.HOME)
    }
  }

  return (
    <div className="personalise-header" id="personalise-header-quiz">
      <div className="m-a-personalise-logo-section text-center">
        <a href="/" className="d-block py-4">
          <img className="mx-auto d-none d-md-block" src={ROUTES.MA_LOGO} alt="logo"/>
          <img className="mx-auto d-block d-md-none" src={ROUTES.MA_ELEPHANT_LOGO} alt="logo"/>
        </a>
      </div>
      <button className="personalize-close-btn d-flex p-2" onClick={(e) => { closePersonalise(e) }}>
        <i className="icon-close mr-2"/>
        <span className="personalise-close-btn-text onboarding-font-3 font-medium">Exit</span>
      </button>
      {/*<div className="container">*/}
      {/*    <div className="row">*/}
      {/*        <div className="col d-flex justify-content-between py-2">*/}
      {/*            <p className="text-body font-medium personalise-text">My Registry Quiz</p>*/}
      {/*            <i className="icon-close align-self-center personalise-close" onClick={(e) => { closePersonalise(e) }} />*/}
      {/*        </div>*/}
      {/*    </div>*/}
      {/*</div>*/}
    </div>
  );
}

export default withRouter(PersonaliseHeader);