import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from 'react-redux';
import { setCashContributedAlreadyInfo } from 'modules/guest-view/GuestViewAction';
import ProductDetailsModel from '../product-details-model/ProductDetailsModel';
import Counter from "../counter/Counter";
import { store } from "store/Store";
// import './AlreadyBoughtProduct.scss';
import { LOCAL_STORAGE_GUEST_GIFT_BUY_KEY } from 'utils/constants';

const CashContributedAlready = (props) => {

  const dispatch = useDispatch();
  const stateRegistryInfo = useSelector(state => state.guestView.info)
  const productInfo = useSelector(state => state.guestView.cashContributedAlready)
  const amount = useSelector(state => state.guestView.cashContributedAlready.amount)
  const name = useSelector(state => state.guestView.cashContributedAlready.name)
  const email = useSelector(state => state.guestView.cashContributedAlready.email)
  const giftMessage = useSelector(state => state.guestView.cashContributedAlready.giftMessage)

  const nameChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('name')
    prodInfo.name = ev.target.value;
    dispatch(setCashContributedAlreadyInfo(prodInfo));
  }

  const emailChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('email')
    prodInfo.email = ev.target.value;
    dispatch(setCashContributedAlreadyInfo(prodInfo));
  }

  const amountChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    props.onFocusHandler('amount')
    if (ev.target.value.length > 0) {
			const newPrice = Number(ev.target.value);
			const hasDecimal = (ev.target.value.split('.').length === 2 ? ev.target.value.split('.')[1].length <= 2 : true)
			if ((newPrice >= 0) && hasDecimal) {
        prodInfo.amount = ev.target.value;
			}
		} else {
      prodInfo.amount = '';
		}
    dispatch(setCashContributedAlreadyInfo(prodInfo));
  }

  const giftMessageChangeHandler = (ev) => {
    ev.persist();
    let prodInfo = { ...productInfo };
    prodInfo.giftMessage = ev.target.value;
    dispatch(setCashContributedAlreadyInfo(prodInfo));
  }

  return (
    <div className="row add-gift-another-store">
      <div className="col">
        <div className="row pb-2">
          <div className="col-12 col-md-5 py-4 add-gift-image-select">
            <div className="mr-3">
              <ProductDetailsModel data={props.data} />
            </div>
          </div>
          {/* // I Bought This Already Model Body */}
          <div className="col-12 col-md-7 mt-4">
            <div className="ml-3">
              <div className="row col">
                <div className="col">
                  <p className="font-body font-medium">Great, let’s update their registry.</p>
                  <p className="text-sm mt-1">This info will help {stateRegistryInfo.user.firstName} {stateRegistryInfo.user.partnerUser ? 'and' + stateRegistryInfo.user.partnerUser.firstName : ''} keep track of all their gifts.</p>
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label guest-view-required">Name</p>
                  <input
                    className={["w-100 mt-1 px-2 mb-0 py-1", props.fieldErrors.name ? "already-bought-input-error" : ""].join(" ")}
                    type='text'
                    placeholder='John Doe'
                    value={name}
                    onChange={(ev) => nameChangeHandler(ev)}
                  />
                  {props.fieldErrors.name ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.name}</span>
                    : null}
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label guest-view-required">Email Address</p>
                  <input
                    className={["w-100 mt-1 px-2 mb-0 py-1", props.fieldErrors.email ? "already-bought-input-error" : ""].join(" ")}
                    type='text'
                    placeholder='johndoe@email.com'
                    value={email}
                    onChange={(ev) => emailChangeHandler(ev)}
                  />
                  {props.fieldErrors.email ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.email}</span>
                    : null}
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label guest-view-required">Amount</p>
                  <div className="price-input mt-1">
                    <input
                      type="text"
                      className={["w-100 pl-2 pr-1 py-1 mb-0 text-body pl-5", props.fieldErrors.amount ? "already-bought-input-error" : ""].join(" ")}
                      value={amount}
                      onChange={(ev) => amountChangeHandler(ev)}
                    />
                  </div>
                  {props.fieldErrors.amount ?
                    <span
                      className="text-sm already-bought-input-error-text">{props.fieldErrors.amount}</span>
                    : null}
                  {/*<span className="price-input text-body">$</span>*/}
                </div>
              </div>
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Gift Message</p>
                  <textarea className="w-100 mt-1 mb-0 text-body p-1"
                    rows="2"
                    placeholder="Write a message for gift receiver"
                    value={giftMessage}
                    onChange={(ev) => giftMessageChangeHandler(ev)}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default CashContributedAlready;