  signUpClickHandler = (firstNameRef, lastNameRef, emailRef, passwordRef) => {
    let errors = {}
    let valid = true
    if (firstNameRef.current.value.length <= 0) {
      errors['firstName'] = "This field is required"
      valid = false
    }

    if (lastNameRef.current.value.length <= 0) {
      errors['lastName'] = "This field is required"
      valid = false
    }

    if (emailRef.current.value.length <= 0) {
      errors['email'] = "This field is required"
      valid = false
    } else if (!this.checkValidEmail(emailRef.current.value)) {
      errors['email'] = "Invalid email address"
      valid = false
    }

    if (passwordRef.current.value.length <= 0) {
      errors['password'] = "This field is required"
      valid = false
    }

    this.setState({
      signUpErrors: errors
    })

    if (!valid) {
      return
    }

    // Clearing all error messages
    this.clearErrorState()

    let reqData = {
      query: REGISTER_USER,
      operationName: "register_user",
      variables: {
        name: `${firstNameRef.current.value} ${lastNameRef.current.value}`,
        email: emailRef.current.value,
        password: passwordRef.current.value
      }
    }
    this.props.dispatch(setLoader(true))
    graphQLNoToken(reqData).then(res => {
      if (res.data.data.registerUser) {
        const cookies = new Cookies();
        if (process.env.NODE_ENV === 'production') {
          document.cookie = "access_token=" + res.data.data.registerUser.token + ";path=/;domain=.lilhathee.com;secure;sameSite=none";
        } else {
          cookies.set("access_token", res.data.data.registerUser.token, {path: "/"})
        }
        if (this.state.directLogin && res.data.data.registerUser.redirectionUrl) {
          window.open(res.data.data.registerUser.redirectionUrl, "_self")
          return;
        }
        this.setState({
          redirectionUrl: res.data.data.registerUser.redirectionUrl
        })
        this.createRegistry()
        this.submitAllAnswers(null, true)
        this.props.dispatch(setLoggingStatus(true))
        this.props.dispatch(setLoader(false))
      }
    }).catch(err => {
      this.setErrorBasedOnResponse(err, "signUp")
      this.props.dispatch(setLoader(false))
    })
  }

