import React from "react";
import {connect} from "react-redux";
import {TRACK_GIFTS_VIEWS} from "../../components/view-by/ViewBy";
import InfiniteScroll from "react-infinite-scroll-component";
import {getTrackGiftsProduct} from "../../../../utils/commonFunction";
import GiftCard from "../../components/gift-card/GiftCard";
import "./TrackGiftContent.scss";

export const LOCAL_2_API_FILTERS_MAPPING = {
  4: "totalAvailable",
  1: "totalPurchased",
  3: "totalReserved",
  2: "totalFundsReceived",
  5: "totalReturned"
}

// Component for No registry Gifts Found
const NoRecordFound = () => {
  return (
    <h2>No purchased gifts found.</h2>
  )
}

class TrackGiftsContent extends React.Component {

  state = {
    currentView: null
  }

  fetchAvailableFromAddedProducts = (products) => {
    let availableProducts = []
    availableProducts = products.filter(product => {
      if (product.isGroupGift) {
        return !product.isPurchased
      } else {
        return !product.isPurchased && !product.isReserved && !product.isAmountReceived
      }
    })
    availableProducts = availableProducts.map(product => {
      let newProduct = {...product}
      newProduct.purchasedRegistryItem = []
      return newProduct
    })
    return availableProducts
  }

  // Separating available products
  getAvailableProduct = (stateProducts) => {
    let availableProducts = []
    let otherSelected = false

    let currentFilterCount = [...this.props.stateTrackGiftsFilters].filter(filter => filter.count > 0).length
    let unselectedCount = [...this.props.stateTrackGiftsFilters].filter(filter => filter.count > 0 && !filter.selected).length

    if (currentFilterCount === unselectedCount) {
      otherSelected = true
      availableProducts = this.fetchAvailableFromAddedProducts(stateProducts)
    } else {
      for (let filter of this.props.stateTrackGiftsFilters) {
        if (filter.id === 4 && filter.selected) {
          availableProducts = this.fetchAvailableFromAddedProducts(stateProducts)
        } else if (filter.count > 0 && filter.selected) {
          otherSelected = true
        }
      }
    }


    return {otherSelected, availableProducts}
  }

  // Getting unique objects from a list
  getUniqueListBy = (arr, key) => {
    return [...new Map(arr.map(item => [item[key], item])).values()]
  }

  getProductsBasedOnView = (view, stateProducts) => {
    let products = []
    let guestUsersEmails = []
    let refinedProducts = []
    let skipCashFund = false;

    for (const product of [...stateProducts]) {
      if (product.purchasedRegistryItem.length <= 0) {
        refinedProducts.push(product)
      } else {
        let userProducts = []
        let currentProduct = {...product}
        skipCashFund = false;
        for (let user of product.purchasedRegistryItem) {
          if (skipCashFund) {
            skipCashFund = false
            break
          }
          let newUsers = {...user}
          let otherUsers = product.purchasedRegistryItem
          newUsers['otherUsers'] = otherUsers
          currentProduct.purchasedRegistryItem = [newUsers]
          userProducts.push({...currentProduct})
          currentProduct = {...product}
          if (product.isGroupGift && view?.id === TRACK_GIFTS_VIEWS[0].id) {
            skipCashFund = true
          }
        }
        refinedProducts.push(...userProducts)
      }
    }

    if (view?.id === TRACK_GIFTS_VIEWS[0].id) { // [0] index is Gifts
      //  Gifts View
      for (const product of refinedProducts) {
        if (product.purchasedRegistryItem.length > 0) {
          products.push({...product})
        }
      }

      let {otherSelected, availableProducts} = this.getAvailableProduct(stateProducts)

      // Show Available products in Gifts View only if Available Filter is applied
      if (!otherSelected && availableProducts.length > 0) {
        // If only available is selected then show only available products
        let id2Remove = []
        let newProducts = products.filter(product => {
          if (product.isGroupGift && product.purchasedRegistryItem.length > 0) {
            id2Remove.push(product.id)
            return true
          }
          return false
        })
        products = [...newProducts, ...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1)]
      } else if (availableProducts.length > 0) {
        let id2Remove = []
        let newProducts = products.filter(product => {
          if (product.isGroupGift && product.purchasedRegistryItem.length > 0) {
            id2Remove.push(product.id)
            return true
          }
          return false
        })
        products.push(...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1))
      }

    } else {

      let partiallyPurchasedGroupGift = []

      //  Gift Giver View
      for (const product of refinedProducts) {

        if (product.purchasedRegistryItem.length > 0) {
          // Grouping Product
          if (product.isGroupGift) {
            if (product.purchasedRegistryItem.length > 0 && !product.isPurchased) {
              partiallyPurchasedGroupGift.push({...product})
            }

            // Looping through each groupGift User
            for (const groupUser of product.purchasedRegistryItem) {

              // Removing unsearched user from group gift
              if (this.props.stateTrackGiftsSearch.searched) {
                if (groupUser.guestUser) {
                  const user = groupUser.guestUser;
                  const searchedText = this.props.stateTrackGiftsSearch.searchText;
                  if (!(user.email && user.email.toLowerCase().match(searchedText)) && !(user.firstName && user.firstName.toLowerCase().match(searchedText)) && !(user.lastName && user.lastName.toLowerCase().match(searchedText))) {
                    continue
                  }
                }
              }

              if (guestUsersEmails.indexOf(groupUser.guestUser.email) !== -1) {
                let newProducts = products.map(groupedProduct => {
                  if (groupedProduct.user.guestUser.email === groupUser.guestUser.email) {
                    groupedProduct.products = [...groupedProduct.products, product]
                  }
                  return groupedProduct
                })
                products = [...newProducts]
              } else {
                products.push({
                  user: {...groupUser},
                  products: [{...product}]
                })
                guestUsersEmails.push(groupUser.guestUser.email)
              }
            }
          } else {
            //  Not a group Gift
            try {
              if (guestUsersEmails.indexOf(product.purchasedRegistryItem[0].guestUser.email) !== -1) {
                // User already have product
                products = products.map(groupProduct => {
                  if (groupProduct.user.guestUser.email === product.purchasedRegistryItem[0].guestUser.email) {
                    groupProduct.products.push(product)
                  }
                  return groupProduct
                })
              } else {
                // User doesn't have product
                products.push({
                  user: {...product.purchasedRegistryItem[0]},
                  products: [{...product}]
                })
                guestUsersEmails.push(product.purchasedRegistryItem[0].guestUser.email)
              }
            } catch {
              availableProducts.push({...product})
            }
          }
        }
      }

      products = products.map(product => {
        product.products = this.getUniqueListBy([...product.products], "id")
        return product
      })

      let userLastName_products = []
      let userFirstName_products = []

      // Splitting Product based on empty Lastname
      for (let product of products) {
        if (product.user.guestUser.lastName) {
          userLastName_products.push(product)
        } else {
          userFirstName_products.push(product)
        }
      }

      // Sorting based on lastName
      userLastName_products = userLastName_products.sort((a, b) => {
        let aLastName = a.user.guestUser.lastName ? a.user.guestUser.lastName : "";
        let bLastName = b.user.guestUser.lastName ? b.user.guestUser.lastName : "";

        return aLastName.localeCompare(bLastName);
      })

      // Sorting based on firstName
      userFirstName_products = userFirstName_products.sort((a, b) => {
        let aFirstname = a.user.guestUser.firstName ? a.user.guestUser.firstName : "";
        let bFirstname = b.user.guestUser.firstName ? b.user.guestUser.firstName : "";

        return aFirstname.localeCompare(bFirstname);
      })

      // Combining sorted products
      products = [...userLastName_products, ...userFirstName_products]

      // Setting available products
      let {otherSelected, availableProducts} = this.getAvailableProduct(stateProducts)
      partiallyPurchasedGroupGift = this.getUniqueListBy([...partiallyPurchasedGroupGift], "id")
      if (!otherSelected && availableProducts.length > 0) {
        // If only available is selected then show only available products
        let id2Remove = [...partiallyPurchasedGroupGift].map(product => product.id)
        products = [{
          availableProducts: true,
          products: [...partiallyPurchasedGroupGift, ...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1)]
        }]
        // products: [...partiallyPurchasedGroupGift, ...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1)]

      } else if (availableProducts.length > 0) {
        let id2Remove = [...partiallyPurchasedGroupGift].map(product => product.id)
        products.push({
          availableProducts: true,
          products: [...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1)]
        })
        // products: [...availableProducts.filter(product => id2Remove.indexOf(product.id) === -1)]
      }
    }

    return products
  }

  render() {
    let noPurchasedProduct = true
    for (let filter of this.props.stateTrackGiftsFilters) {
      if (filter.id !== 4 && filter.count > 0) {
        noPurchasedProduct = false
        break;
      }
      if (filter.id === 4 && filter.selected) {
        noPurchasedProduct = false
        break;
      }
    }

    if (this.props.stateTrackGiftsProducts.length <= 0 && this.state.currentView && this.props.stateTrackGiftsPagination.hasNextPage) {
      getTrackGiftsProduct(true, this.props.stateTrackGiftsFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)
    }

    // Getting new products when new views are switched
    if (this.state.currentView?.id !== this.props.stateTrackGiftsView?.id) {
      this.setState({
        currentView: {...this.props.stateTrackGiftsView}
      })
      getTrackGiftsProduct(true, this.props.stateTrackGiftsFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)
    }

    const products = this.getProductsBasedOnView(this.props.stateTrackGiftsView, this.props.stateTrackGiftsProducts)

    // if (this.props.stateTrackGiftsProducts.length <= 0 && !this.props.stateTrackGiftsPagination.hasNextPage) {
    if (noPurchasedProduct) {
      return <NoRecordFound/>
    } else if (this.props.stateTrackGiftsView?.id === TRACK_GIFTS_VIEWS[0].id) {

      // For Gifts

      return (
        <div>
          <div className="row mb-1 mb-md-2">
            <div className="col-6 d-none d-md-inline-block">
              <p className="text-sm font-medium">Gift details</p>
            </div>
            <div className="col-6 d-none d-md-inline-block">
              <p className="text-sm font-medium">Gifted by</p>
            </div>
            <div className="col-12 d-inline-block d-md-none">
              <p className="text-sm font-body">Gift details | Gifted by</p>
            </div>
          </div>
          <div className="row">
            <div className="col">
              <InfiniteScroll
                style={{overflow: "unset"}}
                next={() => getTrackGiftsProduct(false, this.props.stateTrackGiftsFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)}
                hasMore={this.props.stateTrackGiftsPagination.hasNextPage}
                loader={<h4>Loading...</h4>}
                dataLength={this.props.stateTrackGiftsProducts.length}
              >
                {products.map(product => {
                  // return <p className="my-5">{product.title}</p>
                  return <GiftCard data={product}/>
                })}
              </InfiniteScroll>
            </div>
          </div>
        </div>
      )
    } else {

      // For Gift Giver

      return (
        <div>
          <InfiniteScroll
            next={() => getTrackGiftsProduct(false, this.props.stateTrackGiftsFilters, this.props.stateTrackGiftsProducts, this.props.stateTrackGiftsPagination, this.props.stateTrackGiftsView, this.props.stateRegistryInfo, this.props.stateTrackGiftsSearch, this.props.dispatch)}
            hasMore={this.props.stateTrackGiftsPagination.hasNextPage}
            loader={<h4>Loading...</h4>}
            dataLength={this.props.stateTrackGiftsProducts.length}
            style={{overflow: "unset"}}
          >
            {products.map(groupedProduct => {
              return (
                <div className="mb-4 track-gifts-gift-giver-view">
                  <div className="mb-2 d-flex">
                    <span
                      className="text-body font-medium track-gifts-gift-giver-user-name"
                      title={groupedProduct.availableProducts ? "Available" : `${groupedProduct.user.guestUser.firstName ? groupedProduct.user.guestUser.firstName : ""} ${groupedProduct.user.guestUser.lastName ? groupedProduct.user.guestUser.lastName : ""}`}>{groupedProduct.availableProducts ? "Available" : `${groupedProduct.user.guestUser.firstName ? groupedProduct.user.guestUser.firstName : ""} ${groupedProduct.user.guestUser.lastName ? groupedProduct.user.guestUser.lastName : ""}`}
                    </span>
                    {!groupedProduct.availableProducts ?
                      <span
                        className="ml-1 text-body track-gifts-gift-giver-user-email">{groupedProduct.user.guestUser.email}</span> : null}
                    <span
                      className="ml-1 text-body font-medium gift-givers-user-gift-count px-2">{`${groupedProduct.products.length} gift${groupedProduct.products.length > 1 ? 's' : ''}`}</span>
                  </div>
                  <div>
                    {groupedProduct.products.map(product => (
                      <GiftCard data={product}/>
                    ))}
                  </div>
                </div>
              )
            })}
          </InfiniteScroll>
        </div>
      )
    }
  }
}

const mapStateToProps = state => ({
  stateTrackGiftsView: state.trackGifts.activeView,
  stateTrackGiftsProducts: state.trackGifts.products,
  stateTrackGiftsPagination: state.trackGifts.paginationInfo,
  stateRegistryInfo: state.registry.info,
  stateTrackGiftsFilters: state.trackGifts.filters,
  stateTrackGiftsSearch: state.trackGifts.search
})

export default connect(
  mapStateToProps
)(TrackGiftsContent);