import React, {useState} from 'react'
import "./Filter.scss";
import RadioButton from 'modules/commons/components/radio-button/RadioButton';
import CheckBox from 'modules/commons/components/check-box/CheckBox';
import { useSelector, useDispatch } from 'react-redux';
import { setParentFilter, setChildFilter, clearFilter } from '../../MyRegistryActions.js';
import Button from "../../../commons/components/button-component/ButtonComponent";
import Modal from '../../../commons/components/modal-component/ModalComponent';


const Filter = (props) => {

  const dispatch = useDispatch()

  const filterData = useSelector(state => state.myRegistry.filterCategory)
  let tempParentFilterId = useSelector(state => state.myRegistry.filterApplied.parentFilter)
  let tempChildFilterIds = useSelector(state => state.myRegistry.filterApplied.childFilter)
  let [showFilterModal, setShowFilterModal] = useState(false);

  const filterContentRef = React.createRef();

  const toggleFilterContent = () => {
    filterContentRef.current.classList.toggle("show");
  }

  // Change event handlers

  const parentChangeHandler = (parent) => {
    dispatch(setParentFilter(parent.id))
    if (parent.child) {
      dispatch(setChildFilter(parent.child.map(child => child.id)))
    } else {
      dispatch(setChildFilter([]))
    }
  }

  const childChangeHandler = (childId, parentId) => {

    if (parentId !== tempParentFilterId) {
      dispatch(setParentFilter(parentId))
      if (tempChildFilterIds.length > 0) {
        dispatch(setChildFilter([]))
      }
    }

    const childIndex = tempChildFilterIds.indexOf(childId)

    if (childIndex === -1) {
      dispatch(setChildFilter([...tempChildFilterIds, childId]))
    } else {
      let newChildArray = [...tempChildFilterIds]
      newChildArray.splice(childIndex, 1)
      dispatch(setChildFilter(newChildArray))
    }
  }

  // Click Handler

  const doneClickHandler = () => {
    toggleFilterContent()
  }


  const clearClickHandler = () => {
    dispatch(clearFilter())
    toggleFilterContent()
  }

  const doneModalClickHandler = () => {
    setShowFilterModal(false);
  }

  const clearModalClickHandler = () => {
    dispatch(clearFilter())
    setShowFilterModal(false);
  }
  const getFilterContent = (radioName = null, modal=false) => {
    return (
      <div className="d-flex flex-column px-5">
      {filterData.map(parentFilter => (
        <div className="filter-item mb-3" key={"filter-" + parentFilter.id}>
          {/*{console.log(parentFilter.id, tempParentFilterId)}*/}
          {/* Parent filter */}
          <RadioButton
            {...parentFilter}
            textClass="text-body"
            centerText="true"
            name={`parentFilter${radioName ? "-1" : ""}${modal ? "-M" : ""}`}
            key={parentFilter.id}
            checked={parentFilter.id === tempParentFilterId ? true : null}
            changeHandler={() => parentChangeHandler(parentFilter)}
          />

          {/* Child filter */}
          {parentFilter.child ? (
            <div className="d-flex flex-column mt-2 pl-5" key={"child-filter-" + parentFilter.id}>
              {parentFilter.child.map(childFilter => (
                <CheckBox
                  {...childFilter}
                  key={childFilter.id}
                  className="mb-4"
                  checked={tempChildFilterIds.includes(childFilter.id) ? true : null}
                  changeHandler={() => childChangeHandler(childFilter.id, parentFilter.id)}
                />
              ))}
            </div>
          )
            : null}
        </div>
      ))}
    </div>
    )
  }
  const filterContent = (
    <div className="d-flex flex-column px-5">
      {filterData.map(parentFilter => (
        <div className="filter-item mb-3" key={"filter-" + parentFilter.id}>
          {/*{console.log(parentFilter.id, tempParentFilterId)}*/}
          {/* Parent filter */}
          <RadioButton
            {...parentFilter}
            textClass="text-body"
            centerText="true"
            name={`parentFilter${props.radioName ? "-1" : ""}`}
            key={parentFilter.id}
            checked={parentFilter.id === tempParentFilterId ? true : null}
            changeHandler={() => parentChangeHandler(parentFilter)}
          />

          {/* Child filter */}
          {parentFilter.child ? (
            <div className="d-flex flex-column mt-2 pl-5" key={"child-filter-" + parentFilter.id}>
              {parentFilter.child.map(childFilter => (
                <CheckBox
                  {...childFilter}
                  key={childFilter.id}
                  className="mb-4"
                  checked={tempChildFilterIds.includes(childFilter.id) ? true : null}
                  changeHandler={() => childChangeHandler(childFilter.id, parentFilter.id)}
                />
              ))}
            </div>
          )
            : null}
        </div>
      ))}
    </div>
  )

  const footerButton = (
    <div className="d-flex filter-content-bottom-buttons">
      <Button className="text-body py-1 px-5 mr-2 font-medium"
        onClick={clearModalClickHandler}
        variant="secondary"
      >Clear
          </Button>
      <Button
        className="text-body py-1 px-3 font-medium"
        onClick={doneModalClickHandler}
        variant="primary"
      >Done
          </Button>
    </div>
  )


  return (
    <div className="filter-section-filter">
      <Button className="px-1 py-1 px-md-3 d-none d-md-inline-block" variant="secondary" onClick={toggleFilterContent}>
        <div className="d-flex">
          <i className="icon-filter-1 mr-1 filter-icon" />
          <span className="font-medium text-body">Filter</span>
        </div>
      </Button>
      <Button className="px-1 py-1 px-md-3 d-inline-block d-md-none" variant="secondary" onClick={()=>setShowFilterModal(true)}>
        <div className="d-flex">
          <i className="icon-filter-1 filter-icon" />
        </div>
      </Button>
      <div className="filter-content py-4" ref={filterContentRef}>
        {/*{filterContent}*/}
        {getFilterContent(props.radioName, false)}
        <div className="d-flex filter-content-bottom-buttons px-5 pt-4">
          <Button className="text-body py-1 px-5 mr-2 font-medium"
            onClick={clearClickHandler}
            variant="secondary"
          >Clear
          </Button>
          <Button
            className="text-body py-1 px-3 font-medium"
            onClick={doneClickHandler}
            variant="primary"
          >Done
          </Button>
        </div>
      </div>
      <Modal
        show={showFilterModal}
        title="Filters"
        footer={footerButton}
        closeModal={()=> setShowFilterModal(false)}>
        {/*{filterContent}*/}
        {getFilterContent(props.radioName, true)}
      </Modal>
    </div>
  )
}

export default Filter;