import React from 'react'
import "./Counter.scss";

const Counter = (props) => {
	return (
		<div className={["counter", "d-flex align-text-center text-center", props.className ? props.className : ""].join(" ")}>
			{props.type === 'square' ?
				<>
					<button className="icon-btn square-minus-icon pl-0" onClick={props.decrementHandler}></button>
					<span className="p-2 text-body counter-text">{props.counter}</span>
					<button className="icon-btn square-plus-icon pr-0" onClick={props.incrementHandler}></button>
				</> :
				<>
					<button className="icon-btn icon-circle-minus pl-0" onClick={props.decrementHandler}></button>
					<span className="mx-2">{props.counter}</span>
					<button className="icon-btn icon-circle-plus pr-0" onClick={props.incrementHandler}></button>
				</>
			}
		</div >
	);
}

export default Counter;