import React from "react";
import "./MarkPurchased.scss";
import MarkAsPurchased from "../../../commons/components/mark-as-purchased/MarkAsPurchased";

class MarkPurchased extends React.Component{
  state = {
    show: false
  }

  modalCloseHandler = () => {
    this.setState({
      show: false
    })
  }

  openPurchasedModal = (ev) => {
    ev.preventDefault();
    this.setState({
      show: true
    })
  }

  render() {
    return (
      <div>
        <a
          href="#"
          className="mark-purchased-link font-medium text-sm"
          onClick={(ev) => this.openPurchasedModal(ev)}>{`Record a ${this.props.data.isGroupGift ? 'contribution' : 'purchase'}`}</a>
        <MarkAsPurchased
          show={this.state.show}
          closeModal={this.modalCloseHandler}
          data={this.props.data}
        />
      </div>
    );
  }
}

export default MarkPurchased;