import { GUEST_VIEW_ACTION_TYPES } from "./GuestViewAction";
import { combineReducers } from "redux";
import defaultData from "data/ReducerDefaultData.json";

const guestViewRegistry = (state = { updated: false }, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.UPDATE_GUEST_VIEW_INFO:
      return { ...action.payload }
    default:
      return state
  }
}

const addedGuestProducts = (state = [], action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_PRODUCT:
      return  [...action.payload];
    default:
      return state;
  }
}

const activeGuestSortFilter = (state = {sortId: 1, sortTitle: 'Featured'}, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.GUEST_ACTIVE_SORT_FILTER:
      return {...action.payload};
    default:
      return state;
  }
}

const setParentFilter = (state, payload) => {
  let newState = {...state}
  newState.parentFilter = payload
  newState.childFilter = [...state.childFilter]

  return newState
}

const setChildFilter = (state, payload) => {
  let newState = {...state}
  newState.childFilter = [...payload]

  return newState
}

const guestViewFilterApplied = (state = defaultData.myRegistry.defaultFilter, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_PARENT_FILTER:
      return setParentFilter(state, action.payload)
    case GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_CHILD_FILTER:
      return setChildFilter(state, action.payload)
    case GUEST_VIEW_ACTION_TYPES.CLEAR_GUEST_VIEW_FILTER:
      return defaultData.myRegistry.defaultFilter
    default:
      return state;
  }
}

const guestFilterCategory = (state = [], action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.SET_GUEST_VIEW_FILTER_CATEGORY:
      return [...action.payload]
    default:
      return state
  }
}

const paginationInfo = (state = defaultData.trackGifts.pagination, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.GUEST_VIEW_SET_PAGINATION_INFO:
      return {...action.payload}
    case GUEST_VIEW_ACTION_TYPES.CLEAR_GUEST_PAGINATION_INFO:
      return defaultData.trackGifts.pagination
    default:
      return state
  }
}

const checkList = (state = [], action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.GET_GUEST_CHECKLIST_ITEMS:
      return action.payload;
    default:
      return state;
  }
}

const alreadyBoughtProduct = (state = defaultData.defaultAlreadyBoughtProductInfo, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.SET_ALREADY_BOUGHT_PRODUCT_INFO:
      return {...action.payload};
    case GUEST_VIEW_ACTION_TYPES.CLEAR_ALREADY_BOUGHT_PRODUCT_INFO:
      return defaultData.defaultAlreadyBoughtProductInfo;
    default:
      return state;
  }
}

const redirectionInfo = (state = {}, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.GUEST_GIFTS_SET_REDIRECTING_INFO:
      return {...action.payload}
    default:
      return state
  }
}

const cashContributedAlready = (state = defaultData.defaultCashContributedAlready, action) => {
  switch (action.type) {
    case GUEST_VIEW_ACTION_TYPES.SET_CASH_CONTRIBUTED_ALREADY_INFO:
      return {...action.payload};
    case GUEST_VIEW_ACTION_TYPES.CLEAR_CASH_CONTRIBUTED_ALREADY_INFO:
      return defaultData.defaultCashContributedAlready;
    default:
      return state;
  }
}

export default combineReducers({
  info: guestViewRegistry,
  product: addedGuestProducts,
  activeGuestSortFilter,
  guestViewFilterApplied,
  guestFilterCategory,
  paginationInfo,
  checkList,
  alreadyBoughtProduct,
  redirectionInfo,
  cashContributedAlready,
});