import React from 'react';
import './FilterSection.scss';
import Filter from '../filter/Filter';
import {connect} from 'react-redux';
import FilterItemChip from '../filter/filter-item-chip/FilterItemChip';
import {clearFilter, setChildFilter} from "../../MyRegistryActions"
import AddGift from "../../../commons/components/add-gift/AddGift";

const FilterSectionChips = ({filterChips, deleteChipHandler, clearFilterHandler}) => {
  return (
    <div className="filtered-items">
      {filterChips.parentFilter ?
        <FilterItemChip text={filterChips.parentFilter.display} className="mr-2"
                        deleteHandler={() => deleteChipHandler(true, null)}/>
        : null}
      {filterChips.childFilter ?
        filterChips.childFilter.map(item =>
          <FilterItemChip
            text={item.display}
            className="mr-2"
            key={filterChips.parentFilter.id + "-" + item.id}
            deleteHandler={() => deleteChipHandler(false, item.id)}/>)
        : null}
      {filterChips.parentFilter ?
        <a className="font-medium text-sm filter-clear-link d-block" href="#" tabIndex="0"
           onClick={(ev) => clearFilterHandler(ev)}>Clear
          all</a>
        : null}
    </div>
  )
}

class FilterSection extends React.Component {

  // Filter chips close handler
  deleteChipHandler = (isParent, childId) => {
    if (isParent) {
      this.props.dispatch(clearFilter())
    } else {
      const newChildArray = this.props.stateFilterApplied.childFilter.filter(element => {
        return element !== childId
      })
      this.props.dispatch(setChildFilter(newChildArray))
    }
  }

  //Clear filter state
  clearFilterHandler = (ev) => {
    ev.preventDefault()
    this.props.dispatch(clearFilter())
  }

  render() {
    // Get all the filter chips from the state based on id
    let filterChips = {}
    if (this.props.stateFilterApplied) {
      let allChild = []
      if (this.props.stateFilterApplied.parentFilter) {
        filterChips.parentFilter = this.props.stateFilterCategory.find(element => {
          return element.id === this.props.stateFilterApplied.parentFilter
        })
        allChild = filterChips.parentFilter.child
      }
      if (this.props.stateFilterApplied.childFilter) {
        filterChips.childFilter = this.props.stateFilterApplied.childFilter.map(item => {
          return allChild.find(element => {
            return element.id === item
          })
        })
      }
    }

    return (
      <>
        <div className="row align-items-center align-items-md-start">
          <div className="col">
            <h4
              className="font-weight-bold mr-2 filter-section-total-items">{this.props.productCount} {this.props.productCount === 1 ? "item" : "items"}</h4>
            <div className="d-none d-md-block">
              <FilterSectionChips
                filterChips={filterChips}
                deleteChipHandler={this.deleteChipHandler}
                clearFilterHandler={this.clearFilterHandler}
              />
            </div>
          </div>
          <div className="col-8 col-md-6 d-flex justify-content-end text-align-right">
            <div className=" mr-3">
              <Filter/>
            </div>
            <div className="position-relative" data-tut="build-step-three">
              <AddGift/>
            </div>
          </div>
        </div>

        <div className="mt-2 d-block d-md-none">
          <FilterSectionChips
            filterChips={filterChips}
            deleteChipHandler={this.deleteChipHandler}
            clearFilterHandler={this.clearFilterHandler}
          />
        </div>
      </>
    )
  }
}


const mapStateToProps = state => {
  return {
    stateFilterApplied: state.myRegistry.filterApplied,
    stateFilteredProducts: state.myRegistry.filteredProducts,
    stateFilterCategory: state.myRegistry.filterCategory
  }
}

export default connect(
  mapStateToProps
)(FilterSection);
