import React, { useState } from 'react';
import "./GiveGiftFromAnotherStore.scss";
import { connect } from "react-redux";
import { withRouter } from 'react-router-dom';
import Modal from "../modal-component/ModalComponent";
import Button from '../button-component/ButtonComponent';
import Counter from "../counter/Counter";
import ProductDetailsModel from '../product-details-model/ProductDetailsModel';
import AlreadyBoughtProduct from "modules/commons/components/already-bought-product/AlreadyBoughtProduct";
import { BUYNOW_REDIRECTION_TIME, LOCAL_STORAGE_GUEST_GIFT_BUY_KEY } from 'utils/constants';
import { setGuestGiftsRedirectionInfo } from 'modules/guest-view/GuestViewAction';
import { MARK_AS_PURCHASED_BY_GUEST, RESERVE_REGISTRY_ITEM_GUEST, REMOVE_RESERVED_ITEM_GUEST } from "api/mutations";
import { toast } from "react-toastify";
import { graphQL } from "api/axios";
import { clearViewGuestPaginationInfo } from "modules/guest-view/GuestViewAction";
import { setLoader } from "modules/registry/RegistryActions";
import { EMAIL_RE } from "utils/constants";

class GiveGiftFromAnotherStore extends React.Component {
  // Variable declarations
  state = {
    updateModal: false,
    quantity: 1,
    isBoughtThisAlready: false,
    name: null,
    email: '',
    registeredEmail: '',
    storeName: null,
    orderNumber: null,
    giftMessage: null,
    currentView: 1,
    errors: {}
  }

  componentDidMount = () => {
    this.setModalData()
    this.buyNowClickHandler(true)
  }

  incrementQuantity = () => {
    if (this.state.quantity < this.props.data.quantityStatus.still_needed)
      this.setState(() => ({
        quantity: this.state.quantity + 1
      }))
  }

  decrementQuantity = () => {
    if (this.state.quantity > 1) {
      this.setState(() => ({
        quantity: this.state.quantity - 1
      }))
    }
  }

  emailChangeHandler = (ev) => {
    ev.persist();
    this.setState({
      email: ev.target.value
    })
    let errors = { ...this.state.errors }

    // Remove the errors while typing
    if (this.state.errors.email) {
      delete errors.email
    }
    this.setState({
      errors: errors
    })
  }

  iBoughtThisAlready = () => {
    this.setState(() => ({
      isBoughtThisAlready: true,
      modalTitle: 'I bought this already',
    }))
  }

  validEmail = () => {
    let valid = true
    let errors = {}
    // validate fields
    if (this.state.email.length <= 0) {
      errors['email'] = "Email address is required field"
      valid = false
    } else {
      if (!this.state.email.match(EMAIL_RE)) {
        errors['email'] = "Invalid Email address"
        valid = false
      }
    }

    this.setState({
      errors: errors
    })

    return valid
  }

  validBoughtThisAlreadyData = () => {
    let valid = true
    let errors = {}
    // validate fields
    if (this.props.productBoughtAlreadyInfo.name.length <= 0) {
      errors['name'] = "Name is required field"
      valid = false
    }

    if (this.props.productBoughtAlreadyInfo.email.length <= 0) {
      errors['email'] = "Email address is required field"
      valid = false
    } else {
      if (!this.props.productBoughtAlreadyInfo.email.match(EMAIL_RE)) {
        errors['email'] = "Invalid Email address"
        valid = false
      }
    }

    this.setState({
      errors: errors
    })

    return valid
  }

  onFocusBoughtThisAlreadyHandler = (fieldName) => {
    let errors = { ...this.state.errors }
    // Remove the errors while typing
    if (this.state.errors.email && fieldName === 'email') {
      delete errors.email
    }
    if (this.state.errors.name && fieldName === 'name') {
      delete errors.email
    }
    if (this.state.errors.storeName && fieldName === 'storeName') {
      delete errors.storeName
    }
    this.setState({
      errors: errors
    })
  }

  buyNowClickHandler = (onLoad = false) => {
    let createNewRecord = false
    let updateProduct = false
    let openModal = false
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))

    if (jsonVisitedItems && jsonVisitedItems.length) {
      for (let item of jsonVisitedItems) {
        if (Number(item.rId) === Number(this.props.stateRegistryInfo.id)) {
          if (Number(item.pId) === Number(this.props.data.id)) {
            this.setState({
              currentView: 3,
            })
          } else {
            updateProduct = true;
            createNewRecord = true;
          }
        } else {
          updateProduct = true;
          createNewRecord = true;
        }
      }
    } else {
      createNewRecord = true;
    }

    // TODO: If Reserved Based on TimeStamp
    // if (jsonVisitedItems) {
    //   for (let item of jsonVisitedItems) {

    //     // If registry doesn't match then delete old and create new record
    //     if (Number(item.rId) === Number(this.props.stateRegistryInfo.id)) {
    //       if (Number(item.pId) === Number(this.props.data.id)) {

    //         let productTimestamp = new Date(item.timestamp)
    //         let currentDate = new Date()

    //         // Resetting hours because only checking the dates
    //         productTimestamp.setHours(0, 0, 0, 0)
    //         currentDate.setHours(0, 0, 0, 0)

    //         // Checking if current date is same as product time stamp
    //         // After 1 day, this product entry will be invalid
    //         if (currentDate - productTimestamp === 0) {
    //           openModal = true
    //         } else {
    //           updateProduct = true;
    //           createNewRecord = true;
    //         }
    //         break;
    //       } else {
    //         updateProduct = true;
    //         createNewRecord = true;
    //       }
    //     } else {
    //       createNewRecord = true;
    //       break;
    //     }
    //   }
    // } else {
    //   createNewRecord = true;
    // }

    if (createNewRecord && !openModal && !onLoad) {
      const newVisitedItemsData = {
        rId: this.props.stateRegistryInfo.id,
        pId: this.props.data.id,
        email: this.state.email,
        quantity: this.state.quantity,
        timestamp: new Date()
      }

      let foundProduct = false
      let newData = null
      if (jsonVisitedItems) {
        newData = jsonVisitedItems.map(item => {
          if (Number(item.rId) === Number(this.props.stateRegistryInfo.id) && Number(item.pId) === Number(this.props.data.id)) {
            foundProduct = true
            return newVisitedItemsData
          }
          return item
        })
        if (!foundProduct) {
          newData.push(newVisitedItemsData)
        } else {
          this.setState({
            currentView: 3
          })
        }
      }

      // Updating localStorage
      localStorage.setItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY, JSON.stringify(updateProduct ? newData : [newVisitedItemsData]))

      //Setting Redirection Store
      this.props.dispatch(setGuestGiftsRedirectionInfo({ store: this.props.data.item?.store?.storeName }))

      // Page refresh itself after 5 sec
      setTimeout(async () => {
        // Getting Update Product
        await this.props.dispatch(clearViewGuestPaginationInfo())
        await this.props.updateGuestViewProduct(this.props.stateRegistryInfo.urlSlug, this.props.stateAddedProducts.length, true)
        this.modalCloseHandler()
        this.props.dispatch(setGuestGiftsRedirectionInfo({}))
      }, BUYNOW_REDIRECTION_TIME);
      window.open(this.props.data.item.storeItemUrl, "_blank")

    }
    // If User is logged in
    if (this.props.stateUserInfo.loggedIn) {
      for (let item of [...this.props.data.purchasedRegistryItem]) {
        if (item.isReserved && item.guestUser.email === this.props.stateUserInfo.user.email) {
          this.setState({
            currentView: 3
          })
          break;
        }
      }
    }
  }

  removeProductFromList = () => {
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    let updatedProducts = jsonVisitedItems.filter(product => product.pId !== this.props.data.id)
    localStorage.setItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY, JSON.stringify(updatedProducts))
  }

  modalCloseHandler = () => {
    this.setState({
      errors: {}
    })
    this.props.close()
  }

  purchaseProduct = async () => {
    // Marking product as purchased
    let data = {
      query: MARK_AS_PURCHASED_BY_GUEST,
      variables: {
        registryUrlSlug: this.props.stateRegistryInfo.urlSlug,
        registryItemId: this.props.data.id,
        isPurchased: true,
        storeName: this.props.productBoughtAlreadyInfo.storeName,
        purchasedQuantity: this.props.productBoughtAlreadyInfo.quantity,
        // contributionAmount: null,
        giftMessage: this.props.productBoughtAlreadyInfo.giftMessage,
        orderNumber: this.props.productBoughtAlreadyInfo.orderNumber,
        name: this.props.productBoughtAlreadyInfo.name,
        email: this.props.productBoughtAlreadyInfo.email
      },
    }
    await graphQL(data).then(async res => {
      if (res.data.errors || res.data.error) {
        // TODO: Add toast proper toast msg
        console.log('Something went wrong-->', res.data.errors)
        toast.error((
          <p>Something went wrong</p>
        ))
      } else {
        // Getting Update Product
        await this.props.dispatch(clearViewGuestPaginationInfo())
        await this.props.updateGuestViewProduct(this.props.stateRegistryInfo.urlSlug, this.props.stateAddedProducts.length, true)
        this.removeProductFromList()
        toast.success((
          <p><b>{this.props.data.title}</b> Purchased!</p>
        ))
        this.modalCloseHandler()
      }
      this.props.dispatch(setLoader(false))
    }).catch(err => {
      // TODO: Add toast proper toast msg
      console.log('Something went wrong-->', err)
      toast.error((
        <p>Something went wrong</p>
      ))
      this.props.dispatch(setLoader(false))
    })
  }

  // Remove Reserved Product then Marking product as purchased
  removeReservedAndAddProducts = async () => {
    let email = null
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    // getting default value for email and quantity
    if (jsonVisitedItems) {
      for (let item of jsonVisitedItems) {
        if (Number(item.rId) === Number(this.props.stateRegistryInfo.id)) {
          if (Number(item.pId) === Number(this.props.data.id)) {
            email = item.email
            break
          }
        }
      }
    }
    let emailFound = false
    // If User is logged in
    if (this.props.stateUserInfo.loggedIn && !email) {
      for (let item of [...this.props.data.purchasedRegistryItem]) {
        if (item.guestUser.email === this.props.stateUserInfo.user.email) {
          email = item.guestUser.email
          emailFound = true
        }
      }
    }
    if (emailFound) {
      let data = {
        query: REMOVE_RESERVED_ITEM_GUEST,
        variables: {
          registryId: this.props.stateRegistryInfo.id,
          registryItemId: this.props.data.id,
          email: email ? email : this.props.productBoughtAlreadyInfo.email
        },
      }
      this.props.dispatch(setLoader(true))
      await graphQL(data).then(async res => {
        if (res.data.errors || res.data.error) {
          // TODO: Add toast proper toast msg
          console.log('Something went wrong-->', res.data.errors)
          toast.error((
            <p>Something went wrong</p>
          ))
          this.props.dispatch(setLoader(false))
        } else {
          this.purchaseProduct()
        }
      }).catch(err => {
        // TODO: Add toast proper toast msg
        console.log('Something went wrong-->', err)
        toast.error((
          <p>Something went wrong</p>
        ))
        this.props.dispatch(setLoader(false))
      })
    }
    else {
      this.purchaseProduct()
    }
  }

  submitDataHandler = async () => {
    // Todo: API calls
    // For Bought this gift already
    if (this.state.isBoughtThisAlready) {
      // Vaidating data
      if (!this.validBoughtThisAlreadyData()) {
        this.props.dispatch(setLoader(false))
        return false
      }
      // Removing reversed product
      this.removeReservedAndAddProducts()
    }
    if (this.state.currentView === 2) {
      if (!this.validEmail()) {
        this.props.dispatch(setLoader(false))
        return false
      }
      // Reserveing product
      let name = this.state.email.split('@')
      let data = {
        query: RESERVE_REGISTRY_ITEM_GUEST,
        variables: {
          registryId: this.props.stateRegistryInfo.id,
          registryItemId: this.props.data.id,
          isReserved: true,
          isPurchased: false,
          purchasedQuantity: Number(this.state.quantity),
          name: name[0],
          email: this.state.email
        },
      }
      this.props.dispatch(setLoader(true))
      await graphQL(data).then(async res => {
        if (res.data.errors || res.data.error) {
          // TODO: Add toast proper toast msg
          console.log('Something went wrong-->', res.data.errors)
          toast.error((
            <p>Something went wrong</p>
          ))
        } else {
          this.buyNowClickHandler()
        }
        this.props.dispatch(setLoader(false))
      }).catch(err => {
        // TODO: Add toast proper toast msg
        console.log('Something went wrong-->', err)
        toast.error((
          <p>Something went wrong</p>
        ))
        this.props.dispatch(setLoader(false))
      })
    }
    if (this.state.currentView < 5 && this.state.currentView !== 2) {
      this.setState({
        currentView: this.state.currentView + 1,
      })
    }
  }

  removeProductReservation = async () => {
    let email = null
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    // getting default value for email and quantity
    if (jsonVisitedItems) {
      for (let item of jsonVisitedItems) {
        if (Number(item.rId) === Number(this.props.stateRegistryInfo.id)) {
          if (Number(item.pId) === Number(this.props.data.id)) {
            email = item.email
            break
          }
        }
      }
    }
    if (this.props.stateUserInfo.loggedIn && !email) {
      for (let item of [...this.props.data.purchasedRegistryItem]) {
        if (item.guestUser.email === this.props.stateUserInfo.user.email) {
          email = item.guestUser.email
        }
      }
    }
    let data = {
      query: REMOVE_RESERVED_ITEM_GUEST,
      variables: {
        registryId: this.props.stateRegistryInfo.id,
        registryItemId: this.props.data.id,
        email: email ? email : this.props.productBoughtAlreadyInfo.email
      },
    }
    this.props.dispatch(setLoader(true))
    await graphQL(data).then(async res => {
      if (res.data.errors || res.data.error) {
        // TODO: Add toast proper toast msg
        console.log('Something went wrong-->', res.data.errors)
        toast.error((
          <p>Something went wrong</p>
        ))
      } else {
        // Getting Update Product
        await this.props.dispatch(clearViewGuestPaginationInfo())
        await this.props.updateGuestViewProduct(this.props.stateRegistryInfo.urlSlug, this.props.stateAddedProducts.length, true)
        this.removeProductFromList()
        console.log('Removed From Reservation')
      }
      this.props.dispatch(setLoader(false))
    }).catch(err => {
      // TODO: Add toast proper toast msg
      console.log('Something went wrong-->', err)
      toast.error((
        <p>Something went wrong</p>
      ))
      this.props.dispatch(setLoader(false))
    })
  }

  copyAddress = (ev) => {
    ev.preventDefault();
    const el = document.createElement('textarea');
    el.value = `
    ${this.props.stateRegistryInfo.user.defaultAddress.address1},
    ${this.props.stateRegistryInfo.user.defaultAddress.address2},
    ${this.props.stateRegistryInfo.user.defaultAddress.city},
    ${this.props.stateRegistryInfo.user.defaultAddress.province},
    ${this.props.stateRegistryInfo.user.defaultAddress.country} ${this.props.stateRegistryInfo.user.defaultAddress.zip}
    `;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    toast.success("Copied to clipboard");
  }

  setModalData = () => {
    this.clearModalData()
    this.setState({
      modalTitle: this.props.title
    })
  }

  clearModalData = () => {
    this.modalBody = ""
    this.modalTitleContent = ""
    this.modalFooterButtons = ""
    this.setState({
      currentView: 1,
      isBoughtThisAlready: false
    })
  }

  render() {

    let modalLeftView = null
    let modalSecondaryBtn = (
      !this.state.isBoughtThisAlready ?
        <Button
          variant="secondary"
          className="MA-gift-footer-btn px-3 py-1 font-medium text-body"
          onClick={() => this.iBoughtThisAlready()}
        >I Bought This Already</Button> : null
    )
    let modalPrimaryBtn = null

    if (this.state.currentView === 1) {
      modalLeftView = (
        <div className="col-12 col-md-7 mt-4">
          <div className="ml-3">
            <div className="row col">
              <div className="col">
                <p className="font-body font-medium">Buy this gift for {this.props.stateRegistryInfo.user.firstName} {this.props.stateRegistryInfo.user.partnerUser ? 'and ' + this.props.stateRegistryInfo.user.partnerUser.firstName : ''}</p>
                <p className="text-sm mt-1">You will be able to buy this gift from {this.props.data.item.store.storeName}.</p>
              </div>
            </div>
            <div className="row col mt-4">
              <div className="col m-a-input-container">
                <p className="text-sm font-medium add-gift-store-label">Quantity</p>
                <Counter
                  className="mt-2"
                  counter={this.state.quantity}
                  incrementHandler={this.incrementQuantity}
                  decrementHandler={this.decrementQuantity}
                />
              </div>
            </div>
          </div>
        </div>
      )
      modalPrimaryBtn = (
        <Button
          variant="primary"
          className="MA-gift-footer-btn px-4 py-1 font-medium text-body guest-footer-btn"
          disabled={(this.state.isBoughtThisAlready && (this.props.productBoughtAlreadyInfo.name.length === 0 || this.props.productBoughtAlreadyInfo.email.length === 0)) || (this.props.isGroupGift && Number(this.state.amount)) === 0 ? true : false}
          onClick={() => this.submitDataHandler()}
        >{!this.state.isBoughtThisAlready ? `Buy On ${this.props.data.item.store.storeName}` : 'Save & Close'}</Button>
      )
    } else if (this.state.currentView === 2) {
      modalLeftView = (
        <div className="col-7 mt-4">
          <div className="ml-3">
            <div className="row col">
              <div className="col">
                <p className="font-body font-medium">Whats your email address?</p>
                <p className="text-sm mt-1">We’ll reserve this gift to make sure no one else tries to buy it until we confitm if you’ve bought this.</p>
              </div>
            </div>
            <div className="row col mt-4">
              <div className="col m-a-input-container">
                <p className="text-sm font-medium add-gift-store-label guest-view-required">Email Address</p>
                <input
                  className={["w-100 mt-1 mb-0 px-2 py-1", this.state.errors.email ? "already-bought-input-error" : ""].join(" ")}
                  type='text'
                  placeholder='johndoe@email.com'
                  value={this.state.email}
                  onChange={(ev) => this.emailChangeHandler(ev)}
                />
                {this.state.errors.email ?
                  <span
                    className="text-sm already-bought-input-error-text">{this.state.errors.email}</span>
                  : null}
              </div>
            </div>
            {this.props.stateRegistryInfo.user.defaultAddress ?
              <div className="row col mt-4">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Ship To Them Directly</p>
                  <div className="text-body guest-address-section px-2 py-1 mt-1 d-flex">
                    <div>
                      {this.props.stateRegistryInfo.user.defaultAddress.address1 ? <p>{this.props.stateRegistryInfo.user.defaultAddress.address1},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.address2 ? <p>{this.props.stateRegistryInfo.user.defaultAddress.address2},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.city ? <p>{this.props.stateRegistryInfo.user.defaultAddress.city},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.province ? <p>{this.props.stateRegistryInfo.user.defaultAddress.province},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.country ? <p>{this.props.stateRegistryInfo.user.defaultAddress.country} {this.props.stateRegistryInfo.user.defaultAddress.zip ? this.props.stateRegistryInfo.user.defaultAddress.zip : null}</p> : null}
                    </div>
                    <div className="ml-auto"><a className="address-copy-link font-medium text-sm mt-1" href="#" onClick={(ev) => this.copyAddress(ev)}>Copy</a></div>
                  </div>
                </div>
              </div> : null}
            <div className="row col mt-4">
              <div className="col">
                <p className="text-sm mt-1">Come back to M+A Registry to tell us if you bought this, so we can help keep track of all their gifts</p>
              </div>
            </div>
          </div>
        </div>
      )
      modalPrimaryBtn = (
        <Button
          variant="primary"
          className="MA-gift-footer-btn px-4 py-1 font-medium text-body guest-footer-btn"
          disabled={this.state.email.length === 0 || (this.state.isBoughtThisAlready && (this.props.productBoughtAlreadyInfo.name.length === 0 || this.props.productBoughtAlreadyInfo.email.length === 0)) || (this.props.isGroupGift && Number(this.state.amount)) === 0 ? true : false}
          onClick={() => this.submitDataHandler()}
        >{!this.state.isBoughtThisAlready ? `Go To ${this.props.data.item.store.storeName}` : 'Save & Close'}</Button>
      )
    } else if (this.state.currentView === 3) {
      modalLeftView = (
        <div className="col-7 mt-4">
          <div className="ml-3">
            <div className="row col">
              <div className="col">
                <p className="font-body font-medium">Did you buy this gift?</p>
                <p className="text-sm mt-1">We’ll reserve this gift to make sure no one else tries to buy it until we confitm if you’ve bought this.</p>
              </div>
            </div>
            <div className="row col">
              <div className="col">
                <p className="text-sm mt-5">
                  Still want to buy? <a className="guest-link font-medium" href={this.props.data.item.storeItemUrl} target="_blank">Return to <span className="guest-store-name">{this.props.data.item.store.storeName}</span></a>
                </p>
              </div>
            </div>
            {this.props.stateRegistryInfo.user.defaultAddress ?
              <div className="row col mt-5">
                <div className="col m-a-input-container">
                  <p className="text-sm font-medium add-gift-store-label">Ship To Them Directly</p>
                  <div className="text-body guest-address-section px-2 py-1 mt-1 d-flex">
                    <div>
                      {this.props.stateRegistryInfo.user.defaultAddress.address1 ? <p>{this.props.stateRegistryInfo.user.defaultAddress.address1},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.address2 ? <p>{this.props.stateRegistryInfo.user.defaultAddress.address2},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.city ? <p>{this.props.stateRegistryInfo.user.defaultAddress.city},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.province ? <p>{this.props.stateRegistryInfo.user.defaultAddress.province},</p> : null}
                      {this.props.stateRegistryInfo.user.defaultAddress.country ? <p>{this.props.stateRegistryInfo.user.defaultAddress.country} {this.props.stateRegistryInfo.user.defaultAddress.zip ? this.props.stateRegistryInfo.user.defaultAddress.zip : null}</p> : null}
                    </div>
                    <div className="ml-auto"><a className="address-copy-link font-medium text-sm mt-1" href="#" onClick={(ev) => this.copyAddress(ev)}>Copy</a></div>
                  </div>
                </div>
              </div> : null}
          </div>
        </div>
      )
      modalPrimaryBtn = (
        <Button
          variant="primary"
          className="MA-gift-footer-btn px-4 py-1 font-medium text-body guest-footer-btn"
          disabled={(this.state.isBoughtThisAlready && (this.props.productBoughtAlreadyInfo.name.length === 0 || this.props.productBoughtAlreadyInfo.email.length === 0)) || (this.props.isGroupGift && Number(this.state.amount)) === 0 ? true : false}
          onClick={!this.state.isBoughtThisAlready ? () => this.iBoughtThisAlready() : () => this.submitDataHandler()}
        >{!this.state.isBoughtThisAlready ? 'Yes. I Bought It' : 'Save & Close'}</Button>
      )
      modalSecondaryBtn = (
        <>
          {!this.state.isBoughtThisAlready ?
            <Button
              variant="secondary"
              className="MA-gift-footer-btn px-3 py-1 font-medium text-body"
              onClick={() => this.removeProductReservation()}
            >No, I Didn’t</Button>
            : null
          }
        </>
      )
    }

    // Body for modal
    let modalBody = (
      <>
        {/* Give M+A Gift Model Body */}
        {!this.state.isBoughtThisAlready ?
          <div className="row add-gift-another-store">
            <div className="col">
              <div className="row">
                <>
                  <div className="col-12 col-md-5 py-4 add-gift-image-select">
                    <div className="mr-3">
                      <ProductDetailsModel data={this.props.data} />
                    </div>
                  </div>
                  {modalLeftView}
                </>
              </div>
            </div>
          </div>
          :
          <AlreadyBoughtProduct
            data={this.props.data}
            validBoughtThisAlreadyData={this.validBoughtThisAlreadyData}
            onFocusHandler={this.onFocusBoughtThisAlreadyHandler}
            fieldErrors={this.state.errors} />
        }
      </>
    )

    // Modal buttons
    let modalFooterButtons = (
      <>
        {modalSecondaryBtn}
        {modalPrimaryBtn}
      </>
    )

    return (
      <>
        <Modal
          show={this.props.show}
          updateModal={this.state.updateModal}
          title={this.state.modalTitle}
          titleContent={this.modalTitleContent}
          footer={modalFooterButtons}
          noBodyPadding={true}

          closeModal={this.props.close}>
          {modalBody}
        </Modal>
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.guestView.info,
    stateUserInfo: state.registry,
    productBoughtAlreadyInfo: state.guestView.alreadyBoughtProduct,
    stateAddedProducts: state.guestView.product
  }
}

export default connect(
  mapStateToProps
)(withRouter(GiveGiftFromAnotherStore));