import React, { useState } from 'react'
import "./GuestFilter.scss";
import { useSelector, useDispatch } from 'react-redux';
import { setChildGuestViewFilter, clearGuestViewFilter, setGuestViewFilterCategory, setGuestViewProducts, clearViewGuestPaginationInfo } from '../../GuestViewAction';
import { FILTERS } from "utils/constants";
import GuestSortFilterMobile from "../guest-sort-filter/GuestSortFilterMobile";

const GuestFilterMobile = (props) => {

	const dispatch = useDispatch()
	let tempParentFilterId = useSelector(state => state.guestView.guestViewFilterApplied.parentFilter)
	let tempChildFilterIds = useSelector(state => state.guestView.guestViewFilterApplied.childFilter)
	let stateAddedProducts = useSelector(state => state.guestView.product)
	const stateCategoriesInfo = useSelector(state => state.guestView.checkList)
	let stateFilterCategories = useSelector(state => state.guestView.guestFilterCategory)
	let [filterCategoryLimit, setFilterCategoryLimit] = useState([])

	const childChangeHandler = async (childId, parentId) => {

		if (parentId !== tempParentFilterId) {
			if (tempChildFilterIds.length > 0) {
				await dispatch(setChildGuestViewFilter([]))
			}
		}

		const childIndex = tempChildFilterIds.indexOf(childId)

		if (childIndex === -1) {
			await dispatch(setChildGuestViewFilter([...tempChildFilterIds, childId]))
		} else {
			let newChildArray = [...tempChildFilterIds]
			newChildArray.splice(childIndex, 1)
			await dispatch(setChildGuestViewFilter(newChildArray))
		}
		await dispatch(clearViewGuestPaginationInfo())
		await dispatch(setGuestViewProducts([]))
		props.setFilterhandler()
	}

	const changeParentLimitHandler = (parentFilterID) => {
		let limitLength = null
		let filterCategory = [...stateFilterCategories].map(parentFilter => {
			if (parentFilter.id === parentFilterID) {
				parentFilter.limit = parentFilter.child.length
				limitLength = parentFilter.child.length
			}
			return parentFilter
		})
		dispatch(setGuestViewFilterCategory(filterCategory))

		let isFound = false
		let newFilterCateoryLimit = [...filterCategoryLimit].map(categorylimit => {
			if (categorylimit.id === parentFilterID) {
				categorylimit.limit = limitLength
				isFound = true
				return categorylimit
			}
			return categorylimit
		})

		props.categoryFilterLimit(newFilterCateoryLimit);
		if (!isFound) {
			newFilterCateoryLimit.push({ 'id': parentFilterID, 'limit': limitLength })
		}
		setFilterCategoryLimit(newFilterCateoryLimit)
	}

	return (
		<div className="view-filter">
			<div className="view-filter-heading pb-1 d-flex align-items-baseline">
				<GuestSortFilterMobile/>
			</div>
			<div className="d-flex flex-column">
				{[...stateFilterCategories].map(parentFilter => (
					<div className="filter-item" key={"filter-" + parentFilter.id}>
						{/* Parent filter */}
						<div className="text-body filter-title pt-5 d-flex">{parentFilter.display}</div>

						{/* Child filter */}
						{parentFilter.child ? (
							<div className="d-flex flex-column mt-2" key={"child-filter-" + parentFilter.id}>
								<div className="mt-3 fliter-checkbox-section">
									{parentFilter.child.map((childFilter, index) => (
										childFilter.count > 0 ?
											index < (parentFilter.limit ? parentFilter.limit : parentFilter.child.length) ?
												<label className="view-fliter-checkbox d-flex align-items-center mb-4" key={childFilter.id}>
													<span className="checkbox__input mr-2">
														<input type="checkbox"
															name={childFilter.display}
															value={childFilter.display}
															checked={tempChildFilterIds.includes(childFilter.id) ? "checked" : ""}
															onChange={() => childChangeHandler(childFilter.id, parentFilter.id)} />
														<span className="checkbox__control d-flex justify-content-center align-items-center">
															<i className="icon-check"></i>
														</span>
													</span>
													<p className="radio__label text-sm">{childFilter.display} {childFilter.count > 0 ? <>({childFilter.count})</> : null}</p>
												</label>
												: null
											: null

									))}
									{(parentFilter.limit && (parentFilter.limit !== parentFilter.child.length) && (parentFilter.child.length - parentFilter.limit) > 0) ?
										<div className="filter-clear-all text-sm font-medium ml-4 pb-3" onClick={() => changeParentLimitHandler(parentFilter.id)}>Show {parentFilter.child.length - parentFilter.limit} more</div>
										: null
									}
								</div>
							</div>
						)
							: null}
					</div>
				))}
			</div>
		</div>
	)
}

export default GuestFilterMobile;
