import React from "react";
import "./GuestViewHeader.scss";
import Button from "../../../commons/components/button-component/ButtonComponent";
import MoreOption from "../../../commons/components/more-option/MoreOption";
import { connect } from "react-redux";
import { toast } from 'react-toastify';
import { setguestViewRegistry } from "modules/view-registry/ViewRegistryActions";
import ShareRegistry from "modules/view-registry/components/share-registry/ShareRegistry";

class GuestViewHeader extends React.Component {

  state = {
    showEditRegistryDetail: false,
    showShareRegistry: false
  }

  closeEditRegistryDetail = () => {
    this.setState({ showEditRegistryDetail: false })
  }

  changeRegistryDetail = (ev) => {
    ev.preventDefault();
    this.setState({ showEditRegistryDetail: true })
  }

  handleDisableViewGuest = () => {
    this.props.dispatch(setguestViewRegistry(false))
  }

  shareHandler = () => {
    // this.props.dispatch(setguestViewRegistry(false))
    this.setState({ showShareRegistry: true })
  }

  closeShareRegistry = () => {
    this.setState({ showShareRegistry: false })
  }

  render() {
    return (
      <>
        {this.props.isGuestViewRegistry ?
          <div className="view-guest-header text-body py-2">
            <div className="container d-flex flex-wrap justify-content-between">
              <div className="d-flex align-items-center view-guest-link order-1 order-md-1 mb-2 mb-md-0"
                onClick={this.handleDisableViewGuest}>
                <i className="icon-chevron-left view-guest-text mr-2" />
                <a className="view-guest-text" tabindex="0">Back To View Registry</a>
              </div>
              <div className="view-guest-text order-3 order-md-2 ml-4 ml-md-0">This is how your guests will see your
              registry
              </div>
              <div className="d-flex order-2 order-md-3">
                <div className="d-flex align-items-baseline guest-registry-status pr-3 mr-3 pl-0 pl-md-3">
                  <i className={this.props.stateRegistryInfo?.isPublic ? "public-status-dot" : "private-status-dot"} />
                  <p
                    className="registry-status-title ml-1">{this.props.stateRegistryInfo?.isPublic ? "Public" : "Private"}</p>
                </div>
                <a className="view-guest-text view-guest-link mb-2 mb-md-0"
                  onClick={() => this.shareHandler()}>Share</a>
              </div>
            </div>
          </div> : null
        }

        <div className="guest-view-registry-header" style={{ backgroundImage: 'url(' + this.props.stateGuestViewRegistry.image.image + ')' }}>

          <div className="header-registry-detail">
            <div className="container my-4">
              <div className="row">
                <div className="col">
                  <div className="d-inline-block px-3 py-3 guest-registry-header-border">
                    <h2>{this.props.stateGuestViewRegistry.displayName ? this.props.stateGuestViewRegistry.displayName : ""}</h2>
                    {this.props.stateGuestViewRegistry.greeting ?
                      <p
                        className="text-sm view-registry-header-greeting">{this.props.stateGuestViewRegistry.greeting}</p>
                      :
                      null
                    }
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ShareRegistry
          show={this.state.showShareRegistry}
          closeShareRegistry={this.closeShareRegistry}
          registryInfo={this.props.stateRegistryInfo}
          hasNoProducts={this.props.stateAddedProducts.length <= 0}
        />
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info,
    stateGuestViewRegistry: state.guestView.info,
    isGuestViewRegistry: state.viewRegistry.guestViewRegistry,
    stateAddedProducts: state.myRegistry.addedProducts
  }
}


export default connect(
  mapStateToProps
)(GuestViewHeader);