import React from "react";
import "./GiftCardPill.scss";

const GiftCardPill = (props) => {
  let className = ""
  let text = ""
  switch (props.variant) {
    case "purchased":
      className = "track-gifts-purchased-pill"
      text = "Purchased"
      break
    case "reserved":
      className = "track-gifts-reserved-pill"
      text = "Reserved"
      break
    case "received":
      className = "track-gifts-received-pill"
      text = "Received"
      break
    case "available":
      className = "track-gifts-available-pill"
      text = props.text
      break
    default:
      className = ""
      break
  }

  return (
    <div className={["track-gifts-pills px-2 d-inline-block", className, props.className ? props.className : ''].join(" ")}>
      <span className="text-sm font-medium">{props.amount != null || undefined ? `${props.amount}` : null} {text}</span>
    </div>
  )
}

export default GiftCardPill