import React from "react";
import "./GiftCard.scss";
import GiftCardPill from "../gift-card-pill/GiftCardPill";
import {CURRENCY} from "../../../../utils/constants";
import BuyNow from "../buy-now/BuyNow";
import MarkPurchased from "../mark-purchased/MarkPurchased";
import moment from 'moment';
import Modal from "../../../commons/components/modal-component/ModalComponent";
import {
  formatPrice,
  checkIsCashFund,
  productImageLoadErrorHandler,
  productImageLoadHandler
} from "../../../../utils/commonFunction";
import GiftStatusPill from "../../../commons/components/gift-status-pill/GiftStatusPill";
import {ROUTES} from "../../../../utils/cdn-assets-links";

class GiftCard extends React.Component {
  friendsDetailRef = React.createRef()

  state = {
    toggleGiftModalHandler: false,
    toggleFriendsDetialsModalHandler: false
  }

  toggleFriendsDetail = e => {
    e.preventDefault()
    if (this.friendsDetailRef.current) {
      this.friendsDetailRef.current.style.display = "block"
    }
  }

  closeFriendsDetail = e => {
    e.preventDefault()
    if (this.friendsDetailRef.current) {
      this.friendsDetailRef.current.style.display = "none"
    }
  }

  giftModalCloseHandler = () => {
    this.setState({toggleGiftModalHandler: false})
  }

  giftModalOpenHandler = () => {
    this.setState({toggleGiftModalHandler: true})
  }

  friendsDetailsOpenModalHandler = () => {
    this.setState({toggleFriendsDetialsModalHandler: true})
  }

  friendsDetailsCloseModalHandler = () => {
    this.setState({toggleFriendsDetialsModalHandler: false})
  }

  render() {
    let isCashFund = checkIsCashFund(this.props.data.registryCategories)

    let showMarkPurchased = false

    if (this.props.data.isGroupGift) {
      if (isCashFund) {
        showMarkPurchased = !this.props.data.isPurchased
      } else {
        showMarkPurchased = !this.props.data.isAmountReceived
      }
    } else if (this.props.data.purchasedRegistryItem.length <= 0){
      if (this.props.data.isCashAcceptedOnly) {
        showMarkPurchased = !this.props.data.isPurchased || !this.props.data.isAmountReceived
      } else {
        showMarkPurchased = !this.props.data.isPurchased
      }
  }

    return (
      <div className="baby-registry-track-gifts-card pt-3 pb-2 pb-md-4">
        <div className="row">

          <div className="col-5 col-sm-3 col-md-1">
            {this.props.data.imageUrl ?
              <img src={this.props.data.imageUrl} className="track-gifts-card-image"
                   onError={(ev) => productImageLoadErrorHandler(ev)}
                   onLoad={(ev) => productImageLoadHandler(ev)}
              />
              :
              <div className="track-gifts-no-product-image"/>
            }
          </div>

          <div className="col-12 col-md-5 my-3 my-md-0">
            <div className="track-gifts-product-description pl-1">
              <div className="d-flex">
                <h4
                  className={["font-medium track-gifts-product-title", !this.props.data.isGroupGift && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].giftMessage && this.props.data.purchasedRegistryItem[0].guestUser ? "mr-5" : "mr-9"].join(" ")}>
                  {this.props.data.title}
                </h4>
                {!this.props.data.isGroupGift && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].giftMessage && this.props.data.purchasedRegistryItem[0].guestUser ?
                  <button
                    className="ml-auto track-gifts-gift-message-container d-none d-md-inline-block text-left px-3">
                    <div className="track-gifts-gift-message-content p-5">
                      <h3>Gift Message</h3>
                      <p
                        className="text-body mt-5 track-gifts-gift-message">{this.props.data.purchasedRegistryItem[0].giftMessage}</p>
                      <p
                        className="mt-3 text-sm font-medium">{`${this.props.data.purchasedRegistryItem[0].guestUser.firstName} ${this.props.data.purchasedRegistryItem[0].guestUser.lastName ? `${this.props.data.purchasedRegistryItem[0].guestUser.lastName[0]}.` : ''}`}</p>
                    </div>
                  </button>
                  : null}

              </div>
              {this.props.data.purchasedRegistryItem.length > 0 && (this.props.data.purchasedRegistryItem[0].storeName || this.props.data.purchasedRegistryItem[0].orderNo) ?
                <p
                  className="text-body">{this.props.data.purchasedRegistryItem[0].storeName ? this.props.data.purchasedRegistryItem[0].storeName : ''} {this.props.data.purchasedRegistryItem[0].orderNo ? '| #' + this.props.data.purchasedRegistryItem[0].orderNo : ''}</p>
                : null}

              {this.props.data.isGroupGift ?
                <div className="d-block mt-2">
                  {this.props.data.purchasedRegistryItem.length > 0 ?
                    this.props.data.isAmountReceived || (Number(this.props.data.registryCategories[0].category.categoryType.id) === 2 && this.props.data.isPurchased) ?
                      <GiftStatusPill variant="purchased"
                                      text={`${formatPrice(this.props.data.quantityStatus.total_contribution_amount, CURRENCY)} Received`}
                                      className="d-inline-block mr-2"/>
                      :
                      <div className="d-inline-block mr-2">
                      <span
                        className="text-sm font-medium">{formatPrice(this.props.data.purchasedRegistryItem[0].groupGift.amountReceived)}</span>
                        <span
                          className="text-sm">{` of ${formatPrice(this.props.data.quantity * this.props.data.item.price)} received`}</span>
                        <div className="track-gifts-cash-fund-progress-bar w-100 mr-2">
                          <div className="track-gifts-cash-fund-progress mw-100"
                               style={{width: `${(this.props.data.purchasedRegistryItem[0].groupGift.amountReceived / (this.props.data.quantity * this.props.data.item.price)) * 100}%`}}/>
                        </div>
                      </div>
                    :
                    <GiftCardPill variant="available"
                                  text={`${formatPrice(this.props.data.quantity * this.props.data.item.price)} Requested`}/>
                  }
                  {this.props.data.purchasedQuantity > 0 && Number(this.props.data.registryCategories[0].category.categoryType.id) !== 2 ?
                    <GiftStatusPill variant="purchased"
                                    text={`${this.props.data.purchasedQuantity} Purchased`}
                                    className="d-inline-block mr-2"/>
                    : null}

                </div>
                :
                <div className="mt-1 mt-md-2">
                  {this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].isPurchased && !this.props.data.purchasedRegistryItem[0].isAmountReceived ?
                    <GiftCardPill variant="purchased" className="mr-1"
                                  amount={this.props.data.purchasedRegistryItem[0].purchasedQuantity > 1 ? this.props.data.purchasedRegistryItem[0].purchasedQuantity : null}/> : null}
                  {this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].isReserved ?
                    <GiftCardPill variant="reserved" className="mr-1"/> : null}
                  {this.props.data.purchasedRegistryItem.length <= 0 ?
                    <GiftCardPill variant="available" className="mr-1"
                                  text={`${this.props.data.quantityStatus ? this.props.data.quantity - this.props.data.quantityStatus.purchased_quantity : this.props.data.quantity} Requested`}/> : null}
                  {this.props.data.isCashAcceptedOnly && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].isAmountReceived ?
                    <>
                      <GiftCardPill variant="received" className="mr-1"
                                    amount={formatPrice(this.props.data.purchasedRegistryItem[0].contributionAmount)}/>
                      {this.props.data.purchasedQuantity > 0 ?
                        <GiftCardPill variant="purchased" className="mr-1"
                                      amount={this.props.data.purchasedQuantity}/>
                        : null}
                    </>
                    : null}

                  {/* TODO: Add date of purchased */}
                  {this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].createdTs ?
                    <span
                      className="text-sm track-gifts-purchased-time">{moment(this.props.data.purchasedRegistryItem[0].createdTs).format('MMM DD, YYYY')}</span>
                    : null}
                </div>
              }


            </div>
          </div>

          <div className="col-12 col-md-6 d-flex flex-md-row flex-column">
            {this.props.data.isGroupGift ?
              // Group Gift

              (this.props.data.purchasedRegistryItem.length > 0 ?
                <div className="track-gifts-group-users-details ">
                  <p
                    className="text-sm font-medium">{`${this.props.data.purchasedRegistryItem[0].otherUsers.length} friend${this.props.data.purchasedRegistryItem[0].otherUsers.length > 1 ? 's' : ''}`}</p>
                  <a href="#"
                     tabIndex={2}
                     className="track-gifts-view-friends-link  text-sm d-none d-md-inline-block font-medium"
                     onClick={(e) => this.toggleFriendsDetail(e)}
                     onBlur={(e) => this.closeFriendsDetail(e)}>View
                  </a>

                  <a href="#" className="track-gifts-view-friends-link text-sm d-inline-block d-md-none font-medium"
                     onClick={this.friendsDetailsOpenModalHandler}>View</a>
                  <div ref={this.friendsDetailRef} className="track-gifts-friends-detail pt-3 pb-1">
                    <div className="friends-detail-header d-flex pb-2 px-3">
                      <p
                        className="text-sm font-medium">{`${this.props.data.purchasedRegistryItem[0].otherUsers.length} friend${this.props.data.purchasedRegistryItem[0].otherUsers.length > 1 ? 's' : ''}`}</p>
                      <i className="icon-close close-friends-detail-icon ml-auto"
                         onClick={(e) => this.closeFriendsDetail(e)}/>
                    </div>
                    <div className="px-3">
                      <table className="m-a-revert-all friends-detail-container">
                        {this.props.data.purchasedRegistryItem[0].otherUsers.map(user => (

                          <tr className="friend-detail m-a-revert-all">
                            <td className="m-a-revert-all p-0">
                              <div className="py-2 mr-8">
                                <p
                                  className="text-sm font-medium">{`${user.guestUser.firstName ? user.guestUser.firstName : ''} ${user.guestUser.lastName ? user.guestUser.lastName : ''}`}</p>
                                <p className="text-sm friend-user-email">{user.guestUser.email}</p>
                              </div>
                            </td>
                            <td className="text-right m-a-revert-all p-0">
                              <p
                                className="text-sm font-medium friend-contribution-amount mr-1">{formatPrice(user.contributionAmount)}</p>
                            </td>
                            <td className="m-a-revert-all p-0">
                              {user.createdTs ?
                                <p
                                  className="text-sm track-gifts-purchased-time">{moment(user.createdTs).format('MMM DD, YYYY')}</p>
                                : null}
                            </td>
                          </tr>
                        ))}
                      </table>
                    </div>
                  </div>
                </div>
                : null)
              :
              // Single guest User
              (this.props.data.purchasedRegistryItem.length > 0 ?
                <div className="track-gifts-user-detail d-flex ml-1 ml-md-0">
                  <div className="w-100">
                    <p
                      className="text-sm font-medium track-gift-user-info-name">{`${this.props.data.purchasedRegistryItem[0].guestUser.firstName ? this.props.data.purchasedRegistryItem[0].guestUser.firstName : ''} ${this.props.data.purchasedRegistryItem[0].guestUser.lastName ? this.props.data.purchasedRegistryItem[0].guestUser.lastName : ''}`}</p>
                    <p
                      className="text-sm track-gifts-user-email track-gift-user-info-name">{this.props.data.purchasedRegistryItem[0].guestUser.email}</p>
                  </div>
                  {!this.props.data.isGroupGift && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].giftMessage && this.props.data.purchasedRegistryItem[0].guestUser ?
                    <button className="ml-auto track-gifts-gift-message-container text-left d-inline-block d-md-none"
                            onClick={this.giftModalOpenHandler}>
                    </button>
                    : null}
                </div>
                : null)

            }
            <div className="ml-auto d-flex align-items-baseline">
              {
                (showMarkPurchased) ?
                  <MarkPurchased data={this.props.data} className="mt-3 mt-md-0"/>
                  : null
              }

              {!this.props.data.isPurchased && ((this.props.data.isCashAcceptedOnly && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].isAmountReceived) || (this.props.data.isGroupGift)) && this.props.data.quantityStatus.buyable_quantity > 0 ?
                <BuyNow productData={this.props.data} className="ml-4 mt-3 mt-md-0"/> : null}

            </div>
          </div>
        </div>
        {!this.props.data.isGroupGift && this.props.data.purchasedRegistryItem.length > 0 && this.props.data.purchasedRegistryItem[0].giftMessage && this.props.data.purchasedRegistryItem[0].guestUser ?
          <Modal
            show={this.state.toggleGiftModalHandler}
            title="Gift Message"
            closeModal={this.giftModalCloseHandler}>
            <p
              className="text-body track-gifts-gift-message">{this.props.data.purchasedRegistryItem[0].giftMessage}</p>
            <p
              className="mt-3 text-sm font-medium">{`${this.props.data.purchasedRegistryItem[0].guestUser.firstName} ${this.props.data.purchasedRegistryItem[0].guestUser.lastName ? `${this.props.data.purchasedRegistryItem[0].guestUser.lastName[0]}.` : ''}`}</p>

          </Modal> : null}
        {this.props.data.isGroupGift && this.props.data.purchasedRegistryItem.length > 0 ?
          <Modal
            show={this.state.toggleFriendsDetialsModalHandler}
            title={`${this.props.data.purchasedRegistryItem[0].otherUsers.length} Friends`}
            closeModal={this.friendsDetailsCloseModalHandler}>
            {this.props.data.purchasedRegistryItem[0].otherUsers.map(user => (
              <div className="friend-detail-modal-container mb-3 pb-3">
                <div className="p-0">
                  <div className="py-2 mr-8">
                    <p
                      className="text-sm font-medium">{`${user.guestUser.firstName ? user.guestUser.firstName : ''} ${user.guestUser.lastName ? user.guestUser.lastName : ''}`}</p>
                    <p className="text-sm friend-user-email">{user.guestUser.email}</p>
                  </div>
                </div>
                <div className="d-flex">
                  <div className="text-right p-0">
                    <p
                      className="text-sm font-medium friend-contribution-amount mr-1">{`${CURRENCY}${user.contributionAmount}`}</p>
                  </div>
                  <div className="p-0">
                    {user.createdTs ?
                      <p
                        className="text-sm track-gifts-purchased-time">{moment(user.createdTs).format('MMM DD, YYYY')}</p>
                      : null}
                  </div>
                </div>
              </div>
            ))}
          </Modal> : null}
      </div>
    );
  }
}

export default GiftCard;