import React, { useState } from "react";
import "./GuestTileCard.scss";
import { formatPrice, productImageLoadErrorHandler, productImageLoadHandler } from "utils/commonFunction";
import GiftStatusPill from "modules/commons/components/gift-status-pill/GiftStatusPill";
import { useSelector, useDispatch } from 'react-redux';
import Button from "modules/commons/components/button-component/ButtonComponent";
import GiveMAGiftModel from "modules/commons/components/give-MA-gift-model/GiveMAGiftModel";
import { M_A_STORE_NAME, LOCAL_STORAGE_GUEST_GIFT_BUY_KEY } from 'utils/constants';
import GiveGiftFromAnotherStore from "modules/commons/components/give-gift-from-another-store/GiveGiftFromAnotherStore";
import { clearAlreadyBoughtProductInfo, clearCashContributedAlreadyInfo } from '../../GuestViewAction';


const GuestTileCard = (props) => {

  const dispatch = useDispatch();
  const stateRegistryInfo = useSelector(state => state.registry)
  const stateGuestViewRegistry = useSelector(state => state.guestView.info)
  const [showMAGiftmodel, setMAGiftModel] = useState(false)
  const [showAnotherStoreGiftmodel, setAnotherStoreGiftmodel] = useState(false)
  const [isGroupGift, setIsGroupGift] = useState(false)
  const [isCashAcceptedOnly, setIsCashAcceptedOnly] = useState(false)
  const [modelTitle, setModelTitle] = useState('')

  const modalCloseHandler = () => {
    dispatch(clearAlreadyBoughtProductInfo())
    dispatch(clearCashContributedAlreadyInfo())
    setMAGiftModel(false)
    setAnotherStoreGiftmodel(false)
  }

  const openModelHandler = () => {
    // TODO: Conditions for different data model
    // For ma product which is not Grop Gift
    if (props.data.item.store.storeName === M_A_STORE_NAME && !props.data.isGroupGift) {
      setMAGiftModel(true)
    }
    // For Group Gift
    else if (props.data.isGroupGift) {
      setIsGroupGift(true)
      setMAGiftModel(true)
    } else if (props.data.isCashAcceptedOnly) {
      setIsCashAcceptedOnly(true)
      setMAGiftModel(true)
    } else if (props.data.item.store.storeName !== M_A_STORE_NAME) {
      setAnotherStoreGiftmodel(true)
    }
    setModelTitle('Give this gift')
    // modelTitle = 'Give This Gift'
  }

  let hasUserReservedProduct = false
  // If non MA product and It's quatity are not still needed
  if (props.data.item.store.storeName !== M_A_STORE_NAME && props.data.quantityStatus.reserved_quantity && props.data.quantityStatus.still_needed <= 0) {
    // If your is login
    if (stateRegistryInfo.loggedIn) {
      for (let item of [...props.data.purchasedRegistryItem]) {
        if (item.isReserved && item.guestUser.email === stateRegistryInfo.user.email) {
          hasUserReservedProduct = true
          break;
        }
      }
    }
  }
  // Checking in Local storage
  if (!hasUserReservedProduct && (props.data.item.store.storeName !== M_A_STORE_NAME && props.data.quantityStatus.reserved_quantity && props.data.quantityStatus.still_needed <= 0)) {
    let jsonVisitedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_GUEST_GIFT_BUY_KEY))
    if (jsonVisitedItems && jsonVisitedItems.length) {
      for (let product of [...props.data.purchasedRegistryItem]) {
        for (let item of jsonVisitedItems) {
          if (Number(item.rId) === Number(stateGuestViewRegistry.id) && Number(item.pId) === Number(props.data.id) && product.guestUser.email === item.email) {
            hasUserReservedProduct = true
            break;
          }
        }
        if (hasUserReservedProduct) {
          break;
        }
      }
    }
  }
  // If MA product and It quatity are still needed
  if (props.data.item.store.storeName === M_A_STORE_NAME || props.data.quantityStatus.still_needed > 0) {
    hasUserReservedProduct = true
  }

  return (
    <div className={["tile-card", props.className ? props.className : ""].join(" ")}>
      <div className="guest-tile-card p-3" onClick={hasUserReservedProduct && !props.hideDetails ? () => openModelHandler() : () => { }}>
        <div className="product-image text-center pointer mb-3 guest-tile-card-image">
          <img className="mx-auto" src={props.data?.imageUrl}
            onError={(ev) => productImageLoadErrorHandler(ev)}
            onLoad={(ev) => productImageLoadHandler(ev)}
            alt={props.data?.title}
          />
          {props.data.mostWanted ?
            <div
              className="d-flex align-items-center guest-must-have added-gift-must-have-btn px-1 guest-added-gift-must-have-active">
              <i className="icon mr-1 d-block heart-fill" />
              <p className="text-sm added-gift-must-have-text">Must Have</p>
            </div>
            : <></>}
        </div>
        <div className="product-bottom">
          <h4 className="font-medium guest-product-title">{props.data.title}</h4>
          {!props.hideDetails ?
            <>
              <div className="d-flex mt-1 guest-product-price">
                {props.data.item.hideTotal || props.hideDetails ? null :
                  <p className="">{formatPrice(props.data.item.price)}</p>
                }
              </div>

              <div className={["d-flex mt-2 flex-wrap align-items-start", !props.hideDetails ? "guest-tile-pills-section" : ""].join(" ")}>
                {props.data.quantityStatus.purchased_quantity && !props.data.item.isCashFund ?
                  <GiftStatusPill className="mr-1 my-1" variant="purchased" text={props.data.quantityStatus.purchased_quantity + " Purchased"} />
                  : null}
                {props.data.quantityStatus.reserved_quantity && !props.data.item.isCashFund ?
                  <GiftStatusPill className="mr-1 my-1" variant="reserved" text={props.data.quantityStatus.reserved_quantity + " Reserved"} />
                  : null}
                {props.data.quantityStatus.still_needed && !props.data.item.isCashFund ?
                  <GiftStatusPill className="mr-1 my-1" variant="default" text={`${props.data.quantityStatus.still_needed} ${props.data.quantityStatus.purchased_quantity === 0 ? " Requested" : " Still Needed"}`} />
                  : null}
                {props.data.isGroupGift ?
                  <div>
                    <div className="guest-toggle-pills px-1 mr-3 d-flex align-items-center mt-md-2 mt-lg-0">
                      <i className="guest-icon-active mr-1" />
                      <span className="text-sm guest-icon-text">Group Gift</span>
                    </div>
                    {props.data.item.hideTotal ? null :
                      <div className="mt-md-1">
                        <span className="text-sm font-medium received-amount">Received {formatPrice(Math.round((props.data.quantityStatus.total_contribution_amount % props.data.item.price) * 100) / 100)} </span>
                        <span className="text-sm total-amount">of {formatPrice(Math.round(props.data.item.price * props.data.quantityStatus.still_needed * 100) / 100)}</span>
                        <div className="group-gift-background">
                          <div className="group-gift-progress mw-100" style={{ width: `${((props.data.quantityStatus.total_contribution_amount % props.data.item.price) * 100) / (props.data.item.price * props.data.quantity)}%` }} />
                        </div>
                      </div>
                    }
                  </div>
                  : null}
              </div>
            </>
            :
            <div className="d-flex mt-2 flex-wrap align-items-end">
              {(props.data.isGroupGift || props.data.quantityStatus.purchased_quantity) && !props.data.item.isCashFund ?
                <GiftStatusPill className="mr-1" variant="purchased" text={props.data.quantity + " Purchased"} />
                : null}
              {props.data.quantityStatus.reserved_quantity && !props.data.item.isCashFund ?
                <GiftStatusPill className="mr-1" variant="reserved" text={props.data.quantityStatus.reserved_quantity + " Reserved"} />
                : null}
              {!props.data.item.hideTotal && props.data.item.isCashFund ?
                <GiftStatusPill className="mr-1" variant="purchased" text={formatPrice(props.data.quantityStatus.total_contribution_amount) + " Received "} />
                : null}
            </div>
          }
          {hasUserReservedProduct && !props.hideDetails ?
            <div className="w-100 mt-3">
              <Button variant="secondary" className="guest-tile-give-this-gift-btn font-medium text-body py-2 w-100"
                onClick={() => openModelHandler()}
              >Give This Gift</Button>
            </div> : null}
        </div>
      </div>
      {showMAGiftmodel ?
        <GiveMAGiftModel
          show={showMAGiftmodel}
          close={modalCloseHandler}
          title={modelTitle}
          data={props.data}
          isGroupGift={isGroupGift}
          isCashAcceptedOnly={isCashAcceptedOnly}
          updateGuestViewProduct={props.updateGuestViewProduct}
        /> : null}

      {showAnotherStoreGiftmodel ?
        <GiveGiftFromAnotherStore
          show={showAnotherStoreGiftmodel}
          close={modalCloseHandler}
          title={modelTitle}
          data={props.data}
          updateGuestViewProduct={props.updateGuestViewProduct}
        /> : null}
    </div>

  )
}

export default GuestTileCard;