import React from 'react';
import "./GuestSortFilter.scss";
import { useSelector, useDispatch } from 'react-redux';
import { activeGuestSortFilter } from '../../GuestViewAction';

const GuestSortFilter = (props) => {

  const dispatch = useDispatch()
  const activeSortFilterState = useSelector(state => state.guestView.activeGuestSortFilter)
  const filterContentRef = React.createRef();

  const toggleFilterContent = () => {
    filterContentRef.current.classList.toggle("show");
    filterContentRef.current.classList.remove("hide-filter-content");
  }


  const handleSortSelection = (sortItem) => {
    dispatch(activeGuestSortFilter(sortItem))
    filterContentRef.current.classList.toggle("show");
    filterContentRef.current.classList.add("hide-filter-content");
  }

  const sortByList = [
    {
      sortId: 1,
      sortTitle: 'Featured'
    },
    {
      sortId: 2,
      sortTitle: 'Price: Low to High'
    },
    {
      sortId: 3,
      sortTitle: 'Price: High to Low'
    }
  ]

  const dropdownClickHandler = (ev) => {
    // ev.stopPropagation()
    let option = document.querySelector("#guest-registry-sort-content");
    if (option && option.classList.contains("remove-sort-dropdown")) {
      let toggle = document.querySelector("#guest-view-sort-toggle");
      if (toggle){
        toggle.blur()
      }
      // if (sortBtnRef.current){
      //   sortBtnRef.current.blur()
      // }
      option.classList.remove("remove-sort-dropdown");
    } else {
      option.classList.add("remove-sort-dropdown");
      if (option && option.classList.contains("hide-filter-content")) {
        option.classList.remove("hide-filter-content")
      }
    }
toggleFilterContent()
  }

  const removeExtraDropdownClass = () => {
    let option = document.querySelector("#guest-registry-sort-content");
    if (option.classList.contains("remove-sort-dropdown")) {
      option.classList.remove("remove-sort-dropdown");
    }
  }

  return (
    <div className="d-flex guest-sort-section">
      <div className="sort-section-filter mr-3">
      <a tabIndex={1} className="sort-filter-btn px-2 w-100 d-flex justify-content-between align-items-center py-1"
         id="guest-view-sort-toggle"
         onClick={(ev) => dropdownClickHandler(ev)}
         onBlur={removeExtraDropdownClass}>
        <span className="font-body selected-sort-text text-body">{activeSortFilterState.sortTitle}</span>
        <i className="icon-filter-1 icon-chevron-down" />
      </a>
      <div className="filter-content" ref={filterContentRef} id="guest-registry-sort-content">
        <div className="d-flex flex-column">
          {sortByList.map(sortItem => {
            return (
              <div className="filter-item py-2 px-2" key={"sortid-" + sortItem.sortId} onClick={() => handleSortSelection(sortItem)}>
                <div className="text-sm">{sortItem.sortTitle}</div>
              </div>
            );
          })}
          </div>
        </div>
      </div>
      <i className={["guest-layout-icon-btn icon-list pl-0", props.isListViewActive ? "active-layout-guest" : ""].join(" ")} onClick={() => props.setGuestLayout(true)} />
      <i className={["guest-layout-icon-btn icon-tiles pl-0", !props.isListViewActive ? "active-layout-guest" : ""].join(" ")} onClick={() => props.setGuestLayout(false)} />
    </div>
  )
}

export default GuestSortFilter;
