import {REGISTRY_ACTION_TYPES} from "./RegistryActions"
import {combineReducers} from "redux";
import defaultData from "data/ReducerDefaultData.json";

const registryInfo = (state = {}, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.UPDATE_REGISTRY_INFO:
      return action.payload
    default:
      return state
  }
}

const loggedIn = (state = false, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_LOGGING_STATUS:
      return action.payload
    default:
      return state
  }
}

const todo = (state = null, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_TODO:
      return [...action.payload]
    default:
      return state
  }
}

const onboardingRegistry = (state = "", action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_ONBOARDING_REGISTRY:
      return action.payload
    default:
      return state
  }
}

const loader = (state = false, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_LOADER:
      return action.payload
    default:
      return state
  }
}

const user = (state = {}, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_USER_INFO:
      return action.payload
    default:
      return state
  }
}

const metaInformation = (state = null, action) => {
  switch (action.type) {
    case REGISTRY_ACTION_TYPES.SET_META_INFORMATION:
      return {...action.payload}
    default:
      return state
  }
}

export default combineReducers({
  info: registryInfo,
  loggedIn,
  todo,
  onboardingRegistry,
  loader,
  user,
  seo: metaInformation
});