import React, { useState } from "react";
import "./FindRegistry.scss";
import { FIND_REGISTRY } from "api/queries";
import { graphQL } from 'api/axios';
import moment from 'moment';
import { IMPORT_REGISTRY_EMAIL } from "utils/constants";

const FindRegistry = () => {
  const [searchName, setSearchName] = useState('')
  const [allRegistry, setAllRegistry] = useState([])
  const [hasRegistry, setHasRegistry] = useState(true)
  const [searchRegistry, setSearchRegistry] = useState(false)

  const findRegistry = (registryName) => {
    let reqData = {
      query: FIND_REGISTRY,
      variables: {
        registryName: registryName
      }
    }
    graphQL(reqData).then(res => {
      if (res.data.data.findRegistry) {
        let registries = [...res.data.data.findRegistry.edges]
        setAllRegistry(registries)
        if (registries.length > 0) {
          setHasRegistry(true)
        } else {
          setHasRegistry(false)
        }
      }
    }).catch(err => {
      setHasRegistry(false)
    })
  }

  const handleClearSearch = () => {
    setSearchName('')
    setAllRegistry([])
  }

  const textChangeHandler = async (ev) => {
    await setSearchName(ev.target.value)
    setSearchRegistry(true)
  }

  const findRegistryHandler = async () => {
    await findRegistry(searchName)
    setSearchRegistry(false)
  }

  const searchOnEnter = (ev) => {
    ev.persist();
    ev.preventDefault();
    if (ev.keyCode === 13) { //Enter keycode is 13
      findRegistryHandler()
    }
  }

  const handleRegistryUrlSlug = (urlSlug) => {
    window.open(urlSlug)
  }

  return (
    <div className="mt-10 find-registry">
      <div className="row">
        <div className="col-4 offset-4">
          <div className="find-registry-header text-center">Find a Baby Registry</div>
          <div className="mt-6 find-registry-search-container mx-auto">
            <input
              type="text"
              placeholder="Search registry..."
              value={searchName}
              onKeyUp={(ev) => searchOnEnter(ev)}
              onChange={(ev) => textChangeHandler(ev)}
              className="find-registry-search-input pl-6 text-body py-1 w-100 mw-100 pr-11 m-a-unset-line-height"
            />
            <i className="icon-search find-registry-search-icon" />
            {searchName.length > 0 ?
              searchRegistry ?
                <a className="find-registry-search-input-link text-body" href="#"
                  onClick={() => {
                    findRegistryHandler()
                  }}>Search</a>
                :
                <a className="find-registry-search-input-link text-body" href="#"
                  onClick={() => {
                    handleClearSearch()
                  }}>Clear</a>
              : null}
          </div>


        </div>
      </div>
      <div className={["row", allRegistry.length ? "mb-4 mt-10" : ""].join(" ")}>
        <div className="col-10 offset-1">
          {[...allRegistry].map(data => (
            <div className="p-4 find-registry-card mx-5" key={data.node.urlSlug}>
              <div className="d-flex align-items-center">
                <div className="d-flex align-items-baseline">
                  <h3 className="find-registry-displayName">{data.node.displayName}</h3>
                  <p className="ml-2 find-registry-content">{data.node.dueDate !== null ? moment(data.node.dueDate).format('MMM DD, YYYY') : ""}</p>
                </div>
                <div className="ml-auto">
                  <button className="find-registry-action-btn p-2"
                    onClick={() => handleRegistryUrlSlug(data.node.urlSlug)} />
                </div>
              </div>
              {data.node.user.defaultAddress ?
                <p
                  className="mt-1 find-registry-content">{data.node.user.defaultAddress?.city}, {data.node.user.defaultAddress?.province}</p>
                : null}
            </div>
          ))}

        </div>
      </div>
      <div className="row">
        <div className="col">
          <div className="mt-6 text-center">
            {searchName.length > 0 ?
              hasRegistry ?
                // If has registries in list
                <div className="find-registry-content">
                  <h4>Didn’t find the registry that you are looking for?</h4>
                  <h4 className="mb-2">Check with the family or shower host for <a
                    href={"mailto:" + IMPORT_REGISTRY_EMAIL} className="find-registry-email-link" tabIndex
                    target='_blank'>direct link</a> or</h4>
                  <a
                    className="px-3 py-1 find-registry-help-disable-btn text-body font-medium d-inline-block"
                    href={"mailto:" + IMPORT_REGISTRY_EMAIL}
                    target='_blank'
                    tabindex="0"
                  >Email Us For Help</a>
                </div>
                :
                // If has no registries in list
                <div className="find-registry-content">
                  <h4>We couldn’t find any registries that match your search...</h4>
                  <h4 className="mb-2">Check with the family or shower host for <a
                    href={"mailto:" + IMPORT_REGISTRY_EMAIL} className="find-registry-email-link" tabIndex
                    target='_blank'>direct link</a> or</h4>
                  <a
                    className="px-3 py-1 find-registry-help-primary-btn text-body font-medium d-inline-block"
                    href={"mailto:" + IMPORT_REGISTRY_EMAIL}
                    target='_blank'
                    tabindex="0"
                  >Email Us For Help</a>
                </div>
              :
              // Search is not been started yet
              <div className="font-medium find-registry-content">
                <p>Looking for Monica+Andy baby registry?</p>
                <p>Begin typing to search</p>
              </div>
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default FindRegistry;