import React from "react";
import "./GiftStatusPill.scss";

const GiftStatusPill = (props) => {
  let pillVariantClass = ""

  if (props.variant === 'purchased') {
    pillVariantClass = "purchased-pill";
  } else if (props.variant === 'reserved') {
    pillVariantClass = "reserved-pill";
  } else if (props.variant === 'group-gift') {
    pillVariantClass = "group-gift-pill";
  } else if (props.variant === 'default') {
    //  Default is still needed
    pillVariantClass = "default-pill";
  } else {
    pillVariantClass = "default-pill";
  }

  return (
    <div
      className={["gift-status-pill", props.variant === 'group-gift' ? "pl-5 pr-2":"px-3", pillVariantClass, props.className ? props.className : ""].join(" ")}>
      <span className="text-sm font-medium">{props.text}</span>
    </div>
  )
}

export default GiftStatusPill;